import {
	SIZE_MEDIUM,
	BUTTON_ICONIC_CLICKED,
	DEFAULT_HEADER_TITLE,
	SIDE_PANELS
} from '../constants';
import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import styles from './toolbar.scss';
import rtlBehavior from '@servicenow/behavior-rtl';
import isEmpty from 'lodash/isEmpty';
import '@servicenow/now-heading';
import '@servicenow/now-badge';
import '@servicenow/now-label-value';
import '@servicenow/now-dropdown';

const view = (state) => {
	const {
		title,
		rowCount,
		count,
		subtitle,
		displayHelpButton
	} = state.properties;

	return (
		<div className="sn-toolbar">
			<div className="sn-title-container">
				<div className="sn-title-main-container">
					{!isEmpty(title)
						? <now-heading level="1" label={title} className="main-title" variant="header-tertiary" wont-wrap hasNoMargin/>
						: <Fragment/>
					}
					{rowCount
						? <now-badge size={SIZE_MEDIUM} status="low" value={count} variant="secondary" />
						: <Fragment/>
					}
				</div>
				{!isEmpty(subtitle)
					? <now-label-value-inline label={subtitle} truncated className="sn-sub-title"/>
					: <Fragment/>
				}
			</div>
			<div className="sn-toolbar-icons-container">
				<slot name="toolbar-icons"></slot>
				<div className="sn-toolbar-icons">
					<now-button-iconic
						className="toolbar-icon"
						data-contextMenuId="toolbar-panel-toggle"
						config-aria={{
							'aria-label': SIDE_PANELS.SETTINGS_PANEL_TOGGLE
						}}
						icon="gear-outline"
						title="Settings"
						size={SIZE_MEDIUM}
						variant='secondary'
						append-to-payload={{
							type: SIDE_PANELS.SETTINGS_PANEL_TOGGLE,
							contextMenuId: 'toolbar-panel-toggle'
						}}
						on-click={(event) => event.stopPropagation()}
					/>
					{displayHelpButton?<now-button-iconic
						className="toolbar-icon"
						data-contextMenuId="toolbar-help-panel-toggle"
						configAria={{
							'aria-label': SIDE_PANELS.HELP_PANEL_TOGGLE
						}}
						icon="circle-info-outline"
						title="Help"
						size={SIZE_MEDIUM}
						variant="secondary"
						append-to-payload={{
							type: SIDE_PANELS.HELP_PANEL_TOGGLE,
							contextMenuId: 'toolbar-help-panel-toggle'
						}}
						on-click={(event) => event.stopPropagation()}
					/>:<Fragment />}
				</div>
			</div>
		</div>
	);
};

createCustomElement('sn-datagrid-header-toolbar', {
	renderer: {
		type: snabbdom,
		view
	},
	behaviors: [
		{ behavior: rtlBehavior }
	],
	properties: {
		rowCount: { default: false },
		count: { default: 0 },
		title: { default: DEFAULT_HEADER_TITLE },
		subtitle: { default: '' },
		displayHelpButton: { default: false }
	},
	styles: styles,
	actionHandlers: {
		[BUTTON_ICONIC_CLICKED]: {
			stopPropagation: true,
			effect({ dispatch, action: { payload } }) {
				const action = payload.type;

				dispatch(action, payload);
			}
		}
	}
});

export default view;
