import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { renderSection, renderConfigSection } from './section';
import { SECTION } from '../../../constants';

const renderVirtualScroller = ({ state, setRef, syncScroll, configSectionWidth }) => {
	return (
		<div
			className="virtual-scroller"
		>
			{ configSectionWidth > 0
				? renderConfigSection({ configSectionWidth })
				: <Fragment/>
			}
			{ Object.values(SECTION).map(section => renderSection({ state,
				section,
				setRef,
				syncScroll
			})) }
		</div>
	);
};

export default renderVirtualScroller;
