import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-icon';
import { resizeColumn } from '../../helpers/resizer';
import { createRef } from '@servicenow/ui-renderer-snabbdom';
import { COLUMN_RESIZER_WIDTH,
	ASCENDING_ORDER,
	BUTTON_CLICKED,
	SUB_COLUMNS_EXPANDED,
	SHOW_HEADER_CONTEXT_MENU,
	EXPAND_ALL_TITLE,
	COLLAPSE_ALL_TITLE,
	ALL_TASKS_EXPANDED,
	COLUMN_SORTED,
	ENTER_KEY,
	DESCENDING_ORDER,
	ASCENDING_ORDER_TITLE,
	DESCENDING_ORDER_TITLE,
	EXPAND_COLUMN_GROUP_TITLE,
	COLLAPSE_COLUMN_GROUP_TITLE,
	COLUMN_ACTION_TITLE,
	NONE,
	CONTAINER_SELECTED,
	COLUMN_DRAG_EVENTS,
	SELECTED_CELL_CLASS,
	ARROW_UP_KEY,
	ARROW_DOWN_KEY,
	ARROW_RIGHT_KEY,
	ARROW_LEFT_KEY,
	KEYBOARD_NAVIGATED,
	PRIMARY,
	CELL_CLICKED,
	COLUMN_AUTOFITTED,
	DATA_TYPES
} from '../../../constants';
import isEmpty from 'lodash/isEmpty';
import isArray from 'lodash/isArray';
import '@servicenow/now-button';
import { addOutsideScrollListener } from '../../../util/outsideScroll';
import { handleColumnDragStart, handleColumnDragEnd } from '../../helpers/columnDragDrop';

const getAriaSort = (sortOrder) => {
	if (sortOrder === ASCENDING_ORDER) {
		return ASCENDING_ORDER_TITLE;
	} else if (sortOrder === DESCENDING_ORDER) {
		return DESCENDING_ORDER_TITLE;
	}

	return NONE;
};

const isColumnSelected = ({ selectedCell, columnId }) => {
	return selectedCell && selectedCell.columnId === columnId && selectedCell.rowIndex === 0;
};

const renderCell = ({
	state,
	column,
	dispatch,
	parentColumn = null,
	sort,
	columnWidth,
	refs: {
		columnRef = null,
		gridHeaderContainerRef,
		bodyWrapperRef
	},
	isRTL,
	shouldCreateResizer = false,
	section
}) => {
	const { config: { hasHierarchy, rowHeight, rowGroup }, groupBy, sortBy, selectedCell, tree } = state;
	const cellHeaderRef = columnRef || createRef();
	const { columnDefinition, subColumns, columnId, expandSubColumns } = column;
	const colWithExpandableSubCols = isArray(subColumns) && subColumns.length > 0;
	const cellWidth = `${columnWidth - COLUMN_RESIZER_WIDTH}px`;
	const isExpandAllVisible = hasHierarchy || groupBy.length > 0;
	const noSubCols = isEmpty(subColumns);
	const isCellDraggable = noSubCols && // doesn't have subColumns
		columnDefinition.groupable && // grouping is allowed on the column
		!groupBy.includes(columnDefinition.name) && // is not already grouped
		rowGroup; // grouping is allowed by config

	const columnClassName = 'header-col';
	const mainColumnClassName = parentColumn ? ' header-col--sub' : ' header-col--main';
	const columnSelected = isColumnSelected({ selectedCell, columnId });
	const selectedColumnClassName = columnSelected ? SELECTED_CELL_CLASS : '';
	const tabIndex = columnSelected ? '0' : '-1';
	const allowSorting = sort && noSubCols;
	const sorted = sortBy.name === columnId;
	const { order } = sortBy;
	const contextMenuId = `${columnId}_0_${mainColumnClassName}`;
	const isGroupHierarchyCol = columnDefinition.type === DATA_TYPES.TEXT_TYPE;

	return (
		<Fragment>
			<div
				className={`${columnClassName} ${mainColumnClassName} ${selectedColumnClassName}`}
				ref={cellHeaderRef}
				aria-label={columnDefinition.label}
				title={columnDefinition.label}
				style={{
					width: cellWidth,
					cursor: isCellDraggable ? 'move' : 'pointer'
				}}
				draggable={isCellDraggable ? 'true' : 'false'}
				ondragstart={(event) => {
					handleColumnDragStart({
						event,
						columnName: columnId,
						columnLabel: columnDefinition.label,
						rowHeight,
						dispatch
					});
					dispatch(COLUMN_DRAG_EVENTS.START);
				}}
				ondragend={() => {
					handleColumnDragEnd();
					dispatch(COLUMN_DRAG_EVENTS.END);
				}}
				aria-sort={getAriaSort(order)}
				tabindex={noSubCols ? '0': null}
				role={noSubCols ? 'columnheader': null}
				on-click={(e) => {
					e.stopPropagation();
					allowSorting && dispatch(COLUMN_SORTED, {
						name: columnId
					});
					dispatch(CELL_CLICKED, {
						rowIndex: 0,
						columnId,
						segment: PRIMARY
					});
				}}
				on-focus={() => {
					dispatch(CELL_CLICKED, {
						rowIndex: 0,
						columnId,
						segment: PRIMARY
					});
				}}
				on-keydown={(e) => {
					switch (e.keyCode) {
						case ENTER_KEY: allowSorting && dispatch(COLUMN_SORTED, {
							name: columnId
						});
							break;
						case ARROW_UP_KEY:
						case ARROW_DOWN_KEY: bodyWrapperRef && bodyWrapperRef.current.scrollTo({ top: 0 });
						// eslint-disable-next-line no-fallthrough
						case ARROW_RIGHT_KEY:
						case ARROW_LEFT_KEY:
							e.preventDefault();
							dispatch(KEYBOARD_NAVIGATED, {
								keyCode: e.keyCode,
								shiftKey: e.shiftKey,
								isRTL,
								segment: PRIMARY
							});
					}
				}}
			>
				{
					isGroupHierarchyCol && isExpandAllVisible
						? <div
							className='sn-datagrid-icons sn-datagrid-icons--always-visible'
							tabIndex={tabIndex}
							role="button"
							ariaLabel={tree.expanded ? COLLAPSE_ALL_TITLE : EXPAND_ALL_TITLE}
							title={tree.expanded ? COLLAPSE_ALL_TITLE : EXPAND_ALL_TITLE}
							on-click={(event) => {
								event.stopPropagation();
								dispatch(BUTTON_CLICKED, {
									type: ALL_TASKS_EXPANDED,
									expandAll: !tree.expanded
								});
							}}
							on-keydown={(event) => {
								event.stopPropagation();
								if (event.keyCode === ENTER_KEY) {
									dispatch(BUTTON_CLICKED, {
										type: ALL_TASKS_EXPANDED,
										expandAll: !tree.expanded
									});
								}
							}}
						>
							<now-icon
								icon={tree.expanded ? 'chevron-down-fill' : isRTL ? 'chevron-left-fill': 'chevron-right-fill'}
								size="md"
								variant="tertiary"
								bare={true}
							></now-icon>
						</div>
						: <Fragment/>
				}
				<div className={`header-col-content${isGroupHierarchyCol ? ' header-col-tree-content': ''}`}>
					<p className="header-col-content-text now-p-inline-end--xs">{columnDefinition.label}</p>
					{
						colWithExpandableSubCols
							? <now-button-iconic
								className={`${ expandSubColumns ? 'collapse' : 'expand'}-column-group now-m-inline-start--xs button-action`}
								bare
								icon={expandSubColumns && isRTL || !expandSubColumns && !isRTL ? 'chevron-right-fill' : 'chevron-left-fill'}
								configAria={{
									'aria-label': expandSubColumns ? COLLAPSE_COLUMN_GROUP_TITLE : EXPAND_COLUMN_GROUP_TITLE
								}}
								size="sm"
								variant="tertiary"
								append-to-payload={{
									type: SUB_COLUMNS_EXPANDED,
									column: column
								}}
							/> : <Fragment/>
					}
					{sorted && order === ASCENDING_ORDER
						? <now-icon icon="sort-ascending-outline" className='now-p-inline-end--xs now-p-inline-start--xs' size="md" title={ASCENDING_ORDER_TITLE}/>
						: <Fragment/>
					}
					{sorted && order === DESCENDING_ORDER
						? <now-icon icon="sort-descending-outline" className='now-p-inline-end--xs now-p-inline-start--xs' size="md" title={DESCENDING_ORDER_TITLE}/>
						: <Fragment/>
					}
				</div>
				{
					column.filter
						? <now-icon className='now-p-inline-end--xs now-p-inline-start--xs' icon="filter-outline" size="md" />
						: <Fragment/>
				}
				<div className="header-buttons">
					<now-button-iconic
						bare
						icon="ellipsis-v-fill"
						size="sm"
						variant="tertiary"
						className="sn-datagrid-icons button-action"
						configAria={{ 'aria-label': COLUMN_ACTION_TITLE }}
						on-keydown={event => {
							if (event.keyCode === ENTER_KEY) {
								event.stopImmediatePropagation();
							}
						}}
						data-contextMenuId={contextMenuId}
						on-click={event => {
							event.stopPropagation();
							const target = event.target.shadowRoot.querySelector('button');
							
							addOutsideScrollListener(target, () => dispatch(CONTAINER_SELECTED));

							dispatch(SHOW_HEADER_CONTEXT_MENU, {
								show: true,
								column,
								parentColumn,
								section,
								contextMenuId,
								segment: PRIMARY,
								currentTarget: event.currentTarget
							});
						}}
					/>
				</div>
			</div>
			{shouldCreateResizer
				? <div
					style={{ minWidth: `${COLUMN_RESIZER_WIDTH}px` }}
					className="column-resizer-container"
				>
					<div
						className="column-resizer"
						ondblclick={() => {
							dispatch(COLUMN_AUTOFITTED, {
								column: column,
								columns: state.columns
							});
						}}
						on-mousedown={() => {
							const { left, right } = cellHeaderRef.current.getBoundingClientRect();

							// it will resize the sub column
							resizeColumn({
								column,
								dispatch,
								gridCellHeaderStart: isRTL ? right : left,
								isRTL
							});
						}}
					>
					</div>
				</div>
				: <Fragment/>
			}
		</Fragment>
	);
};

export default renderCell;
