import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import rtlBehavior from '@servicenow/behavior-rtl';
import styles from './styles.scss';
import '@servicenow/now-tabs';
import '../sn-datagrid-filter';
import view from './view';

/**
 * We have made help panel as a separate component to avoid one more dependency between
 * sn-datagrid and now-gantt. 
 * TODO: convert other panels into shareable components instead of functional renderers
 */
createCustomElement('sn-datagrid-help-panel', {
	renderer: {
		type: snabbdom,
		view
	},
	behaviors: [
		{ behavior: rtlBehavior }
	],
	initialState: {},
	properties: {
		readonly: { default: false },
		rowCopyPaste: { default: true },
		reorderRows: { default: {} },
		previewRecordIcon: { default: true },
		setRowContextMenu: { default: true },
		reorderColumns: { default: true },
		sidePanel: { default: {} }
	},
	actionHandlers: {},
	styles
});
