import { renderSection, renderConfigSection } from './section';
import { createRef } from '@servicenow/ui-renderer-snabbdom';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { SECTION } from '../../../constants';
import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import { isComponentRTL } from '../../helpers/gridStateParams';
import '../../../sn-datagrid-header-context-menu';


const renderGridHeader = ({
	state,
	dispatch,
	setRef,
	syncScroll,
	headerHeight,
	configSectionWidth,
	allowRowsReordering,
	bodyWrapperRef
}) => {
	const { columns, config, headerContextMenu, properties: { dir }, groupBy } = state;
	const { LEFT, CENTER, RIGHT } = SECTION;

	if (isEmpty(config)) {
		return;
	}

	const {
		filter,
		rowGroup,
		freezeColumns,
		hasHierarchy,
		resetColumns
	} = config;
	const sectionRefs = {
		[LEFT]: createRef(),
		[CENTER]: createRef(),
		[RIGHT]: createRef()
	};
	const gridHeaderContainerRef = createRef();
	const isRTL = isComponentRTL(dir);

	return (
		<Fragment>
			<div
				ref={gridHeaderContainerRef}
				id="grid-header"
				className="header"
				role="rowgroup"
				style={{
					height: `${headerHeight}px`,
					minHeight: `${headerHeight}px`
				}}>
				{ configSectionWidth > 0
					? renderConfigSection({
						configSectionWidth,
						allowRowsReordering,
						dispatch,
						state,
						bodyWrapperRef
					})
					: <Fragment/>
				}
				{ Object.values(SECTION).map(section => renderSection({
					state,
					dispatch,
					section,
					refs: {
						gridHeaderRef: sectionRefs[section],
						gridHeaderContainerRef,
						bodyWrapperRef
					},
					setRef,
					syncScroll,
					isRTL
				})) }
				{
					get(state, 'headerContextMenu.show', false)
						? <now-popover
							hide-tail
							manage-opened
							opened={true}
							positionTarget={headerContextMenu.currentTarget}>
							<sn-datagrid-header-context-menu
								slot="content"
								filter={filter}
								rowGroup={rowGroup}
								freezeColumns={freezeColumns}
								hasHierarchy={hasHierarchy}
								column={headerContextMenu.column}
								parentColumn={headerContextMenu.parentColumn}
								section={headerContextMenu.section}
								groupBy={groupBy}
								columns={columns}
								resetColumns={resetColumns}
							>
							</sn-datagrid-header-context-menu>
						</now-popover>
						: <Fragment/>
				}
			</div>
		</Fragment>
	);
};

export default renderGridHeader;
