import '@servicenow/now-tabs';
import {
	COLUMNS,
	COLUMNS_LABEL,
	ROWS,
	ROWS_LABEL,
	SIDE_PANEL_TAB,
	ALTERNATE_ROW_LABEL,
	ALTERNATE_HIGHLIGHT_TOGGLE
} from '../../../constants';
import renderColumnConfigMenu from './columnConfigMenu';

export const renderSidePanel = (state, dispatch) => {
	const { sidePanel, config } = state;

	return (
		<div className="panel">
			<now-tabs
				selectedItem={sidePanel.mode}
				items={[
					{ id: COLUMNS, label: COLUMNS_LABEL },
					{ id: ROWS, label: ROWS_LABEL }]}
				fixedWidth={true}
				appendToPayload={{ type: SIDE_PANEL_TAB }}></now-tabs>
			<div className="panel-container">
				{
					sidePanel.mode === ROWS
						? <div className="panel-row-items-container">
							<div className="panel-item">
								<p className="panel-item-label">{ALTERNATE_ROW_LABEL}</p>
								<now-toggle
									size="md"
									checked={config.alternateRowHighlight}
									append-to-payload={{ type: ALTERNATE_HIGHLIGHT_TOGGLE }}
									config-aria={{ 'aria-label': ALTERNATE_ROW_LABEL }}
								/>
							</div>
						</div>
						: renderColumnConfigMenu(state, dispatch)
				}

			</div>
		</div>
	);
};
