const getShadowRootHost = el => {
	if (el.parentElement) {
		return getShadowRootHost(el.parentElement);
	}

	if (el.parentNode && el.parentNode instanceof DocumentFragment) {
		return el.parentNode.host;
	}

	if (el.shadowRoot) {
		return el;
	}

	return null;
};

const querySelector = (el, selector) => {
	if (el.shadowRoot) {
		return el.shadowRoot.querySelector(selector);
	}

	return getShadowRootHost(el).shadowRoot.querySelector(selector);
};

const querySelectorAll = (el, selector) => {
	if (el.shadowRoot) {
		return el.shadowRoot.querySelectorAll(selector);
	}

	return getShadowRootHost(el).shadowRoot.querySelectorAll(selector);
};

const getBrowserScrollbarWidth = (host) => {
	const scrollDiv = host.shadowRoot.querySelector('.scrollbar-measure');

	return scrollDiv.offsetWidth - scrollDiv.clientWidth;
};

const containsClassInPath = (event, className) => {
	const path = event.path || event.composedPath && event.composedPath();
	let isClassInPath;
	if(Array.isArray(className)) {
		isClassInPath = (path, i, className) => path[i].className && className.some(name => path[i].classList.contains(name))
	} else {
		isClassInPath = (path, i, className) => path[i].className && path[i].classList.contains(className)
	}

	for (let i = 0; i < path.length; i++) {
		if (isClassInPath(path, i, className)) {
			return true;
		}
	}

	return false;
};

export { getShadowRootHost, querySelector, querySelectorAll, containsClassInPath, getBrowserScrollbarWidth };
