import '@servicenow/now-button';
import { Fragment, createRef } from '@servicenow/ui-renderer-snabbdom';
import {
	COLUMN_SELECTION_UPDATED,
	TIME_VARIED_FIELDS,
	REARRANGE_COLUMNS_TITLE,
	COLUMN_DRAG_STARTED,
	LEFT_LABEL,
	CENTER_LABEL,
	RIGHT_LABEL,
	COLUMN_MOVE,
	SHIFT_COLUMN_UP_LABEL,
	SHIFT_COLUMN_DOWN_LABEL,
	SUB_COLUMNS_DISPLAY_LABEL,
	SUB_COLUMNS_HIDE_LABEL,
	COLUMN_CONFIG_SUBCOL_TOGGLED,
	REORDER_COLUMN,
	REORDER_SUBCOLUMN,
	COLUMN_CONFIG_ROW_HEIGHT,
	SECTION,
	COLUMN_DRAG_ENDED,
	RESET_COLUMNS_LABEL,
	SN_DATAGRID_COLUMNS_RESET
} from '../../../constants';
import { getGroupedColumns, isEmptySection, getColumnShiftPosition, getVisibleCols, isColumnNonHidable } from '../../helpers/sections';
import isEmpty from 'lodash/isEmpty';
import get from 'lodash/get';
import { renderColumnLayover, renderSubColumnLayover } from '../layovers/columnDragDropLayover';
import { isComponentRTL } from '../../helpers/gridStateParams';
import { trimLabel } from '../../helpers/accessibilityHelpers';

const getColumnMarkup = ({
	columns,
	dispatch,
	column,
	index,
	parentColumn,
	allowReorder,
	isRTL = false,
	noBorder = false,
	containerRef,
	groupBy,
	disabled
}) => {
	const { configExpanded, subColumns, hidden, columnDefinition: { label, name } } = column;
	const rowHeightStyle = { height: `${COLUMN_CONFIG_ROW_HEIGHT}px` };
	const rowClass = `column-config-row${noBorder ? ' no-border' : ''}`;

	return (
		<div className={rowClass} style={rowHeightStyle} key={name}>
			<div className="column-config-row-items">
				{
					allowReorder
						? <now-button-iconic
							bare
							icon="grid-vertical-fill"
							title={REARRANGE_COLUMNS_TITLE}
							configAria={{
								'aria-label': REARRANGE_COLUMNS_TITLE
							}}
							size="md"
							variant="tertiary"
							on-mousedown={(event) => {
								dispatch(COLUMN_DRAG_STARTED, {
									column,
									index,
									parentColumn,
									offsetY: event.clientY - containerRef.current.getBoundingClientRect().top
								});
							}}
							on-click={() => {
								dispatch(COLUMN_DRAG_ENDED, {}, {}, true);
							}}
						/>
						: <div className="column-config-placeholder-button-space"></div>
				}
				{ parentColumn ? <div className="column-config-placeholder-button-space"></div> : <Fragment/> }
				{ !isEmpty(subColumns) && !subColumns[0].timeVariantGroup // TODO: timeVariantColumn parent identifier
					? <now-button-iconic
						bare
						icon={configExpanded ? 'chevron-down-outline' : isRTL ? 'chevron-left-fill' : 'chevron-right-fill'}
						size="sm"
						variant="tertiary"
						append-to-payload={{
							type: COLUMN_CONFIG_SUBCOL_TOGGLED,
							column
						}}
						configAria={{ 'aria-label': configExpanded ? SUB_COLUMNS_HIDE_LABEL : SUB_COLUMNS_DISPLAY_LABEL }}
					/>
					: <div className="column-config-placeholder-button-space"></div>
				}
				<now-button-bare
					size="md"
					iconStart={!hidden ? 'checkbox-checked-outline' : 'checkbox-unchecked-outline'}
					variant="secondary"
					// TODO: now-button-bare doesnt have tooltipContent, now-button does
					tooltipContent={label}
					label={trimLabel(label, 28)}
					append-to-payload={{
						type: COLUMN_SELECTION_UPDATED,
						column,
						parentColumn
					}}
					className={`column-visibility-${!hidden ? 'checked' : 'unchecked'}`}
					disabled={disabled}
				/>
			</div>
			{ allowReorder
				? <div className="column-config-row-items">
					<now-button-iconic
						bare
						icon="arrow-up-outline"
						size="sm"
						variant="tertiary"
						className="arrow-icon"
						configAria={{ 'aria-label': SHIFT_COLUMN_UP_LABEL }}
						on-click={() => {
							dispatch(COLUMN_MOVE, {
								column,
								... !parentColumn && getColumnShiftPosition(columns, column, index - 1, groupBy),
								... parentColumn && { targetIndex: index - 1, parentColumn }
							});
						}}
					/>
					<now-button-iconic
						bare
						icon="arrow-down-outline"
						size="sm"
						variant="tertiary"
						className="arrow-icon"
						configAria={{ 'aria-label': SHIFT_COLUMN_DOWN_LABEL }}
						on-click={() => {
							dispatch(COLUMN_MOVE, {
								column,
								... !parentColumn && getColumnShiftPosition(columns, column, index + 2),
								... parentColumn && { targetIndex: index + 2, parentColumn }
							});
						}}
					/>
				</div>
				: <Fragment/>
			}
		</div>
	);
};
const renderColumns = ({
	dispatch,
	columns,
	parentColumn,
	state,
	containerRef,
	isLastParentExpanded = false,
	isRTL,
	section
}) => {
	const { reorderColumns } = state.config;
	const centerSectionVisibleColLen = section === SECTION.CENTER && getVisibleCols(columns).length;

	return columns.map((column, index, { length }) => {
		const isLastVisibleCenterSectionCol = centerSectionVisibleColLen === 1 && !column.hidden;
		const bodyWrapperRef = createRef();
		const { hideable, timeVariantGroup, columnDefinition, configExpanded, subColumns } = column;
		const isLastColumn = index === length - 1;

		return timeVariantGroup || columnDefinition.groupColumn ? ''
			: <div className="col-state-container">
				{
					getColumnMarkup({
						dispatch,
						column,
						index,
						parentColumn,
						allowReorder: isLastVisibleCenterSectionCol ? false : reorderColumns,
						columns: state.columns,
						disabled: isColumnNonHidable(hideable, parentColumn) || isLastVisibleCenterSectionCol,
						noBorder: isEmpty(parentColumn)
							? isLastColumn && !configExpanded
							: isLastColumn && isLastParentExpanded,
						isRTL,
						containerRef,
						groupBy: state.groupBy
					})
				}
				{
					configExpanded
						? <div className="sub-col-container" ref={bodyWrapperRef}>
							{ get(state, 'columnReorder.type') === REORDER_SUBCOLUMN && get(state, 'columnReorder.parentColumn.columnId') === column.columnId
								? <div key='sub-col-layover'>
									{ renderSubColumnLayover({
										state,
										dispatch,
										bodyWrapperRef
									})}
								</div>
								: <Fragment/>
							}
							{renderColumns({
								dispatch,
								columns: subColumns,
								parentColumn: column,
								reorderColumns,
								state,
								containerRef: bodyWrapperRef,
								isLastParentExpanded: isLastColumn && configExpanded,
								isRTL,
								section
							})}
						</div>
						: <Fragment/>
				}
			</div>;
	});
};

const renderGroupedColumns = ({
	columns
}) => {
	return columns.map(column => {
		return (
			<div className='col-state-container'>
				{
					getColumnMarkup({
						column,
						allowReordering: false
					})
				}
			</div>
		);
	});
};

const renderColumnSections = ({
	columns,
	dispatch,
	state,
	bodyWrapperRef
}) => {
	const { dir } = state.properties;
	const isRTL = isComponentRTL(dir);
	const { LEFT, CENTER, RIGHT } = SECTION;
	const rowHeightStyle = { height: `${COLUMN_CONFIG_ROW_HEIGHT}px` };
	const columnsExist = {
		[LEFT]: !isEmptySection(columns[LEFT], LEFT, state.groupBy),
		[CENTER]: !isEmptySection(columns[CENTER], CENTER),
		[RIGHT]: !isEmptySection(columns[RIGHT], RIGHT)
	};
	const sectionLabels = {
		[LEFT]: !isRTL ? LEFT_LABEL : RIGHT_LABEL,
		[CENTER]: CENTER_LABEL,
		[RIGHT]: !isRTL ? RIGHT_LABEL : LEFT_LABEL
	};

	return (
		<Fragment>
			{Object.entries(columns).map(([section, sectionColumns]) => {
				if (columnsExist[section]) {
					return (
						<Fragment>
							{section !== CENTER || columnsExist[LEFT] || columnsExist[RIGHT]
								? <div className="column-config-placeholder-row" style={rowHeightStyle}>{sectionLabels[section]}</div>
								: <Fragment/>}
							{renderColumns({
								dispatch,
								columns: sectionColumns,
								state,
								containerRef: bodyWrapperRef,
								isRTL,
								section
							})}
						</Fragment>
					);
				}
			})}
		</Fragment>
	);
};

const renderColumnConfigMenu = (state, dispatch) => {
	const { columns, config } = state;
	const { LEFT, RIGHT, CENTER } = SECTION;
	const orderedColumns = [
		...columns[LEFT],
		...columns[CENTER],
		...columns[RIGHT]
	];
	const groupedColumns = Object.values(getGroupedColumns(orderedColumns));
	const bodyWrapperRef = createRef();

	// Setting Row Height inline becouse of layover calculations
	const rowHeightStyle = { height: `${COLUMN_CONFIG_ROW_HEIGHT}px` };
	return (
		<Fragment>
			<div className="column-config-menu-container">
				<div className="column-config-menu" ref={bodyWrapperRef} >
					<div
						on-click={(event => {
							event.stopImmediatePropagation();
						})}
						style={{position: 'relative'}}
						
					>
						<div>
							{
								renderColumnSections({
									state,
									dispatch,
									columns,
									bodyWrapperRef
								})
							}
							{groupedColumns.length > 0
								? <div>
									<div className="column-config-placeholder-row" style={rowHeightStyle}>{TIME_VARIED_FIELDS}</div>
									{renderGroupedColumns({
										dispatch,
										columns: groupedColumns
									})}
								</div>
								: <Fragment/>
							}
						</div>
						{ get(state, 'columnReorder.type') === REORDER_COLUMN
							? renderColumnLayover({
								state,
								dispatch,
								bodyWrapperRef
							})
							: <Fragment/>
						}
						
					</div>
					
					
				</div>
				{
					config.resetColumns
						? <div className="column-config-menu-footer">
							<now-button
								label={RESET_COLUMNS_LABEL}
								config-aria={{ 'aria-label': RESET_COLUMNS_LABEL }}
								on-click={() => {
									dispatch(SN_DATAGRID_COLUMNS_RESET);
								}}
							></now-button>
						</div>
						: <Fragment/>
				}

			</div>
		</Fragment>
	);
};

export default renderColumnConfigMenu;
