/* istanbul ignore file */
import {
	MIN_COLUMN_WIDTH,
	FONT_FAMILY,
	NOW_GLOBAL_SPACE_LG
} from '../../constants';

// We have to create our own ghost image for drag and drop columns because of a bug in chrome
export const handleColumnDragStart = ({
	event,
	columnName,
	columnLabel,
	rowHeight = 40
}) => {
	const canvas = document.createElement('canvas');

	canvas.width = 1000;
	canvas.height = 500;
	canvas.style.width = '1000px';
	canvas.style.height = '500px';
	canvas.id = 'column-drag-drop';
	canvas.style.position = 'absolute';
	canvas.style.left = '-1000px';
	// we have to append the canvas the the body for chrome
	document.body.append(canvas);

	const ctx = canvas.getContext('2d');

	ctx.strokeStyle = '#1e856d'; // $now-color--primary-1
	ctx.lineWidth = 0.5;
	ctx.font = `bold ${NOW_GLOBAL_SPACE_LG}px ${FONT_FAMILY}`;
	ctx.textAlign = 'left';
	ctx.textBaseline = 'middle';
	ctx.fillStyle = '#ddede9'; // $now-color--primary-0

	const columnLabelWidth = ctx.measureText(columnLabel).width + 20;
	let rectWidth = Math.max(columnLabelWidth, MIN_COLUMN_WIDTH);
	let rectHeight = rowHeight;
	const rectX = 10;
	const rectY = 10;

	if (navigator.userAgent.toLowerCase().indexOf('firefox') > -1) {
		// Firefox shrinks the ghost image so we have to enlarge it manually

		rectWidth = 2 * rectWidth;
		rectHeight = 2 * rectHeight;
		ctx.font = `bold ${2 * NOW_GLOBAL_SPACE_LG}px ${FONT_FAMILY}`;
		ctx.lineWidth = 1;
	}

	ctx.fillRect(rectX, rectY, rectWidth, rectHeight);
	ctx.strokeRect(rectX, rectY, rectWidth, rectHeight);

	ctx.fillStyle = '#000000';
	ctx.fillText(columnLabel, 2 * rectX, rectY + rectHeight / 2);

	event.dataTransfer.setDragImage(canvas, rectX, rectY);
	event.dataTransfer.setData('columnName', columnName);
};

export const handleColumnDragEnd = () => {
	const canvas = document.getElementById('column-drag-drop');

	document.body.removeChild(canvas);
};
