import renderRow from './row';
import { Fragment, createRef } from '@servicenow/ui-renderer-snabbdom';
import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import find from 'lodash/find';
import debounce from 'lodash/debounce';
import {
	ROW_DRAG_STARTED,
	SECTION_MEMBER,
	REARRANGE_ROWS_TITLE,
	ROWS_SELECTION_TOGGLED,
	AGGREGATION,
	SELECTED_CELL_CLASS,
	SELECTED_ROW_CLASS,
	SELECTED_ROW_CELL_CLASS,
	SN_DATAGRID_PREVIEW_RECORD_ICON_CLICKED,
	ROW_CONTEXT_MENU_SHOWN,
	GRID_TASK_EXPANDED,
	TOTAL,
	ROW_CONTEXT_MENU,
	PREVIEW_RECORD,
	UNSELECT_TASK,
	SELECT_TASK,
	ARROW_UP_KEY,
	ARROW_DOWN_KEY,
	ARROW_RIGHT_KEY,
	ARROW_LEFT_KEY,
	ENTER_KEY,
	KEYBOARD_NAVIGATED,
	CELL_CLICKED,
	CONFIG_SECTION_BORDER_WIDTH,
	CONFIG_SECTION_COLUMN_NAME,
	REARRANGE_ROWS_DISABLED_TITLE,
	ALL_ROWS_SELECTION_TOGGLED
} from '../../../constants';
import {
	getColumnsWidth,
	getVisibleCols,
	getSectionWidthStyle,
	shouldHighlightRow
} from '../../helpers/sections';
import { isComponentRTL } from '../../helpers/gridStateParams';
import { getCellContextMenuItems } from '../../helpers/editingHelper';
import { getTreeUtils } from '../../store/tree/IndexTree';

const handleRowContextMenuClick = ({
	event,
	task,
	dispatch,
	segment,
	contextMenuId
}) => {
	event.preventDefault();
	event.stopPropagation();

	dispatch(ROW_CONTEXT_MENU_SHOWN, {
		task,
		column: {
			columnId: CONFIG_SECTION_COLUMN_NAME
		},
		segment,
		contextMenuId,
		currentTarget: event.currentTarget
	});
};

export const renderSection = ({
	visibleTasks,
	state,
	dispatch,
	section,
	syncScroll,
	setRef,
	height,
	segment,
	bodyWrapperRef
}) => {
	const refName = `${segment}-gridBodyRef`;
	const columns = state.columns[section];
	const visibleCols = getVisibleCols(columns);
	if (isEmpty(visibleCols)) {
		setRef(refName, null, section);

		return <Fragment/>;
	}

	const {
		config,
		cellContextMenu,
		sectionsWidth: {
			[section]: sectionWidth
		},
		selectedCell,
		bodySectionKeySuffix,
		selectedTasksCount
	} = state;
	const { BODY } = SECTION_MEMBER;
	const columnsWidth = getColumnsWidth(visibleCols);
	const sectionWidthStyle = getSectionWidthStyle({ section, sectionWidth, columnsWidth, state });
	const gridBodyRef = createRef();
	const { cellContextMenuRenderer } = config;

	setRef(refName, gridBodyRef, section);


	return (
		<div
			style={{ ...sectionWidthStyle }}
			ref={gridBodyRef}
			key={`${section}_${bodySectionKeySuffix}`}
			className={`sn-datagrid-section sn-datagrid-section--${section}`}
			on-scroll={e => syncScroll(e, section, BODY)}
		>
			<div
				style={{
					width: `${columnsWidth}px`,
					height: height ? height : '100%'
				}}
			>
				{
					cellContextMenu && cellContextMenu.section === section &&
					cellContextMenu.segment === segment
						? <now-popover
							hide-tail
							manage-opened
							opened={true}
							positionTarget={cellContextMenu.currentTarget}>
							<sn-datagrid-context-menu
								slot="content"
								parentState={state}
								defaultItems={getCellContextMenuItems(state, cellContextMenu)}
								contextMenu={cellContextMenu}
								contextMenuRenderer={cellContextMenuRenderer}
							/>
						</now-popover>
						: <Fragment/>
				}
				{visibleTasks.map(task => {
					return renderRow({
						config,
						columns,
						task,
						dispatch,
						state,
						gridBodyRef,
						section,
						segment,
						selectedCell,
						bodyWrapperRef
					});
				})}
			</div>
		</div>
	);
};

const renderConfigSectionMarkUp = ({
	tasks,
	treeUtils,
	task,
	taskPos,
	segment,
	config,
	dispatch,
	allowRowsReordering,
	selectedCell,
	isRTL,
	bodyWrapperRef,
	sortBy,
	selectedTasksCount
}) => {
	const {
		rowSelectionCheckbox,
		setRowContextMenu,
		previewRecordIcon,
		hasHierarchy
	} = config;
	let isTaskExpanded;
	const contextMenuId = `${CONFIG_SECTION_COLUMN_NAME}_${task.index + 1}`;
	const configCellSelected = selectedCell && selectedCell.columnId === CONFIG_SECTION_COLUMN_NAME &&
	selectedCell.rowIndex === task.index + 1 && selectedCell.segment === segment;
	const selectedCellClassName = configCellSelected ? ` ${SELECTED_CELL_CLASS}`: '';

	const initiateRowDrag = () => {
		isTaskExpanded = task.expanded;
		let tasksToReorder = [task];
		// do not allow multi drag and drop for hierarchy tasks
		if (!hasHierarchy && task.selected) {
			tasksToReorder = tasks.filter(task => task.selected);
		}

		if (hasHierarchy) {
			if (isTaskExpanded) {
				dispatch(GRID_TASK_EXPANDED, {
					id: task.id,
					expanded: false
				});
			}
			if (selectedTasksCount > 1) {
				dispatch(ALL_ROWS_SELECTION_TOGGLED, { allTasksSelected: true });
				dispatch(ROWS_SELECTION_TOGGLED, { task });
			}
		}

		dispatch(ROW_DRAG_STARTED, {
			tasksToReorder,
			draggedTaskPos: taskPos
		});
	};
	const initiateRowDragDebounced = debounce(initiateRowDrag, 50);
	const checkboxIconName = task.selected ? 'checkbox-checked-fill' : 'checkbox-unchecked-fill';
	const tabIndex = configCellSelected || segment === AGGREGATION ? '0': '-1';
	// reordering is disabled when any column is sorted or if the task is the only root node
	const disableReordering = !isEmpty(sortBy.name) || treeUtils.isTaskSingleRootNode(task.id);
	// TODO: isSummary hardcoded temporarily for sn-task-planner usecase
	const summaryTaskHandling = task.taskData.isSummary;
	const rearrangeRowsTitle = disableReordering ? REARRANGE_ROWS_DISABLED_TITLE : REARRANGE_ROWS_TITLE;
	const style = {
		display: task.temporary ? 'none': null
	};

	return (
		<div
			className={`sn-datagrid-cell sn-datagrid-cell--config ${selectedCellClassName}`}
			tabindex={tabIndex}
			aria-colindex='0'
			name={task.id + '_cell_config'}
			on-click={(e) => {
				e.stopPropagation();
				dispatch(CELL_CLICKED, {
					rowIndex: task.index + 1,
					columnId: CONFIG_SECTION_COLUMN_NAME,
					segment: segment
				});
			}}
			on-keydown={(e) => {
				switch (e.keyCode) {
					case ARROW_UP_KEY:
					case ARROW_DOWN_KEY:
					case ARROW_RIGHT_KEY:
					case ARROW_LEFT_KEY:
						e.preventDefault();
						dispatch(KEYBOARD_NAVIGATED, {
							keyCode: e.keyCode,
							isRTL,
							segment,
							bodyWrapperRef
						});
				}
			}}
			on-focus={() => {		
				if(segment === AGGREGATION) {
					dispatch(CELL_CLICKED, {
						rowIndex: task.index + 1,
						columnId: CONFIG_SECTION_COLUMN_NAME,
						segment: AGGREGATION
					});
				}
			}}
		>
			{
				allowRowsReordering
					? <div
						className={`sn-datagrid-cell-config-item ${disableReordering? 'sn-datagrid-cell-config-item--disabled': ''}`}
						tabIndex={disableReordering ? '-1': tabIndex}
						role="button"
						ariaLabel={rearrangeRowsTitle}
						title={rearrangeRowsTitle}
						on-click={() => {
							!disableReordering && initiateRowDragDebounced.cancel();
						}}
						on-mousedown={() => {
							!disableReordering && initiateRowDragDebounced();
						}}
						on-keydown={(event) => {
							if (event.keyCode === ENTER_KEY && !disableReordering) {
								event.preventDefault();
								event.stopImmediatePropagation();
								initiateRowDrag();
							}
						}}
					>
						<now-icon
							icon="grid-vertical-fill"
							size="md"
							bare={true}
						>
						</now-icon>
					</div>
					: <Fragment/>
			}
			{
				segment === AGGREGATION ? `${TOTAL}:` : ''
			}
			{
				rowSelectionCheckbox
					? <div
						style={style}
						className={`sn-datagrid-cell-config-item${ summaryTaskHandling
							? ' sn-datagrid-cell-config-item--disabled'
							: ''
						} button-action`}
						tabIndex={summaryTaskHandling ? '-1': tabIndex}
						role="button"
						ariaLabel={task.selected ? UNSELECT_TASK : SELECT_TASK}
						title={task.selected ? UNSELECT_TASK : SELECT_TASK}
						on-click={() => {
							if (!summaryTaskHandling) {
								dispatch(ROWS_SELECTION_TOGGLED, { task });
							}
						}}
						on-keydown={(event) => {
							if (event.keyCode === ENTER_KEY && !summaryTaskHandling) {
								event.preventDefault();
								event.stopImmediatePropagation();
								dispatch(ROWS_SELECTION_TOGGLED, { task });
							}
						}}
					>
						<now-icon
							size="md"
							icon={checkboxIconName}
							bare={true}
						>
						</now-icon>
					</div>
					: <Fragment/>
			}
			{
				setRowContextMenu
					? <div
						style={style}
						className={`sn-datagrid-cell-config-item${summaryTaskHandling? ' sn-datagrid-cell-config-item--disabled': ''}`}
						tabIndex={summaryTaskHandling ? '-1': tabIndex}
						title={ROW_CONTEXT_MENU}
						role="button"
						data-contextMenuId={contextMenuId}
						ariaLabel={ROW_CONTEXT_MENU}
						on-click={event => {
							if (!summaryTaskHandling) {
								handleRowContextMenuClick({
									event,
									task,
									dispatch,
									segment,
									contextMenuId
								});
							}
						}}
						on-keydown={(event) => {
							if (event.keyCode === ENTER_KEY && !summaryTaskHandling) {
								handleRowContextMenuClick({
									event,
									task,
									dispatch,
									segment,
									contextMenuId
								});
							}
						}}
					>
						<now-icon
							icon="ellipsis-v-fill"
							size="md"
							bare={true}
						></now-icon>
					</div>
					: <Fragment/>
			}
			{
				previewRecordIcon
					? <div
						style={style}
						className='sn-datagrid-cell-config-item'
						tabIndex={tabIndex}
						title={PREVIEW_RECORD}
						role="button"
						ariaLabel={PREVIEW_RECORD}
						on-click={event => {
							event.preventDefault();
							dispatch(SN_DATAGRID_PREVIEW_RECORD_ICON_CLICKED, {
								taskId: task.id
							});
						}}
					>
						<now-icon
							icon="circle-info-outline"
							size="md"
							bare={true}
						></now-icon>
					</div>
					: <Fragment/>
			}
		</div>
	);
};

export const renderConfigSection = ({
	visibleTasks,
	state,
	dispatch,
	segment,
	gridBodyContainerRef,
	configSectionWidth,
	allowRowsReordering,
	bodyWrapperRef
}) => {
	const {
		config,
		tasksToReorder,
		selectedCell = {},
		tree,
		properties: { dir },
		sortBy = {},
		selectedTasksCount = 0
	} = state;

	const isRTL = isComponentRTL(dir);
	const {tasks} = tree;
	const treeUtils = getTreeUtils(tree);

	const {
		rowHeight,
		alternateRowHighlight
	} = config;

	return (
		<div
			className="sn-datagrid-section sn-datagrid-section--config"
			style={{
				minWidth: `${configSectionWidth}px`
			}}
		>
			{
				visibleTasks.map((task) => {
					const taskPos = rowHeight * task.index;
					const style = {
						height: `${rowHeight}px`,
						transform: `translate(0px, ${taskPos}px)`,
						minWidth: `${configSectionWidth - CONFIG_SECTION_BORDER_WIDTH}px`
					};

					const alternateRowClassName = shouldHighlightRow(alternateRowHighlight, task.index)? ' sn-datagrid-row--alternate' : '';
					const selectedRowClassName = task.selected ? ` ${SELECTED_ROW_CLASS}`: '';
					const selectedRowCellClassName = selectedCell.rowIndex === task.index + 1 && selectedCell.segment === segment ? ` ${SELECTED_ROW_CELL_CLASS}`: '';
					const reorderingRowClassName = find(tasksToReorder, taskToReorder => taskToReorder.id === task.id) ? ' sn-datagrid-row--reordering' : '';

					return (
						<div
							className={`sn-datagrid-row${alternateRowClassName}${selectedRowClassName}${selectedRowCellClassName}${reorderingRowClassName}`} //eslint-disable-line
							style={style}
							data-rowindex={String(task.index+1)}
						>
							{ renderConfigSectionMarkUp({
								tasks,
								treeUtils,
								task,
								taskPos,
								segment,
								gridBodyContainerRef,
								config,
								dispatch,
								allowRowsReordering,
								selectedCell,
								isRTL,
								bodyWrapperRef,
								sortBy,
								selectedTasksCount
							})}
						</div>
					);
				})
			}
		</div>
	);
};
