import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import rtlBehavior from '@servicenow/behavior-rtl';
import view from './view';
import styles from './styles/index.scss';
import {
	gridActionHandlers,
	gridClientActionHandlers,
	dragAndDropActionHandlers,
	columnDragAndDropActionHandlers,
	keyboardNavHandlers,
	panelActionHandlers
} from '../core/actions';
import { CONFIG_CLIENT } from '../constants';
import transformConfig from '../core/helpers/transformConfig';
import componentHandlers from '../common/actions/component';
import { getGridInitialState } from '../core/helpers/gridStateParams';
import eventHandlers from '../core/eventHandlers';
import { transformState } from '../core/helpers/gridStateParams';

createCustomElement('sn-datagrid-client', {
	renderer: { type: snabbdom },
	view,
	initialState: {
		...getGridInitialState(),
		visibleHeight: 0,
		config: transformConfig(CONFIG_CLIENT),
		gridWidth: 0
	},
	transformState,
	styles,
	actionHandlers: {
		...componentHandlers,
		...gridActionHandlers,
		...gridClientActionHandlers,
		...dragAndDropActionHandlers,
		...columnDragAndDropActionHandlers,
		...keyboardNavHandlers,
		...panelActionHandlers
	},
	behaviors: [
		{ behavior: rtlBehavior }
	],
	properties: {
		config: {
			default: CONFIG_CLIENT
		},
		columns: {
			default: []
		},
		tasks: {
			default: []
		},
		sortBy: {
			default: {}
		},
		groupBy: {
			default: []
		}
	},
	eventHandlers
});
