import Column from './Column';
import {
	TASK_EXPANDED,
	EXPAND_ROW_TITLE,
	BUTTON_CLICKED,
	HIERARCHY_SPACING,
	COLLAPSE_ROW_TITLE,
	ENTER_KEY
} from '../../constants';

/**
 * Column model for the primary column. The primary column should have a type of `text`
 * The expected data type is string. If not, then the consumer has to write its own `getValue` and `getDisplayValue`
 */
export default class Text extends Column {
	renderer({ task, displayValue, dispatch, isRTL, tabIndex=0 }) {
		const { level, expanded, childCount } = task;
		const hasChildren = childCount > 0;
		const classList = `sn-datagrid-level-${level} ${
			hasChildren
				? 'sn-datagrid-cell-parent-cell'
				: 'sn-datagrid-cell-leaf-cell'
		}`;
		const contentClassName = hasChildren && expanded
			? 'sn-datagrid-tree-content sn-datagrid-tree-content--expanded'
			: 'sn-datagrid-tree-content';
		const style = {};
		if (!hasChildren) {
			if(level) {
				style.visibility = 'hidden';
			} else {
				// Root level items should not have extra space
				style.display = 'none';
			}
		}

		return (
			<div
				className={classList}
				style={{
					[isRTL ? 'paddingRight': 'paddingLeft']: `${HIERARCHY_SPACING * level}px`
				}}
				{...(hasChildren && {
					'aria-expanded': expanded ? 'true': 'false'
				})}
				aria-level={level}
			>

				<div
					style={style}
					className='sn-datagrid-icons sn-datagrid-icons--always-visible'
					tabIndex={tabIndex}
					role="button"
					ariaLabel={expanded ? COLLAPSE_ROW_TITLE : EXPAND_ROW_TITLE}
					title={expanded ? COLLAPSE_ROW_TITLE : EXPAND_ROW_TITLE}
					on-click={(event) => {
						event.stopPropagation();
						dispatch(BUTTON_CLICKED, {
							type: TASK_EXPANDED,
							id: task.id,
							expanded: !expanded
						});
					}}
					on-keydown={(event) => {
						event.stopPropagation();
						if (event.keyCode === ENTER_KEY) {
							dispatch(BUTTON_CLICKED, {
								type: TASK_EXPANDED,
								id: task.id,
								expanded: !expanded
							});
						}
					}}
				>
					<now-icon
						icon={expanded ? 'chevron-down-fill' : isRTL ? 'chevron-left-fill': 'chevron-right-fill'}
						size="sm"
						variant="tertiary"
						bare={true}
					></now-icon>
				</div>

				<div className={contentClassName}>
					{ displayValue }{task.childCount? ` (${task.childCount})`: ''}
				</div>
			</div>
		);
	}
}
