import {
	SCROLL_POSITION_UPDATED,
	COLUMN_SORTED,
	GRID_ALL_TASKS_EXPANDED,
	GRID_COLUMN_FILTERED,
	GRID_GROUP_UNGROUP,
	GRID_TASK_EXPANDED,
	SCROLL_ENDED
} from '../../constants';
import {
	handleSortByColumn,
	handleColumnFilter,
	handleGroupUnGroup,
	handleExpandCollapse,
	handleExpandCollapseAll,
	handleScrollPositionUpdated,
	handleScrollEnded
} from './helpers/gridClient';

export const gridClientActionHandlers = {
	[COLUMN_SORTED]: {
		effect: handleSortByColumn,
		stopPropagation: true
	},
	[GRID_COLUMN_FILTERED]: {
		effect: handleColumnFilter,
		stopPropagation: true
	},
	[GRID_GROUP_UNGROUP]: {
		effect: handleGroupUnGroup,
		stopPropagation: true
	},
	[GRID_TASK_EXPANDED]: {
		effect: handleExpandCollapse,
		stopPropagation: true
	},
	[GRID_ALL_TASKS_EXPANDED]: {
		effect: handleExpandCollapseAll,
		stopPropagation: true
	},
	[SCROLL_POSITION_UPDATED]: {
		modifier: {
			name: 'throttle',
			limit: 20
		},
		effect: handleScrollPositionUpdated,
		stopPropagation: true
	},
	[SCROLL_ENDED]: {
		// we are triggering SCROLL_ENDED on every scroll event
		// hence we are using debounce here to trigger it only when scroll event stops
		modifier: { name: 'debounce', delay: 80 },
		effect: handleScrollEnded,
		stopPropagation: true
	}
};
