import columnStore from './index';

function generateColumnId(column) {
	/**
	 * TODO: replace columnId with column.name everywhere
	 * we are assuming all column names should be unique always
	 * hence column name should be enough to identify columns
	 * and subsequently we should remove usage of column id from every place
	 * and should just use column name
	 */

	return column.name;
}

export default class Model {
	constructor(column, parent = null) {
		const columnType = columnStore[column.type] || columnStore.string;

		this.columnDefinition = column;
		this.width = column.width;
		this.autoWidth = 0;
		this.hidden = column.hidden;
		this.filter = null;
		this.renderer = column.renderer || columnType.renderer;
		this.getValue = column.getValue || columnType.getValue;
		this.getDisplayValue = column.getDisplayValue || columnType.getDisplayValue;
		this.setValue = column.setValue || columnType.setValue;
		this.getSortComparator = column.getSortComparator || columnType.getSortComparator;
		this.section = column.section;
		this.collapsed = column.collapsed || false;
		this.expandSubColumns = column.expandSubColumns || false;
		this.subColumns = [];
		this.parent = parent;
		this.columnId = generateColumnId(column, parent);
		this.editable = !!column.editable;
		this.copyPaste = column.copyPaste !== false;
		this.timeVariantGroup = column.timeVariantGroup;
		this.hideable = column.hideable !== false;
		this.configExpanded = false;
	}
}
