import '@servicenow/now-text-link';
import '@servicenow/now-button';
import noop from 'lodash/noop';
import isEmpty from 'lodash/isEmpty';
import { CONTEXTMENU_OPTION_CLICKED } from '../constants';
import { createLog } from '../util/logger';
import { getContextMenuAPIs } from '../core/helpers/editingHelper';
import { trimLabel } from '../core/helpers/accessibilityHelpers';

const LOG = createLog('ContextMenu');

/**
 * Custom Event Dispatcher
 * @param {Object} dispatch
 * @param {String} eventName
 * @param {Object} metadata
 * @param {Object} optionalPayload
 */
const customEventDispatcher = (dispatch, eventName, data, optionalPayload, disabled) => {
	return () => {
		if (!disabled) {
			dispatch(CONTEXTMENU_OPTION_CLICKED, {
				eventName,
				data,
				optionalPayload
			});
		}
	};
};

const renderContextMenuItems = (
	contextMenuRenderer = noop, payload, dispatch, defaultItems = [], contextMenuAPIs
) => {
	let contextMenuRendererItems = [];
	try {
		contextMenuRendererItems = contextMenuRenderer(payload, contextMenuAPIs) || [];
	} catch (e) {
		LOG.error('Error in Context Menu Renderer');
		LOG.error(e);
	}

	return contextMenuRendererItems.concat(defaultItems).map(({
		eventName,
		label,
		icon = '',
		disabled = false,
		optionalPayload = {},
		divider = false
	}) => {
		const trimedLabel = trimLabel(label);

		return <now-button
			bare
			variant="secondary"
			size="md"
			href="javascript:void(0)"
			className={divider ? "cell-context-menu-border" : ""}
			icon={icon}
			tooltipContent={trimedLabel.includes('...') ? label : null}
			disabled={disabled}
			configAria={{ 'aria-label': label }}
			label={trimedLabel}
			on-click={customEventDispatcher(dispatch,
				eventName,
				payload,
				optionalPayload,
				disabled
			)}
		></now-button>;
	}
	);
};

export default (state, { dispatch }) => {
    const { 
        contextMenuRenderer,
        contextMenu,
        defaultItems,
        parentState
    } = state.properties;
	const { task, column } = contextMenu;
	const payload = {
		task: task.taskData
	};
	if (!isEmpty(column.columnDefinition)) {
		payload.column = column.columnDefinition;
	}
	const contextMenuAPIs = getContextMenuAPIs(parentState, task);

	return (
		<div
			on-click={(event => event.stopImmediatePropagation())}
			className="cell-context-menu">
			{ renderContextMenuItems(
				contextMenuRenderer,
				payload,
				dispatch,
				defaultItems,
				contextMenuAPIs
			) }
		</div>
	);
};
