import isEmpty from 'lodash/isEmpty';
import { createRef } from '@servicenow/ui-renderer-snabbdom';
import { getPrefixedActionNames } from './helpers/editingHelper';
import {
	ENTER_KEY,
	ESC_KEY,
	DATA_TYPES,
	TAB_KEY,
	ARROW_DOWN_KEY,
	ARROW_UP_KEY,
	ARROW_LEFT_KEY,
	ARROW_RIGHT_KEY,
	S_CHAR_KEY,
	NOW_BUTTON
} from '../../constants';
import { getSlotName } from '../../core/helpers/editingHelper';
import { EDIT_ID_CLASS } from './constants';

const {
	STRING_TYPE,
	NUMBER_TYPE,
	DATE_TIME_TYPE,
	DATE_TYPE,
	REFERENCE_TYPE,
	AVATAR_TYPE,
	TEXT_TYPE
} = DATA_TYPES;

const isArrowKeyClicked = event => {
	return event.keyCode === ARROW_DOWN_KEY ||
		event.keyCode === ARROW_UP_KEY ||
		event.keyCode === ARROW_LEFT_KEY ||
		event.keyCode === ARROW_RIGHT_KEY;
};

const keyboardAccessibilityFn = ({
	event,
	dispatch,
	name,
	actions
}) => {
	if (isArrowKeyClicked(event) || event.keyCode === ENTER_KEY) {
		// stopping propagation to disable keyboard navigation and cell level actions
		event.stopPropagation();
	} else if (event.keyCode === TAB_KEY) {
		// When last editable element is tabbed we have to come out of editing mode
		dispatch(actions.EDIT_CELL_TABBED, { name, event });
	} else if (event.keyCode === ESC_KEY) {
		event.stopPropagation();
		// Come out of editing mode when the user presses esc
		dispatch(actions.ROW_EDIT_ENDED);
	}
};

const getEditForm = ({
	columnConfig,
	actions,
	taskId,
	dispatch,
	table,
	config
}) => {
	const choiceOrReferenceRef = createRef();
	const { type, name, displayValue, value, isChoice, choices = [], attributes, dbColumnName } = columnConfig;
	const dbFieldName = dbColumnName || name;
	const dictionary = {
		attributes
	};
	// maintaining backward compatibility between paris and quebec
	const slotName = columnConfig.slotName || getSlotName(name, taskId);
	const fullWidthStyle = { width: '100%' };
	let format = config.dateTimeFormat || 'YYYY/MM/DD HH:mm:ss';
	let timePicker = true;

	const handleChoiceOrReferenceValueChange = ({ value, displayValue }) => {
		/**
		 * We have to manually update the value props as sn-record-choice doesn't update the value automatically
		 * We have to manually update the value and displayValue props as sn-record-reference-connected resets
		 * after losing focus
		 */
		choiceOrReferenceRef.current.value = value;
		choiceOrReferenceRef.current.displayValue = displayValue;
		dispatch(actions.CHOICE_OR_REF_VALUE_CHANGED, {
			type,
			name,
			value,
			displayValue
		});
	};
	const handleKeydown = event => {
		if ((event.ctrlKey|| event.metaKey) && event.keyCode === S_CHAR_KEY) {
			event.stopPropagation();
			event.preventDefault();
			dispatch(actions.ROW_EDITED);
		} else {
			keyboardAccessibilityFn({
				event,
				name,
				dispatch,
				actions
			});
		}
	};
	const handleKeyup = event => {
		if (event.keyCode === ENTER_KEY) {
			const path = event.path || (event.composedPath && event.composedPath());
			if (path.length > 0 && path[0].className.indexOf(NOW_BUTTON) > -1) {
				return;
			}
			dispatch(actions.ROW_EDITED);
		}
	};
	const handleClick = event => event.stopPropagation();

	if (type === DATE_TYPE) {
		format = config.dateFormat || 'YYYY/MM/DD';
		timePicker = false;
	}

	if (isChoice && !isEmpty(choices)) {
		return <sn-record-choice
			className={EDIT_ID_CLASS}
			style={fullWidthStyle}
			ref={choiceOrReferenceRef}
			value={displayValue}
			slot={slotName}
			choices={choices}
			onValueChange={handleChoiceOrReferenceValueChange}
			on-keydown={handleKeydown}
			on-click={handleClick}
		/>;
	}
	switch (type) {
		case STRING_TYPE:
		case NUMBER_TYPE:
		case TEXT_TYPE:
		default: return <now-input
			name={name}
			className={EDIT_ID_CLASS}
			type={type === NUMBER_TYPE ? 'number' : 'text'}
			slot={slotName}
			style={fullWidthStyle}
			value={type === NUMBER_TYPE ? value : displayValue}
			/**
					We have to use both keydown and keyup events for accessibility
					The event order is keydown -> character prints -> keyup

					We have to stop the propagation in the keydown event since it's the first event and the same event
					is listened in the grid cell.
					And we can can't dispatch the editing event in keydown as the character hasn't printed yet.
					So we have to dispatch the editing event in keyup

					We can't use just keypress for both the events as then we won't be able to stop the keydown event
				*/
			on-keyup={handleKeyup}
			on-keydown={handleKeydown}
			on-click={handleClick}
		></now-input>;
		case DATE_TYPE:
		case DATE_TIME_TYPE: return <now-record-date-picker
			className={EDIT_ID_CLASS}
			slot={slotName}
			value={displayValue}
			style={fullWidthStyle}
			append-to-payload={{ name }}
			format={format}
			timePicker={timePicker}
			on-click={handleClick}
			on-keydown={handleKeydown}
			on-keyup={handleKeyup}
		/>;
		case AVATAR_TYPE:
		case REFERENCE_TYPE: return <sn-record-reference-connected
			style={fullWidthStyle}
			className={EDIT_ID_CLASS}
			slot={slotName}
			tableName={table}
			dictionary={dictionary}
			ref={choiceOrReferenceRef}
			fieldName={dbFieldName}
			recordSysId={taskId}
			onValueChange={handleChoiceOrReferenceValueChange}
			displayValue={displayValue}
			value={value}
			on-click={handleClick}
			on-keydown={handleKeydown}
			on-keyup={handleKeyup}
			/>;
	}
};

export const createEditSlots = ({
	rowEditing,
	dispatch,
	config = {},
	actionPrefix = ''
}) => {
	return () => {
		if (!config.readonly && rowEditing) {
			const { taskId, columnsConfig, table } = rowEditing;
			const actions = getPrefixedActionNames(actionPrefix);

			return columnsConfig.map(columnConfig => {
				return getEditForm({
					columnConfig,
					actions,
					taskId,
					dispatch,
					table,
					config
				});
			});
		}
	};
};
