import {
	ROW_DRAG_STARTED,
	ROW_DRAG_ENDED,
	COLUMN_DRAG_EVENTS
} from '../../constants';
import { handleRowDragStart, handleRowDragEnd } from './helpers/rowReorder';
import { columnDragEventStartHandler, columnDragEventEndHandler, columnDragEventOverHandler, columnDragEventLeaveHandler } from './helpers/columnDragGrouping';


export const dragAndDropActionHandlers = {
	[ROW_DRAG_STARTED]: {
		stopPropagation: true,
		effect({
			action: { payload: { tasksToReorder, draggedTaskPos } },
			updateState
		}) {
			handleRowDragStart({ tasksToReorder, draggedTaskPos, updateState });
		}
	},
	[ROW_DRAG_ENDED]: {
		stopPropagation: true,
		effect({
			state,
			updateState,
			dispatch,
			action: { payload: { targetIndex, parentSourceIndex }, error }
		}) {
			handleRowDragEnd({ state, updateState, dispatch, targetIndex, parentSourceIndex, error });
		}
	},
	[COLUMN_DRAG_EVENTS.START]: {
		stopPropagation: true,
		effect: columnDragEventStartHandler
	},
	[COLUMN_DRAG_EVENTS.END]: {
		stopPropagation: true,
		effect: columnDragEventEndHandler
	},
	[COLUMN_DRAG_EVENTS.OVER]: {
		stopPropagation: true,
		effect: columnDragEventOverHandler
	},
	[COLUMN_DRAG_EVENTS.LEAVE]: {
		stopPropagation: true,
		effect: columnDragEventLeaveHandler
	}
};
