import {
	GRID_COLUMN_RESIZED,
	MIN_COLUMN_WIDTH,
	END_RESIZE_GRID_COLUMN,
	MIN_SECTION_WIDTH,
	SECTION_RESIZED,
	END_RESIZE_SECTION,
	MIN_COLUMN_CONTENT_WIDTH
} from '../../constants';
import throttle from 'lodash/throttle';
import { getColumnBufferWidth } from './tasks';
import { getMaxPinnedSectionWidth } from './sections';

const handleResize = (onResize, onResizeEnd) => {
	const throttledOnResize = throttle(onResize, 20);

	function stopResize() {
		window.removeEventListener('mouseup', stopResize);
		window.removeEventListener('mousemove', throttledOnResize);
		onResizeEnd();
	}
	window.addEventListener('mousemove', throttledOnResize);
	window.addEventListener('mouseup', stopResize);
};

export const resizeColumn = ({ column, dispatch, gridCellHeaderStart, isRTL }) => {
	const minColumnWidth = getColumnBufferWidth(column) + MIN_COLUMN_CONTENT_WIDTH || MIN_COLUMN_WIDTH;
	const onResize = event => {
		const canResizeColumn = isRTL
			? event.pageX + minColumnWidth < gridCellHeaderStart
			: event.pageX - minColumnWidth > gridCellHeaderStart;

		if (canResizeColumn) {
			dispatch(GRID_COLUMN_RESIZED, {
				column,
				width: Math.abs(event.pageX - gridCellHeaderStart)
			});
		}
	};
	const onResizeEnd = () => dispatch(END_RESIZE_GRID_COLUMN);

	handleResize(onResize, onResizeEnd);
};

export const resizeSection = ({ section, dispatch, sectionHeaderStart, columns, gridWidth, sectionsWidth }) => {
	const maxSectionWidth = getMaxPinnedSectionWidth({ columns, gridWidth, sectionsWidth, section });
	const onResize = event => {
		const newSectionWidth = Math.abs(event.pageX - sectionHeaderStart);

		if (newSectionWidth >= MIN_SECTION_WIDTH && newSectionWidth <= maxSectionWidth) {
			dispatch(SECTION_RESIZED, {
				section,
				newSectionWidth
			});
		}
	};
	const onResizeEnd = () => dispatch(END_RESIZE_SECTION);

	handleResize(onResize, onResizeEnd);
};
