import isEmpty from 'lodash/isEmpty';
import {
	ROW_EDIT_MODE,
	GRID_COPY_CELLS,
	GRID_PASTE_CELLS,
	GRID_EDIT_STARTED,
	GRID_COPY_ROWS,
	GRID_PASTE_ROWS,
	GRID_INSERT_ROWS,
	EDIT_CELL,
	COPY_CELL,
	PASTE_CELL,
	EDIT_ROW,
	COPY_ROW,
	PASTE_ROW,
	CELL_EDIT_MODE,
	INSERT_COPIED_ROW_ABOVE,
	INSERT_COPIED_ROW_BELOW,
	INSERT_MODE,
	V_CHAR_KEY,
	C_CHAR_KEY
} from '../../constants';
import { getTreeUtils } from '../store/tree/IndexTree';

export const getSlotName = (columnName, taskId) => {
	return taskId + '_'+ columnName;
};

const getColConfigForEditingUtil = (column, task) => {
	const { columnDefinition, getDisplayValue, getValue } = column;
	const displayValue = getDisplayValue(column, task);
	const value = getValue(column, task);

	return {
		...columnDefinition,
		displayValue,
		value,
		slotName: getSlotName(columnDefinition.name, task.id)
	};
};

const getAllColumnConfig = (columns, task) => {
	const allColumns = [
		...columns.left,
		...columns.center,
		...columns.right
	];
	const colConfig = [];

	allColumns.forEach(col => {
		if (!isEmpty(col.subColumns)) {
			col.subColumns.forEach(subCol => {
				if (subCol.editable) {
					colConfig.push(getColConfigForEditingUtil(subCol, task));
				}
			});
		} else if (col.editable) {
			colConfig.push(getColConfigForEditingUtil(col, task));
		}
	});

	return colConfig;
};

export const getColConfigForEditing = (columns, task, column, editMode) => {
	return editMode === ROW_EDIT_MODE
		? getAllColumnConfig(columns, task)
		: column.editable ? [getColConfigForEditingUtil(column, task)]: [];
};

export const getCellContextMenuItems = (state, cellContextMenu) => {
	const { config, clipBoard = [], selectedCell: { selectedRowIds = new Set() } } = state;
	const { readonly = true, editMode } = config;
	const items = [];
	const { task, column } = cellContextMenu;

	if (!readonly && !task.temporary) {
		const selectedTasks = [...selectedRowIds];
		const countText = selectedTasks.length > 0 ? ` (${selectedTasks.length})`: '';
		const pasteCountText = clipBoard.length > 0 ? ` (${clipBoard.length})`: '';

		if (column.editable && editMode === CELL_EDIT_MODE) {
			items.push({
				label: `${EDIT_CELL}${countText}`,
				eventName: GRID_EDIT_STARTED,
				optionalPayload: { task, column }
			});
		}
		if (column.copyPaste) {
			items.push({
				label: `${COPY_CELL}${countText}`,
				eventName: GRID_COPY_CELLS
			});
		}
		if (column.copyPaste && column.editable) {
			items.push({
				label: `${PASTE_CELL}${pasteCountText}`,
				disabled: isEmpty(clipBoard),
				eventName: GRID_PASTE_CELLS
			});
		}
	}

	return items;
};

export const getRowContextMenuItems = (state, contextMenu) => {
	const { config, copiedRowIds = [], selectedTasksCount = 0 } = state;
	const { task } = contextMenu;
	const { readonly = true, editMode, rowCopyPaste } = config;
	const items = [];
	const count = task.selected ? selectedTasksCount : selectedTasksCount + 1;
	const countText = count > 0 ? ` (${count})`: '';
	const pasteCountText = copiedRowIds.length > 0 ? ` (${copiedRowIds.length})`: '';

	if (!readonly && !task.temporary) {
		if (editMode === ROW_EDIT_MODE) {
			items.push({
				label: `${EDIT_ROW}${countText}`,
				eventName: GRID_EDIT_STARTED,
				optionalPayload: { task }
			});
		}
		if (rowCopyPaste) {
			items.push({
				label: `${COPY_ROW}${countText}`,
				eventName: GRID_COPY_ROWS
			});
			items.push({
				label: `${PASTE_ROW}${pasteCountText}`,
				disabled: isEmpty(copiedRowIds),
				eventName: GRID_PASTE_ROWS
			});
			items.push({
				label: `${INSERT_COPIED_ROW_ABOVE}${pasteCountText}`,
				disabled: isEmpty(copiedRowIds),
				eventName: GRID_INSERT_ROWS,
				optionalPayload: { mode: INSERT_MODE.ABOVE }
			});
			items.push({
				label: `${INSERT_COPIED_ROW_BELOW}${pasteCountText}`,
				disabled: isEmpty(copiedRowIds),
				eventName: GRID_INSERT_ROWS,
				optionalPayload: { mode: INSERT_MODE.BELOW }
			});
		}
	}

	return items;
};

export const getContextMenuAPIs = (state, task) => {
	const getSelectedTasksCount = () => state.selectedTasksCount;
	const isGrouped = () => state.groupBy && state.groupBy.length > 0;
	const isSorted = () => !isEmpty(state.sortBy.name);
	const isCurrentTaskSelected = () => task.selected;
	const isFiltered = () => !isEmpty(state.tree._filterExpression);

	return {
		getSelectedTasksCount,
		isGrouped,
		isSorted,
		isFiltered,
		isCurrentTaskSelected
	};
};

export const handleCopyPasteKeyboardShortcuts = (
	e, dispatch, selectedCell, tree, task, column, isRowPasteDisabled, isCellPasteDisabled, isRowCopyPasteAllowed
) => {
	if ((e.ctrlKey || e.metaKey) &&
	(e.keyCode === V_CHAR_KEY || e.keyCode === C_CHAR_KEY)) {
		const isRowAction = e.ctrlKey && e.metaKey;
		const isPasteAction = e.keyCode === V_CHAR_KEY;
		const isRowActionAllowed = isRowCopyPasteAllowed &&
			(!isPasteAction || isPasteAction && isRowPasteDisabled);
		const isCellActionAllowed = column.copyPaste &&
			(!isPasteAction || isPasteAction && isCellPasteDisabled);

		if (isRowAction && isRowActionAllowed ||
			!isRowAction && isCellActionAllowed) {
			return;
		}
		const payload = { task, column };

		if (isRowAction) {
			dispatch(
				isPasteAction ? GRID_PASTE_ROWS: GRID_COPY_ROWS,
				payload
			);
		} else if (!column.groupColumn) {
			const { selectedRowIds = new Set().add(task.id) } = selectedCell;
			const treeUtils = getTreeUtils(tree);

			dispatch(
				isPasteAction ? GRID_PASTE_CELLS: GRID_COPY_CELLS,
				{ ...payload, selectedTasks: [...selectedRowIds].map(id => treeUtils.getNodeById(id)) }
			);
		}
	}
};
