import { actionTypes } from '@servicenow/ui-core';

import { KEYBOARD_NAVIGATED } from '../../constants';

import {
	handleComponentRendering,
	handleKeyboardNav
} from './helpers/keyboardNav';

const { COMPONENT_RENDERED } = actionTypes;

export const keyboardNavHandlers = {
	[COMPONENT_RENDERED]: {
		effect: handleComponentRendering,
		stopPropagation: true
	},
	[KEYBOARD_NAVIGATED]: {
		modifier: { name: 'throttle', limit: 75 }, // throttle is to avoid focus jumping
		effect: handleKeyboardNav,
		stopPropagation: true
	}
};
