import {
	OPERATORS,
	LIKE,
	CONTAINS,
	EQUAL_TO,
	NOT_EQUAL_TO,
	EQUALS,
	NOTEQUALS,
	DATA_TYPES,
	IN
} from '../../../constants';
import { getColumnModelFromId } from '../../columnModel';

const filterValidator = (obj, filterExpressionObj, columns, valueMap) => {
	const {
		columnId,
		operator,
		value
	} = filterExpressionObj;
	const colModel = getColumnModelFromId(columns, columnId);
	const { getDisplayValue, getValue } = colModel;
	const cellValue = colModel.columnDefinition.type === DATA_TYPES.NUMBER_TYPE ||
		colModel.columnDefinition.isChoice ? getValue(colModel, obj) : getDisplayValue(colModel, obj);

	try {
		switch (operator) {
			case LIKE: {
				if (colModel.columnDefinition.type === DATA_TYPES.AVATAR_GROUP_TYPE) {
					const { _data: { avatar_group }} = obj;
					for (let index = 0; index < avatar_group.length; index++) {
						const displayValue = avatar_group[index].displayValue;
						if (displayValue.toLowerCase().includes(value.toLowerCase())) {
							return true;
						}
					}
					return false;
				}
				return colModel.columnDefinition.type === DATA_TYPES.NUMBER_TYPE
					? parseFloat(value.replace(',', ''), 10) === parseFloat(String(cellValue).replace(',', ''), 10)
					: cellValue.toLowerCase().includes(value.toLowerCase());
			}
			case EQUAL_TO:
				return cellValue.startsWith(value);
			case CONTAINS:
				return cellValue.includes(value);
			case NOT_EQUAL_TO:
				return cellValue !== value;
			case EQUALS:
				return cellValue.startsWith(value);
			case NOTEQUALS:
				return cellValue !== value;
			case IN: {
				if (cellValue === undefined) {
					return valueMap.hasOwnProperty(null) && valueMap.null.selected;
				}
				if (valueMap[cellValue]) {
					return valueMap[cellValue].selected;
				} else {
					return false;
				}
			}
			default:
				return false;
		}
	} catch (e) {
		return false;
	}
};

const OperationSolver = expression => {
	for (let i = 0; i < OPERATORS.length; i++) {
		if (expression && expression.search(OPERATORS[i]) > 0) {
			const lst = expression.split(OPERATORS[i]);

			return {
				columnId: lst[0],
				operator: OPERATORS[i],
				value: lst[1]
			};
		}
	}
};

export const ExpressionSolver = (expressionStatement) => {
	return expressionStatement.split('^').map(expression => {
		return OperationSolver(expression);
	});
};


export const ExpressionValidator = (data, columns, expressions, choiceListValueMaps = {}) => {
	for (let i = 0; i < expressions.length; i++) {
		let _result = true;

		if (Array.isArray(expressions[i])) {
			_result = ExpressionValidator(expressions[i]);
		} else if (expressions[i]) {
			const { columnId } = expressions[i];
			const valueMap = choiceListValueMaps[columnId] ? choiceListValueMaps[columnId].selectedChoices : {};
			_result = filterValidator(data, expressions[i], columns, valueMap);
		}

		if (!_result) {
			return false;
		}
	}

	return true;
};

/**
 * Generates Filter expression based on choices.
 * @param {Object} selectedChoices
 */
const generateInChoiceFilterExpression = (selectedChoices) => {
	const choices = Object.keys(selectedChoices);
	const selectedChoicesArr = [];
	for (let index = 0; index < choices.length; index++) {
		if(selectedChoices[choices[index]].selected) {
			selectedChoicesArr.push(selectedChoices[choices[index]].displayValue);
		}
	}
	return selectedChoicesArr.join(',');
}

export const makeFilterExpression = (columns) => {
	let filterExpression = '';

	const _makeFilterExpression = (_columns) => {
		_columns.forEach(({
			filter,
			columnId,
			subColumns = []
		}) => {
			if (filter) {
				const { operator } = filter;

				switch (operator) {
					case IN: {
						const { selectedChoices } = filter;

						filterExpression = `${filterExpression}^${columnId}${filter.operator}${generateInChoiceFilterExpression(selectedChoices)}`;
						break;
					}
					default:
						filterExpression = `${filterExpression}^${columnId}${filter.operator}${filter.text}`;
						break;
				}
			}

			if (subColumns.length > 0) {
				_makeFilterExpression(subColumns);
			}
		});
	};

	_makeFilterExpression(columns);

	return filterExpression;
};
