import { actionTypes } from '@servicenow/ui-core';
import { FILTER_UPDATED } from '../constants';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

export const actionHandlers = {
	[FILTER_UPDATED]: {
		stopPropagation: true,
		effect({ action: { payload }, updateState }) {
			updateState({
				filter: { ...payload }
			});
		}
	},
	[COMPONENT_BOOTSTRAPPED]: {
		stopPropagation: true,
		effect({ state: { properties: { column: { filter = {} } } }, updateState }) {
			updateState({
				filter
			});
		}
	}
};
