import {
	HIERARCHY_SPACING,
	MAXIMUM_COLUMN_WIDTH,
	TOTAL_COLUMN_PADDING_WIDTH,
	SMALL_ICON_BUTTON_WIDTH,
	DATA_TYPES,
	MIN_COLUMN_CONTENT_WIDTH,
	FONT_FAMILY,
	PRIMARY,
	CONFIG_SECTION_COLUMN_NAME,
	AVATAR_COLUMN_PADDING_WIDTH,
	AVATAR_GROUP_COLUMN_WIDTH
} from '../../constants';
import { updateColumns, getAggregationConfigs } from '../columnModel';
import { getAllVisibleBottomLevelCols, getAllLeafColumns } from './sections';
import { getTreeUtils } from '../store/tree/IndexTree';

export const countSelectedTasks = tasks => {
	return tasks.reduce((prevCount, task) => {
		return task.selected ? ++prevCount : prevCount;
	}, 0);
};

export const getColumnBufferWidth = (column) => {
	const { columnDefinition: { type, renderer } } = column;

	// As header cell is also measured, context menu is always present
	let bufferWidth = TOTAL_COLUMN_PADDING_WIDTH + SMALL_ICON_BUTTON_WIDTH;

	if (type === DATA_TYPES.TEXT_TYPE) { // for expand collapse icon
		bufferWidth += SMALL_ICON_BUTTON_WIDTH;
	}
	if (renderer) { // custom renderer usually has extra content, so increasing the buffer to accomodate it
		bufferWidth += SMALL_ICON_BUTTON_WIDTH;
	}

	return bufferWidth;
};

const getHeaderCellWidth = (label, ctx, font) => {
	ctx.font = `600 ${font}`;
	const colNameWidth = ctx.measureText(label).width;

	return Math.max(MIN_COLUMN_CONTENT_WIDTH, colNameWidth);
};

export const getColumnAutoWidth = (tasks, column) => {
	const bufferWidth = getColumnBufferWidth(column);

	if (column.columnDefinition.type === 'avatar_group') {
		return AVATAR_GROUP_COLUMN_WIDTH + bufferWidth;
	}

	const ctx = document.createElement('canvas').getContext('2d');
	const { type, groupColumn, label } = column.columnDefinition;

	// As internal conponent font-size is 1rem, it will match document.body fontsize
	const font = `${getComputedStyle(document.documentElement).fontSize} ${FONT_FAMILY}`;
	const columnContentWidth = tasks.reduce((taskWidth, currentTask) => {
		const value = groupColumn
			? `${column.getDisplayValue(column, currentTask) || ''}${currentTask.childCount? ` (${currentTask.childCount})`: ''}`
			: column.getDisplayValue(column, currentTask) || '';
		let currentTaskWidth = 0;
		// Extra spacing for bold text
		if (type === DATA_TYPES.TEXT_TYPE) {
			ctx.font = '600 ' + font;
			currentTaskWidth += HIERARCHY_SPACING * (currentTask.level + 1);
		} else {
			ctx.font = 'normal ' + font;
		}
		currentTaskWidth += ctx.measureText(value).width;

		if (column.columnDefinition.type === 'avatar') {
			currentTaskWidth += AVATAR_COLUMN_PADDING_WIDTH;
		}

		return Math.max(currentTaskWidth, taskWidth);
	}, getHeaderCellWidth(label, ctx, font));

	return Math.ceil(Math.min(columnContentWidth + bufferWidth, MAXIMUM_COLUMN_WIDTH));
};

export const autoSizeColumn = ({ visibleTasks, columns, column }) => {
	const autoWidth = getColumnAutoWidth(visibleTasks, column);

	return updateColumns(columns, column, { autoWidth, width: undefined });
};

export const autoSizeAllColumns = ({ visibleTasks, columns }) => {
	const allAutoSizableColumns = getAllVisibleBottomLevelCols(columns);
	let updatedColumns = columns;

	allAutoSizableColumns.forEach(column => {
		updatedColumns = autoSizeColumn({ column, columns: updatedColumns, visibleTasks });
	});

	return updatedColumns;
};

export const getSelectedCellFromTasksToReorder = ({
	tasksToReorder,
	tree,
	selectedCell = {}
}) => {
	let updatedSelectedCell = selectedCell;

	if (tasksToReorder && tasksToReorder[0]) {
		const task = tree.tasks.find(task => tasksToReorder[0].id === task.id);
		if (task) {
			updatedSelectedCell = {
				rowIndex: task.index + 1,
				columnId: CONFIG_SECTION_COLUMN_NAME,
				segment: PRIMARY
			};
		}
	}

	return updatedSelectedCell;
};


/*
 * This method is meant to be used when task prop is updated
 * When a new tree is built, it updates the tree with respect to the current state
 * and returns updated tasks and other params
 */
export const getUpdatedTasksWithCurrentState = ({
	selectedCell,
	tree,
	columns,
	tasksToReorder,
	selectedTasks = []
}) => {
	const treeUtils = getTreeUtils(tree);

	selectedTasks.forEach(selectedTask => {
		const newTask = tree.tasks.find(updatedTask => selectedTask.id === updatedTask.id);

		if (newTask) {
			treeUtils.updateTask(newTask, {
				selected: true
			});
		}
	});

	const aggregateConfig = getAggregationConfigs(columns);
	const filterExpression = getAllLeafColumns(columns)
		.filter(column => column.filter)
		.reduce((filterExpression, column) => {
			const { columnId, filter } = column;

			return `${filterExpression}^${columnId}${filter.operator}${filter.text}`;
		}, '');

	treeUtils.filter(filterExpression, aggregateConfig, columns);

	const updatedSelectedCell = getSelectedCellFromTasksToReorder({
		tasksToReorder,
		tree,
		selectedCell
	});

	return {
		tree: {
			...tree
		},
		tasksToReorder: null,
		selectedCell: updatedSelectedCell,
		selectedTasksCount: countSelectedTasks(tree.tasks)
	};
};

export const getEditableSelectedTasks = (
	tasks, id
) => tasks.filter(task => task.selected && !task.temporary || task.id === id);
