import renderCell from './cell';
import isEmpty from 'lodash/isEmpty';
import find from 'lodash/find';
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line
import { getVisibleCols, getVisibleSubCols, shouldHighlightRow } from '../../helpers/sections';
import { SELECTED_ROW_CLASS, SELECTED_ROW_CELL_CLASS } from '../../../constants';
let columnCount;

const renderCells = ({ columns, config, task, dispatch, state, gridBodyRef, section, segment, bodyWrapperRef }) => {
	return columns.map((column) => {
		return !isEmpty(column.subColumns)
			? renderCells({
				columns: getVisibleSubCols(column),
				config,
				task,
				dispatch,
				state,
				gridBodyRef,
				section,
				segment,
				bodyWrapperRef
			})
			: renderCell({
				config,
				column,
				task,
				dispatch,
				state,
				colIndex: columnCount++,
				gridBodyRef,
				columns,
				section,
				segment,
				bodyWrapperRef
			});
	});
};

const renderRow = ({ config, columns, task, dispatch, state, gridBodyRef, section,
	segment, selectedCell = {}, bodyWrapperRef }) => {
	columnCount = 1;
	const { rowHeight, alternateRowHighlight } = config;
	const { tasksToReorder, isReorderingRows } = state;
	if (isEmpty(task)) {
		return <Fragment/>;
	}
	const { index } = task;
	const style = {
		height: `${rowHeight}px`,
		top: `${rowHeight * index}px`
	};

	const selectedRowClassName = task.selected ? ` ${SELECTED_ROW_CLASS}`: '';
	const selectedRowCellClassName = selectedCell.rowIndex === index + 1 && selectedCell.segment === segment ? ` ${SELECTED_ROW_CELL_CLASS}`: '';
	const alternateRowClassName = shouldHighlightRow(alternateRowHighlight, index) ? ' sn-datagrid-row--alternate' : '';
	const reorderingRowClassName = isReorderingRows && find(tasksToReorder, taskToReorder => taskToReorder.id === task.id) ? ' sn-datagrid-row--reordering' : '';
	const classNames = `sn-datagrid-row${alternateRowClassName}${selectedRowClassName}${selectedRowCellClassName}${reorderingRowClassName}`; //eslint-disable-line

	return (
		<Fragment>
			<div
				className={classNames}
				style={style}
				role="row"
				data-rowindex={String(index+1)}
			>
				{renderCells({
					columns: getVisibleCols(columns),
					config,
					task,
					dispatch,
					state,
					gridBodyRef,
					section,
					segment,
					bodyWrapperRef
				})}
			</div>
		</Fragment>
	);
};

export default renderRow;
