import renderGridHeader from '../core/views/header';
import renderGridBody from '../core/views/body';
import renderVirtualScroller from '../core/views/virtualScroller';
import { Fragment } from '@seismic/snabbdom-renderer';
import { createRef } from '@servicenow/ui-renderer-snabbdom';
import { getScrollSynchronizer } from '../core/helpers/scrollSynchronizer';
import { isComponentRTL } from '../core/helpers/gridStateParams';
import {
	PRIMARY,
	AGGREGATION,
	HELP_LABEL,
	PERSONALIZE_LABEL, SIDE_PANELS
} from '../constants';
import '@servicenow/now-pagination';
import '../sn-datagrid-header-toolbar';
import '../sn-datagrid-config-panel';
import { renderSidePanel } from '../core/views/popovers/sidePanel';
import renderGroupByPillsContainer from '../core/views/groupByPillsContainer';


const view = (state, { dispatch }) => {
	const { setRef, syncScroll } = getScrollSynchronizer();
	const {
		properties: { pagination, dir, tasks: propTasks },
		sidePanel,
		columns,
		groupBy,
		displaySubColumnRow,
		tree: { tasks, totalTaskNode },
		configSectionWidth,
		allowRowsReordering
	} = state;

	const {
		rowHeight,
		toolbar,
		rowGroup,
		filter,
		displayTotalRow
	} = state.config;
	const isRTL = isComponentRTL(dir);
	const bodyWrapperRef = createRef();

	const hasScroll = state.scrollHeight > state.visibleHeight;
	const scrollPaddingStyle = {
		[isRTL ? 'paddingLeft' : 'paddingRight']: `${hasScroll ? state.scrollbarWidth: 0}px`
	};
	const bodyHeight = `${
		!hasScroll
			?	state.visibleHeight
			:	state.scrollHeight }px`;
	const headerHeight = displaySubColumnRow ? rowHeight * 2 : rowHeight;

	const getSettingsPanelContents = () => {
		return (
			<sn-datagrid-config-panel className="side-panel" panelTitle={PERSONALIZE_LABEL}>
				<div slot="sn-datagrid-panel-content" className="side-panel-container">
					{ renderSidePanel(state, dispatch) }
				</div>
			</sn-datagrid-config-panel>
		);
	};

	const getHelpPanelContents = () => {
		return (
			<sn-datagrid-config-panel className="side-panel" panelTitle={HELP_LABEL}>
				<div slot="sn-datagrid-panel-content" className="side-panel-container">
					<sn-datagrid-help-panel
						readonly={state.config.readonly}
						rowCopyPaste={state.config.rowCopyPaste}
						reorderRows={state.config.reorderRows}
						previewRecordIcon={state.config.previewRecordIcon}
						setRowContextMenu={state.config.setRowContextMenu}
						reorderColumns={state.config.reorderColumns}
						sidePanel={state.sidePanel}
					></sn-datagrid-help-panel>
				</div>
			</sn-datagrid-config-panel>
		);
	};

	const getSidePanelContents = () => {
		if (!sidePanel.display) {
			return <Fragment />;
		}
		switch (sidePanel.selectedPanel) {
			case SIDE_PANELS.SETTINGS_PANEL_TOGGLE:
				return getSettingsPanelContents();
			case SIDE_PANELS.HELP_PANEL_TOGGLE:
				return getHelpPanelContents();
		}
	};

	return (
		<div className="sn-datagrid-container"
			style={{ visibility: `${state.visibleHeight === 0 ? 'hidden': 'visible'}` }}
			role="main"
		>
			<div className="scrollbar-measure" tabIndex="-1"></div>
			{
				toolbar.display
					? <sn-datagrid-header-toolbar {...{
						...toolbar,
						count: propTasks.length,
						rowGroup,
						filter
					}}>
						<slot slot='toolbar-icons' name="toolbar-icons"/>
					</sn-datagrid-header-toolbar>
					: <Fragment/>
			}
			{
				renderGroupByPillsContainer({
					state,
					groupBy,
					columns,
					dispatch,
					isRTL
				})
			}
			<div className="sn-datagrid"
				role="grid"
			>
				<div style={scrollPaddingStyle}>
					{ renderGridHeader({
						state,
						dispatch,
						setRef,
						syncScroll,
						headerHeight,
						configSectionWidth,
						allowRowsReordering
					}) }
				</div>
				<section
					className="sn-datagrid-body-wrapper"
					ref={bodyWrapperRef}
				>
					{ renderGridBody({
						visibleTasks: tasks,
						state,
						dispatch,
						bodyWrapperRef,
						setRef,
						syncScroll,
						height: bodyHeight,
						segment: PRIMARY,
						configSectionWidth,
						allowRowsReordering
					})}
				</section>
				{
					displayTotalRow
						? <section
							className="sn-datagrid-total-body-wrapper"
							style={scrollPaddingStyle}
						>
							{ renderGridBody({
								visibleTasks: [totalTaskNode],
								state,
								dispatch,
								setRef,
								syncScroll,
								height: `${rowHeight}px`,
								segment: AGGREGATION,
								configSectionWidth
							})
							}
						</section>
						: <Fragment/>
				}
				<div style={scrollPaddingStyle}>
					{ renderVirtualScroller({ state, setRef, syncScroll, configSectionWidth }) }</div>
				{
					getSidePanelContents()
				}
			</div>
			<div>
				<now-pagination {...pagination} />
			</div>
		</div>
	);
};

export default view;
