import {
	COLUMN_DRAG_STARTED,
	COLUMN_DRAG_ENDED,
	COLUMN_MOVE
} from '../../constants';
import { handleColumnReorder, handleColumnReorderEnd } from './helpers/columnReorder';

export const columnDragAndDropActionHandlers = {
	[COLUMN_DRAG_STARTED]: {
		stopPropagation: true,
		effect({
			action: { payload: { column, offsetY, parentColumn } },
			updateState
		}) {
			handleColumnReorder({
				updateState,
				column,
				offsetY,
				parentColumn
			});
		}
	},
	[COLUMN_DRAG_ENDED]: {
		stopPropagation: true,
		effect({
			state,
			updateState,
			dispatch,
			action: { payload: { targetIndex, section }, error }
		}) {
			const { columnReorder: { column, parentColumn } } = state;

			handleColumnReorderEnd({
				state,
				updateState,
				dispatch,
				targetIndex,
				parentColumn,
				column,
				section,
				error
			});
		}
	},
	[COLUMN_MOVE]: {
		stopPropagation: true,
		effect({
			state,
			updateState,
			dispatch,
			action: {
				payload: {
					targetIndex,
					parentColumn,
					column,
					section
				}
			}
		}) {
			handleColumnReorderEnd({
				state,
				updateState,
				dispatch,
				targetIndex,
				section,
				parentColumn,
				column
			});
		}
	}
};
