import Column from './Column';
import '@servicenow/now-avatar';

/**
 * This is the default avatar type column model
 * It is expected that the data will be an object containing `value` and `displaValue` fields
 * with an optional imageSrc attribute
 */
export default class Avatar extends Column {
	renderer({ displayValue, column, task }) {
		const columnName = column.columnDefinition.name;
		const taskData = task.taskData[columnName];
		const imageSrc = taskData && taskData.imageSrc;

		return (
			<div className="sn-datagrid-content">
				<span className="avatar-icon" style={{ display: displayValue ? '' : 'none' }}>
					<now-avatar
						size="sm"
						user-name={displayValue}
						hideTooltip={true}
						image-src={imageSrc}
					>
					</now-avatar>
				</span>
				<span className="avatar-content">
					{displayValue}
				</span>
			</div>
		);
	}
}

