
import {
	BUTTON_CLICKED,
	GRID_COLUMN_RESIZED,
	END_RESIZE_GRID_COLUMN,
	SUB_COLUMNS_EXPANDED,
	MOVE_COLUMN_TO_SECTION,
	SHOW_HEADER_CONTEXT_MENU,
	CONTAINER_SELECTED,
	COLUMN_SELECTION_UPDATED,
	COLUMN_FILTERED,
	GRID_COLUMN_FILTERED,
	ALL_TASKS_EXPANDED,
	GRID_ALL_TASKS_EXPANDED,
	TASK_EXPANDED,
	GRID_TASK_EXPANDED,
	GROUP_UNGROUP,
	GRID_GROUP_UNGROUP,
	COLUMN_DEFINITION_ATTRIBUTE_UPDATED,
	BUTTON_BARE_CLICKED,
	BUTTON_ICONIC_CLICKED,
	COLUMN_AUTOFITTED,
	ALL_COLUMN_AUTOFITTED,
	CONTEXT_MENU_COLUMN_HIDDEN,
	CELL_CLICKED,
	ALL_ROWS_SELECTION_TOGGLED,
	ROWS_SELECTION_TOGGLED,
	SECTION_RESIZED,
	END_RESIZE_SECTION,
	CELL_BLURRED,
	COLUMN_SETTINGS_DISPLAYED,
	COLUMNS,
	UNGROUP_ALL,
	ALTERNATE_HIGHLIGHT_TOGGLE,
	TOGGLE_CLICKED,
	NOW_TABS_ITEM_SELECTED,
	SIDE_PANEL_TAB,
	COLUMN_CONFIG_SUBCOL_TOGGLED,
	RESET_CONTEXT_MENU,
	SIDE_PANELS,
	NOW_PILL_DISMISSED,
	GRID_COLUMN_AUTOFITTED,
	CONTEXTMENU_OPTION_CLICKED,
	GRID_COPY_CELLS,
	GRID_PASTE_CELLS,
	GRID_EDIT_STARTED,
	COLUMNS_INIT_WIDTH_AUTO_ADJUSTED,
	GRID_COPY_ROWS,
	GRID_PASTE_ROWS,
	COLUMNS_RESET,
	GRID_INSERT_ROWS,
	AVATAR_GROUP_POPOVER_OPENED_SET,
	NOW_POPOVER_OPENED_TOGGLE,
	COPY_RESET_REQUESTED
} from '../../constants';
import { CELL_CONTEXT_MENU_SHOWN, ROW_CONTEXT_MENU_SHOWN } from '../../constants';
import {
	handleColumnMove,
	handleResizeColumn,
	handleResizeSection,
	handleShowHeaderContextMenu,
	handleShowCellContextMenu,
	handleShowRowContextMenu,
	handleAllRowsSelectionToggle,
	handleRowSelectionToggle,
	handleCellClicked,
	handleUpdateColumnsOnResizeEnd,
	handleUpdateColumnsDefinition,
	handleExpandSubColumn,
	handleColumnConfigSubcolToggled,
	handleUpdateColumnSelection,
	handleColumnAutoSize,
	handleAllColumnAutoSize,
	handleSidePanelTabChange,
	handleAlternateRowHighlightToggle,
	handleContainerSelected,
	handleCellBlurred,
	handleContextmenuOptionClick,
	handleColumnsWithoutWidthAutoSize,
	handleCopyCells,
	handlePasteCells,
	handleGridEditStarted,
	handleCopyRows,
	handlePasteRows,
	handleColumnReset,
	handleInsertRows,
	handleOverflowPopover,
	handlePopoverDisplayToggle,
	handleCopyResetRequested
} from './helpers/grid';

export const gridActionHandlers = {
	[MOVE_COLUMN_TO_SECTION]: {
		effect: handleColumnMove,
		stopPropagation: true
	},
	[GRID_COLUMN_RESIZED]: {
		effect: handleResizeColumn,
		stopPropagation: true
	},
	[END_RESIZE_GRID_COLUMN]: {
		effect: handleUpdateColumnsOnResizeEnd,
		stopPropagation: true
	},
	[END_RESIZE_SECTION]: {
		effect: handleUpdateColumnsOnResizeEnd,
		stopPropagation: true
	},
	[SECTION_RESIZED]: {
		effect: handleResizeSection,
		stopPropagation: true
	},
	[SHOW_HEADER_CONTEXT_MENU]: {
		effect: handleShowHeaderContextMenu,
		stopPropagation: true
	},
	[CELL_CONTEXT_MENU_SHOWN]: {
		effect: handleShowCellContextMenu,
		stopPropagation: true
	},
	[ROW_CONTEXT_MENU_SHOWN]: {
		effect: handleShowRowContextMenu,
		stopPropagation: true
	},
	[CONTEXTMENU_OPTION_CLICKED]: {
		stopPropagation: true,
		effect: handleContextmenuOptionClick
	},
	[CELL_CLICKED]: {
		effect: handleCellClicked,
		stopPropagation: true
	},
	[ALL_ROWS_SELECTION_TOGGLED]: {
		effect: handleAllRowsSelectionToggle,
		stopPropagation: true
	},
	[ROWS_SELECTION_TOGGLED]: {
		effect: handleRowSelectionToggle,
		stopPropagation: true
	},
	[COLUMN_DEFINITION_ATTRIBUTE_UPDATED]: {
		effect: handleUpdateColumnsDefinition,
		stopPropagation: true
	},
	[CONTAINER_SELECTED]: {
		effect: handleContainerSelected,
		stopPropagation: true
	},
	[CELL_BLURRED]: {
		effect: handleCellBlurred,
		stopPropagation: true
	},
	[GRID_COLUMN_AUTOFITTED]: {
		effect: handleColumnAutoSize,
		stopPropagation: true
	},
	[BUTTON_CLICKED]: ({
		state,
		action,
		dispatch,
		updateState
	}) => {
		const { payload } = action;
		switch (payload && payload.type) {
			case SUB_COLUMNS_EXPANDED: {
				action.stopPropagation();
				handleExpandSubColumn({ payload, state, updateState });
				break;
			}

			case COLUMN_FILTERED: {
				action.stopPropagation();
				dispatch(GRID_COLUMN_FILTERED, payload);
				break;
			}

			case TASK_EXPANDED: {
				action.stopPropagation();
				dispatch(GRID_TASK_EXPANDED, payload);
				break;
			}

			case ALL_TASKS_EXPANDED: {
				action.stopPropagation();
				dispatch(GRID_ALL_TASKS_EXPANDED, payload);
				break;
			}

			default:
				break;
		}
	},
	[BUTTON_BARE_CLICKED]: ({
		state,
		action,
		dispatch,
		updateState
	}) => {
		const { payload } = action;
		switch (payload && payload.type) {
			case GROUP_UNGROUP: {
				action.stopPropagation();
				dispatch(GRID_GROUP_UNGROUP, payload);
				break;
			}
			case COLUMN_AUTOFITTED: {
				action.stopPropagation();
				dispatch(GRID_COLUMN_AUTOFITTED, payload);
				dispatch(CONTAINER_SELECTED);
				break;
			}

			case ALL_COLUMN_AUTOFITTED: {
				action.stopPropagation();
				handleAllColumnAutoSize({ state, updateState, dispatch });
				dispatch(CONTAINER_SELECTED);
				break;
			}

			case CONTEXT_MENU_COLUMN_HIDDEN: {
				handleUpdateColumnSelection({ state, payload, updateState, dispatch });
				dispatch(CONTAINER_SELECTED);
				break;
			}

			case COLUMN_SELECTION_UPDATED: {
				action.stopPropagation();
				handleUpdateColumnSelection({ state, payload, updateState, dispatch });
				break;
			}

			case COLUMN_SETTINGS_DISPLAYED: {
				action.stopPropagation();
				updateState({
					...RESET_CONTEXT_MENU,
					sidePanel: {
						display: true,
						selectedPanel: SIDE_PANELS.SETTINGS_PANEL_TOGGLE,
						mode: COLUMNS
					}
				});
				break;
			}

			case UNGROUP_ALL: {
				action.stopPropagation();
				dispatch(GRID_GROUP_UNGROUP, { groupByValue: [] });
				break;
			}

			default:
				break;
		}
	},
	[BUTTON_ICONIC_CLICKED]: ({
		state,
		action,
		updateState
	}) => {
		const { payload } = action;
		switch (payload && payload.type) {
			case SUB_COLUMNS_EXPANDED: {
				action.stopPropagation();
				handleExpandSubColumn({ payload, state, updateState });
				break;
			}
			case COLUMN_CONFIG_SUBCOL_TOGGLED: {
				action.stopPropagation();
				handleColumnConfigSubcolToggled({ payload, state, updateState });
				break;
			}
		}
	},
	[NOW_PILL_DISMISSED]: ({
		action,
		dispatch
	}) => {
		const { payload = {} } = action;

		action.stopPropagation();
		dispatch(GRID_GROUP_UNGROUP, payload);
	},
	[TOGGLE_CLICKED]: params => {
		const { action } = params;
		const { payload } = action;
		switch (payload && payload.type) {
			case ALTERNATE_HIGHLIGHT_TOGGLE:
				action.stopPropagation();
				handleAlternateRowHighlightToggle(params);
				break;
		}
	},
	[NOW_TABS_ITEM_SELECTED]: params => {
		const { action } = params;
		const { payload } = action;
		switch (payload && payload.type) {
			case SIDE_PANEL_TAB:
				action.stopPropagation();
				handleSidePanelTabChange(params);
				break;
		}
	},
	[COLUMN_AUTOFITTED]: {
		effect: handleColumnAutoSize,
		stopPropagation: true
	},
	// it adjusts the columns widths on column initialization
	[COLUMNS_INIT_WIDTH_AUTO_ADJUSTED]: {
		effect: handleColumnsWithoutWidthAutoSize,
		stopPropagation: true
	},
	[GRID_COPY_CELLS]: {
		effect: handleCopyCells,
		stopPropagation: true
	},
	[GRID_PASTE_CELLS]: {
		effect: handlePasteCells,
		stopPropagation: true
	},
	[GRID_EDIT_STARTED]: {
		effect: handleGridEditStarted,
		stopPropagation: true
	},
	[GRID_COPY_ROWS]: {
		effect: handleCopyRows,
		stopPropagation: true
	},
	[GRID_PASTE_ROWS]: {
		effect: handlePasteRows,
		stopPropagation: true
	},
	[GRID_INSERT_ROWS]: {
		effect: handleInsertRows,
		stopPropagation: true
	},
	[COLUMNS_RESET]: {
		effect: handleColumnReset,
		stopPropagation: true
	},
	[AVATAR_GROUP_POPOVER_OPENED_SET]: {
		effect: handleOverflowPopover,
		stopPropagation: true
	},
	[NOW_POPOVER_OPENED_TOGGLE]: {
		stopPropagation: true,
		effect: handlePopoverDisplayToggle
	},
	[COPY_RESET_REQUESTED]: {
		stopPropagation: true,
		effect: handleCopyResetRequested
	}
};
