import debounce from 'lodash/debounce';

export const getScrollSynchronizer = () => {
	const refs = {};
	const getRef = (key, path) => path ? refs[path][key] : refs[key];
	const setRef = (key, value, path) => {
		if (path) {
			refs[path] = Object.assign({}, refs[path], { [key]: value });
		} else {
			refs[key] = value;
		}
	};
	let scrollOrigin;
	const resetScrollOrigin = debounce(() => {
		scrollOrigin = undefined;
	}, 50);

	return {
		getRef,
		setRef,
		syncScroll: (e, section, origin) => {
			if (!scrollOrigin) {
				scrollOrigin = origin;
			} else if (scrollOrigin !== origin) {
				return;
			}
			Object.values(getRef(section)).forEach(sectionRef => {
				sectionRef.current.scrollTo({
					left: e.target.scrollLeft
				});
			});
			resetScrollOrigin();
		}
	};
};


