import { CELL_BLURRED } from '../constants';
import { containsClassInPath } from './helpers/elements';

export default [{
	events: ['click'],
	effect({
		action: {
			payload: {
				event
			}
		},
		dispatch
	}) {
		if (!containsClassInPath(event, 'sn-datagrid-cell')) {
			dispatch(CELL_BLURRED);
		}
	},
	target: document
}];
