import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './styles.scss';
import rtlBehavior from '@servicenow/behavior-rtl';
import '@servicenow/now-tabs';
import '../sn-datagrid-filter';
import { actionHandlers } from './actions';
import view from './view';
import { TAB_ITEMS } from './constants';

createCustomElement('sn-datagrid-header-context-menu', {
	renderer: {
		type: snabbdom,
		view
	},
	behaviors: [
		{ behavior: rtlBehavior }
	],
	initialState: {
		tab: TAB_ITEMS[0].id
	},
	properties: {
		filter: { default: false },
		rowGroup: { default: false },
		freezeColumns: { default: false },
		hasHierarchy: { default: false },
		column: { default: {} },
		groupBy: { default: [] },
		columns: { default: {} },
		parentColumn: { default: {} },
		section: { default: null },
		resetColumns: { default: false }
	},
	styles: styles,
	actionHandlers: {
		...actionHandlers
	}
});
