export const IN = 'IN';
/**
 * Order is important
 * During conversion from filter expression (Encoded query) to list of filter
 * operation (Object of column, filter_type, value)
 * we are finding filter_operation substring and then splitting across that,
 * so to work correctly this behaviour NOT EQUAL should be checked before EQUAL.
 */
export const OPERATORS = Object.freeze([LIKE, CONTAINS, EQUAL_TO, NOT_EQUAL_TO, EQUALS, NOTEQUALS, IN]);

