import groupBy from 'lodash/groupBy';
import isEmpty from 'lodash/isEmpty';
import noop from 'lodash/noop';
import uniq from 'lodash/uniq';
import { buildTree } from '../store/tree';
import { getTreeUtils } from '../store/tree/IndexTree';
import { createColumnModel } from '../columnModel';
import {
	SECTION,
	RTL,
	NO_SORT,
	GRID_INITIAL_STATE,
	GROUPBY_COUNT_LIMIT,
	GRID_GROUP_UNGROUP
} from '../../constants';
import { createLog } from '../../util/logger';
import { columnsPropHasSubColumns, getConfigSectionWidth, canReorderRows } from './sections';
import { getVisibleTasks } from './smartRendering';

export const getGridInitialState = () => {
	return GRID_INITIAL_STATE;
};

export const validateProperties = (properties) => {
	const { columns, tasks, config } = properties;
	let { sortBy, groupBy } = properties;
	const propCheckLog = createLog('Invalid Property');
	/**
	 * validating columns:
	 * 1. columns[] should not be empty
	 * 2. center section should have atleast one column
	 */
	if (columns.length === 0) {
		propCheckLog.error('columns: columns array should contain atleast on column');
	}

	const centerSectionColumnCount = columns.reduce(
		(centerSectionColumnCount, columns) => !columns.section || columns.section === SECTION.CENTER
			? ++centerSectionColumnCount
			: centerSectionColumnCount,
		0
	);

	if (centerSectionColumnCount === 0) {
		propCheckLog.error('columns: center section should contain atleast on column');
	}

	/**
	 * validating sortBy
	 * checking if the column exists
	 */
	if (!isEmpty(sortBy)) {
		const doesSortColumnExists = columns.find(column => column.name === sortBy.name);
		if (!doesSortColumnExists) {
			propCheckLog.error(`sortBy: column name ${sortBy.name} does not exist in column array`);
			sortBy = {};
		}
	}

	/**
	 * validating groupBy
	 * checking group count is within limits
	 * group columns exist
	 * groupBy values are unique
	 */
	const isGroupWithinLimit = groupBy.length <= GROUPBY_COUNT_LIMIT;
	const doesGroupColumnExists = groupBy.reduce((doesGroupColumnExists, groupColumnName) => {
		return doesGroupColumnExists &&
			columns.find(column => {
				return column.name === groupColumnName ||
				column.subColumns && column.subColumns.find(column => column.name === groupColumnName);
			});
	}, true);
	const isGroupByUnique = uniq(groupBy).length === groupBy.length;

	const shouldAllowGrouping = isGroupWithinLimit && doesGroupColumnExists && isGroupByUnique;
	if (!shouldAllowGrouping) {
		groupBy = [];
		propCheckLog.error('GroupBy: can not group on invalid groupBy property');
	}

	return {
		columns, tasks, config, sortBy, groupBy
	};
};

export const getTasksAndScrollheight = ({
	tasks,
	config,
	sortBy,
	taskExpandMap = {},
	columns = [],
	groupBy = [],
	dispatch = noop
}) => {
	const {
		rowHeight,
		defaultSortBy = {},
		displayTotalRow
	} = config;
	// TODO: derive heirarchyField from config, instead of hardcoding to 'parent'
	const tree = buildTree(tasks, 'parent', defaultSortBy, taskExpandMap, columns, displayTotalRow);
	const treeUtils = getTreeUtils(tree);

	if (!isEmpty(sortBy)) {
		const {
			name,
			order
		} = sortBy;

		treeUtils.sort({
			sortByColumnId: name,
			sortOrder: order,
			columns
		});
	}
	if (!isEmpty(groupBy)) {
		dispatch(GRID_GROUP_UNGROUP, {
			groupByValue: groupBy,
			notifyConsumer: false
		});
	}
	const scrollHeight = tree.tasks.length * rowHeight;

	return {
		scrollHeight,
		tree: {
			...tree
		},
		sortBy: {
			name: null,
			order: NO_SORT,
			...defaultSortBy,
			...sortBy
		}
	};
};

export const getColumnModels = ({
	columns,
	freezeColumns = true,
	config
}) => {
	const {
		CENTER
	} = SECTION;
	const defaultSectionColumns = columns.map(column => {
		const updatedColumn = {
			section: CENTER,
			...column
		};

		if (!freezeColumns) {
			updatedColumn.section = CENTER;
		}

		return updatedColumn;
	});
	const columnModel = createColumnModel(defaultSectionColumns, config);
	const groupedCols = Object.assign({
		left: [],
		center: [],
		right: []
	}, groupBy(columnModel, 'section'));

	return {
		columns: groupedCols,
		displaySubColumnRow: columnsPropHasSubColumns(columns)
	};
};

const getUpdatedColumnDefinition = column => {
	const {
		columnDefinition,
		width,
		hidden,
		section,
		subColumns
	} = column;

	const updatedColumnDefinition = {
		...columnDefinition,
		width,
		hidden,
		section
	};

	if (!isEmpty(subColumns)) {
		updatedColumnDefinition.subColumns = subColumns.map((col) => getUpdatedColumnDefinition(col));
	}

	return updatedColumnDefinition;
};

export const getUpdatedColumnDefinitions = (columns) => {
	const updatedColumns = [];

	Object.values(columns).forEach(section => {
		section.forEach(col => {
			updatedColumns.push(getUpdatedColumnDefinition(col));
		});
	});

	return updatedColumns;
};

export const isComponentRTL = (dir) => {
	return dir === RTL;
};

export const allowGrouping = ({ groupBy = [], groupColumnName = '' }) => {
	const log = createLog('GroupBy');
	if (groupBy.length >= GROUPBY_COUNT_LIMIT) {
		log.error(`Can not group more than ${GROUPBY_COUNT_LIMIT} columns.`);

		return false;
	}
	if (groupBy.includes(groupColumnName)) {
		log.error(`Can not group on already grouped column: ${groupColumnName}.`);

		return false;
	}

	return true;
};

export function transformState(state) {
	return {
		...state,
		visibleTasks: getVisibleTasks(state),
		allowRowsReordering: canReorderRows(state),
		configSectionWidth: getConfigSectionWidth(state)
	};
}

