import '@servicenow/now-icon';
import '@servicenow/now-button';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom';
import {
	MORE_ACTION_TITLE,
	CELL_TITLE,
	CELL_CONTEXT_MENU_SHOWN,
	CELL_CLICKED,
	KEYBOARD_NAVIGATED,
	SELECTED_CELL_CLASS,
	ROWS_SELECTION_TOGGLED,
	ENTER_KEY,
	ARROW_UP_KEY,
	ARROW_DOWN_KEY,
	ARROW_RIGHT_KEY,
	ARROW_LEFT_KEY,
	SELECTED_COL_CELL_CLASS,
	AGGREGATION,
	ALL_ROWS_SELECTION_TOGGLED,
	GRID_EDIT_STARTED
} from '../../../constants';

import { getSlotName, handleCopyPasteKeyboardShortcuts } from '../../helpers/editingHelper';
import { isComponentRTL } from '../../helpers/gridStateParams';
import { getColumnWidth } from '../../helpers/sections';
import isEmpty from 'lodash/isEmpty';

const getElement = (columnDefinitionRenderer, configObject) => {
	return snabbdom.createElement(columnDefinitionRenderer, configObject);
};

const isCellSelected = (selectedCell, rowIndex, columnId, segment) => {
	return selectedCell && selectedCell.rowIndex === rowIndex + 1 &&
		selectedCell.columnId === columnId && selectedCell.segment === segment;
};

const handleCellContextMenuClick = ({
	event,
	task,
	column,
	section,
	segment,
	contextMenuId,
	dispatch
}) => {
	event.stopImmediatePropagation();
	event.preventDefault();

	dispatch(CELL_CONTEXT_MENU_SHOWN, {
		column,
		task,
		section,
		segment,
		contextMenuId,
		currentTarget: event.currentTarget
	});
};

const handleCellClicked = ({
	e, dispatch, segment, temporary, task, rowSelectionCheckbox, columnId, rowIndex, selectedCell
}) => {
	// Cmd + shift + click is for row selection
	if (segment !== AGGREGATION && !temporary) {
		// TODO: isSummary hardcoded temporarily for sn-task-planner usecase
		if ((e.ctrlKey || e.metaKey) && e.shiftKey && !task.taskData.isSummary) { // Add to existing row selection
			dispatch(ROWS_SELECTION_TOGGLED, { task });
		} else if ((e.ctrlKey || e.metaKey) && !e.shiftKey) { // Reset row selection and cells are being multi selected
			dispatch(ALL_ROWS_SELECTION_TOGGLED, { allTasksSelected: true });
		} else if (!rowSelectionCheckbox) { // When row selection checkbox ois missing, clicking selects that row
			dispatch(ALL_ROWS_SELECTION_TOGGLED, { allTasksSelected: true });
			dispatch(ROWS_SELECTION_TOGGLED, { task });
		}
	}
	dispatch(CELL_CLICKED, {
		columnId,
		rowIndex: rowIndex + 1,
		taskId: task.id,
		segment,
		includeCurrent: segment !== AGGREGATION && !temporary,
		appendSelection: (e.ctrlKey || e.metaKey) && !e.shiftKey && selectedCell.columnId === columnId
	});
};

const generateCellDOMElem = ({
	config,
	column,
	task,
	dispatch,
	groupBy,
	displayValue,
	filterExpression,
	renderer,
	columnDefinition,
	section,
	isRTL,
	segment,
	cellSelected,
	overflowPopover
}) => {
	const { setCellContextMenu } = config;
	const { columnDefinition: { cellContextMenu }, columnId } = column;
	const cellTabIndex = cellSelected ? '0' : '-1';
	const contextMenuId = `${columnId}_${task.index + 1}`;
	const contextMenuButtonTitle = `${MORE_ACTION_TITLE} ${columnDefinition.label} ${CELL_TITLE}`;

	return <Fragment>
		{ getElement(renderer, {
			config,
			column,
			task,
			dispatch,
			groupBy,
			displayValue,
			filterExpression,
			isRTL,
			tabIndex: cellTabIndex,
			overflowPopover
		})}
		{
			// TODO: isSummary hardcoded temporarily for sn-task-planner usecase
			setCellContextMenu && cellContextMenu && !task.temporary && !task.taskData.isSummary
				? <div
					className='sn-datagrid-icons'
					data-contextMenuId={contextMenuId}
					tabIndex={cellTabIndex}
					role="button"
					title={contextMenuButtonTitle}
					ariaLabel={contextMenuButtonTitle}
					on-click={event => {
						handleCellContextMenuClick({
							event,
							task,
							column,
							section,
							segment,
							contextMenuId,
							dispatch
						});
					}}
					on-keydown={(event) => {
						if (event.keyCode === ENTER_KEY) {
							handleCellContextMenuClick({
								event,
								task,
								column,
								section,
								segment,
								contextMenuId,
								dispatch
							});
						}
					}}
				>
					<now-icon
						icon="ellipsis-v-fill"
						size="sm"
						variant="tertiary"
						bare={true}
					></now-icon>
				</div>
				: <Fragment/>
		}
	</Fragment>;
};

const renderCell = ({ config, column, task, colIndex, dispatch, state, section, gridBodyRef,
	segment, bodyWrapperRef }) => {
	const rowIndex = task.index;
	const { columnDefinition, renderer, getDisplayValue, editable, columnId } = column;
	const {
		groupBy,
		filterExpression,
		properties: { dir },
		selectedCell,
		config: { readonly, rowSelectionCheckbox, rowCopyPaste },
		tree,
		copiedRowIds,
		clipBoard,
		overflowPopover
	} = state;
	const cellWidth = `${getColumnWidth(column)}px`;
	const style = {
		width: cellWidth,
		minWidth: cellWidth
	};
	const { id, temporary } = task;
	const displayValue = getDisplayValue(column, task);
	const cellSelected = isCellSelected(selectedCell, rowIndex, columnId, segment);
	const selectedColClassName = selectedCell.columnId === columnId &&
		selectedCell.selectedRowIds &&
		selectedCell.selectedRowIds.has(id)
		? ` ${SELECTED_COL_CELL_CLASS}`
		: '';
	const selectedCellClassName = cellSelected ? ` ${SELECTED_CELL_CLASS}`: '';
	const slotName = getSlotName(columnDefinition.name, id);
	// TODO: isSummary hardcoded temporarily for sn-task-planner usecase
	const enableEditing = !readonly && editable && !temporary && !task.taskData.isSummary;
	const isRTL = isComponentRTL(dir);

	return (
		<div
			className={`sn-datagrid-cell${selectedColClassName}${selectedCellClassName}`}
			aria-label={`${columnDefinition.label} - ${displayValue}`}
			title={displayValue}
			aria-colindex={colIndex}
			tabindex={cellSelected ? '0': '-1'}
			style={style}
			role="gridcell"
			on-click={(e) => handleCellClicked({
				e, dispatch, segment, temporary, task, rowSelectionCheckbox, columnId, rowIndex, selectedCell
			})}
			on-keydown={e => {
				switch (e.keyCode) {
					case ENTER_KEY:
						if (enableEditing) {
							dispatch(GRID_EDIT_STARTED, { task, column });
						}
						break;
					case ARROW_UP_KEY:
					case ARROW_DOWN_KEY:
						if (e.shiftKey) { // If multi cell select, then reset row selection
							dispatch(ALL_ROWS_SELECTION_TOGGLED, { allTasksSelected: true });
						}
					// eslint-disable-next-line no-fallthrough
					case ARROW_RIGHT_KEY:
					case ARROW_LEFT_KEY:
						e.preventDefault();
						dispatch(KEYBOARD_NAVIGATED, {
							keyCode: e.keyCode,
							shiftKey: e.shiftKey, // TODO: seperate cell selection logic from keyboard nav
							isRTL,
							segment,
							bodyWrapperRef
						});
				}
				if (!readonly && !temporary) {
					handleCopyPasteKeyboardShortcuts(
						e,
						dispatch,
						selectedCell,
						tree,
						task.taskData,
						columnDefinition,
						isEmpty(copiedRowIds),
						!enableEditing || isEmpty(clipBoard),
						rowCopyPaste
					);
				}
			}}
			on-dblclick={() => {
				dispatch(ALL_ROWS_SELECTION_TOGGLED, { allTasksSelected: true });
				if (enableEditing) {
					dispatch(GRID_EDIT_STARTED, { task, column });
				}
			}}
		>
			{ enableEditing ? <slot name={slotName}>
				{ generateCellDOMElem({
					config,
					column,
					task,
					dispatch,
					groupBy,
					displayValue,
					filterExpression,
					renderer,
					columnDefinition,
					section,
					isRTL,
					gridBodyRef,
					segment,
					cellSelected,
					bodyWrapperRef,
					overflowPopover
				})}
			</slot>
				:	generateCellDOMElem({
					config,
					column,
					task,
					dispatch,
					groupBy,
					displayValue,
					filterExpression,
					renderer,
					columnDefinition,
					section,
					isRTL,
					gridBodyRef,
					segment,
					cellSelected,
					bodyWrapperRef,
					overflowPopover
				})
			}
		</div>
	);
};

export default renderCell;
