import isEmpty from 'lodash/isEmpty';
import flatten from 'lodash/flatten';
import String from './String';
import Text from './Text';
import Date from './Date';
import Number from './Number';
import Reference from './Reference';
import Model from './Model';
import DateTime from './DateTime';
import Avatar from './Avatar';
import AvatarGroup from './AvatarGroup';
import { DATA_TYPES, CELL_EDIT_MODE } from '../../constants';

const {
	STRING_TYPE,
	REFERENCE_TYPE,
	DATE_TIME_TYPE,
	NUMBER_TYPE,
	TEXT_TYPE,
	DATE_TYPE,
	AVATAR_TYPE,
	AVATAR_GROUP_TYPE
} = DATA_TYPES;

const getColumnModel = (col, config, parentId) => {
	const column = Object.assign({}, col);

	if (column.groupColumn) {
		column.cellContextMenu = false;
	}
	if (column.cellContextMenu === undefined) {
		const isGridEditable = config.readonly === false;
		const isCellEdit = config.editMode === CELL_EDIT_MODE;
		const isCopyPasteEnabled = column.copyPaste !== false;

		column.cellContextMenu = isGridEditable && (isCopyPasteEnabled || isCellEdit);
	}

	return new Model(column, parentId);
};

export const createColumnModel = (columns, config = {}) => {
	return columns.map(column => {
		const model = getColumnModel(column, config);
		const { subColumns } = column;
		const { columnId } = model;

		if (!isEmpty(subColumns)) {
			model.subColumns = subColumns.map(column => {
				return getColumnModel(column, config, columnId);
			});
		}

		return model;
	});
};

export const updateColumns = (columns = {}, columnToUpdate = {}, updates = {}) => {
	if (isEmpty(columnToUpdate)) {
		return columns;
	}

	let colPosition = null;
	let parentCol = {};
	let parentIndex = null;
	let colIndex = null;

	Object.entries(columns).forEach(([position, value]) => {
		value.forEach((parentColumn, parentIdx) => {
			if (columnToUpdate.columnId === parentColumn.columnId) {
				colPosition = position;
				colIndex = parentIdx;
			} else if (!isEmpty(parentColumn.subColumns)) {
				parentColumn.subColumns.forEach((column, childIdx)=> {
					if (columnToUpdate.columnId === column.columnId) {
						parentCol = parentColumn;
						colPosition = position;
						colIndex = childIdx;
						parentIndex = parentIdx;
					}
				});
			}
		});
	});

	if (!colPosition) { // columnToUpdate doesn't exist in columns. This can happen for groupColumn
		return columns;
	}

	const positionedColumns = columns[colPosition] || [];
	let newColModel = Object.assign(new Model(columnToUpdate.columnDefinition, parentCol.columnId),
		columnToUpdate, updates);

	if (!isEmpty(parentCol)) {
		const { subColumns } = parentCol;

		newColModel = Object.assign(new Model(parentCol.columnDefinition),
			parentCol, {
				subColumns: [...subColumns.slice(0, colIndex), newColModel, ...subColumns.slice(colIndex+1)]
			});
		colIndex = parentIndex;
	}
	const newPositionedColumns = [...positionedColumns.slice(0, colIndex),
		newColModel, ...positionedColumns.slice(colIndex+1)];

	return {
		...columns,
		[colPosition]: newPositionedColumns
	};
};

export const buildColumnList = columnModels => {
	const allPositionedColumns = flatten(Object.values(columnModels));

	return flatten(allPositionedColumns.map(column => {
		if (!isEmpty(column.subColumns)) {
			return [column, ...column.subColumns];
		}

		return column;
	}));
};

export const findColumn = (columns, colId) => {
	for (const column of columns) {
		if (column.columnId === colId) {
			return {
				column,
				parent: null
			};
		}
		if (!isEmpty(column.subColumns)) {
			const hasColumn = findColumn(column.subColumns, colId);
			if (hasColumn) {
				return {
					column: hasColumn.column,
					parent: column
				};
			}
		}
	}
};

export const getColumnModelFromId = (columns, colId) => {
	let columnData = null;
	const allCols = Object.values(columns);

	for (const column of allCols) {
		columnData = findColumn(column, colId);
		if (columnData) {
			break;
		}
	}

	return columnData? columnData.column: null;
};

export const getAggregationConfigs = (columns) => {
	const allColumnModels = buildColumnList(columns);

	return allColumnModels
		.filter(column => column.subColumns.length === 0 && column.columnDefinition.aggregation)
		.map(column => {
			const getter = column.getValue;
			const setter = column.setValue;

			return {
				aggregation: column.columnDefinition.aggregation,
				columnModel: column,
				getter,
				setter
			};
		});
};

export default {
	[STRING_TYPE]: new String(),
	[TEXT_TYPE]: new Text(),
	[DATE_TYPE]: new Date(),
	[DATE_TIME_TYPE]: new DateTime(),
	[NUMBER_TYPE]: new Number(),
	[REFERENCE_TYPE]: new Reference(),
	[AVATAR_TYPE]: new Avatar(),
	[AVATAR_GROUP_TYPE]: new AvatarGroup()
};
