import '@servicenow/now-avatar';
import '@servicenow/now-icon';
import './components/sn-datagrid-avatar-group-overflow-popover-container';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { OPENED_SET, MAX_OVERFLOW_COUNT } from './constant';

const renderAvatar = (displayValue='', imageSrc='', renderEmpty = true) => {
	return <span className="icon" tabindex="0" style={{ display: renderEmpty ? 'none' : '' }}>
		<now-avatar
			size="sm"
			user-name={displayValue}
			hideTooltip={false}
			image-src={imageSrc}>
		</now-avatar>
	</span>;
};

const renderAssignmentAvatars = (assignmentGroup, maxAssignmentCount, dispatch) => {
	const items = [];

	// Due to Stack VX, Need to use flex-direction: row-reverse, For correct order, reversing the array.
	const itemArr = Array.isArray(assignmentGroup) && assignmentGroup.length > 0
		? assignmentGroup.slice().reverse() : [];

	// Avoiding DOM changes.
	let noOfEmptyAvatar = 0;

	for (let i = 0; i < maxAssignmentCount; i++) {
		if (i > itemArr.length - 1) {
			noOfEmptyAvatar += 1;
		} else {
			const { displayValue, imageSrc } = itemArr[i];

			items.push(renderAvatar(displayValue, imageSrc, false, dispatch));
		}
	}

	const emptyAvatars = [];
	for (let i = 0; i < noOfEmptyAvatar; i++) {
		emptyAvatars.push(renderAvatar());
	}

	return [...emptyAvatars, ...items];
};

const eventHandler = (dispatch, taskId) => {
	return (event) => {
		event.stopImmediatePropagation();
		event.preventDefault();

		dispatch(OPENED_SET, { taskId });
	};
};


const renderOverflowMarkUp = (overflowCount, targetRef, setTargetRef) => {
	return <div className="overflow-icon">
		<span role="img">
			<span className="label"
				ref={targetRef}
				hook-insert={({	elm	}) => setTargetRef(elm)}
				hook-destroy={() => setTargetRef(null)} >
				{ overflowCount < MAX_OVERFLOW_COUNT ? `+${overflowCount}` : <now-icon
					className="now-avatar-icon"
					icon="ellipsis-h-fill"
					size='md'
					role="img" />
				}
			</span>
		</span>
	</div>;
};

const renderOverflowAvatar = ({
	dispatch,
	opened,
	targetRef,
	taskId,
	overflowAssignmentGroup,
	setTargetRef
}) => {
	const overflowCount = overflowAssignmentGroup.length;

	return <div className="overflow-container"
		on-click={eventHandler(dispatch, taskId)}
		style={{
			display: overflowCount > 0 ? '': 'none'
		}}>
		{ renderOverflowMarkUp(overflowCount, targetRef, setTargetRef) }
		{
			opened
				? <now-popover
					manage-opened
					opened={opened}
					hook-insert={({ elm }) => {
						elm.positionTarget = targetRef.current;
					}}>
					<sn-datagrid-avatar-group-overflow-popover-container
						slot="content"
						assignments={overflowAssignmentGroup}
					/>
				</now-popover>
				: <Fragment/>
		}

	</div>;
};

const view = (state, { dispatch, updateState }) => {
	const setTargetRef = (ref) => {
		setTimeout(() => updateState({ targetRef: ref }));
	};
	const { targetRef } = state;
	const {
		assignmentGroup,
		maxAssignmentCount,
		taskId,
		opened
	} = state.properties;

	const avatars = renderAssignmentAvatars(assignmentGroup, maxAssignmentCount, dispatch);

	const overflowAvatar = renderOverflowAvatar({
		dispatch,
		opened,
		targetRef,
		taskId,
		overflowAssignmentGroup: assignmentGroup.slice(maxAssignmentCount),
		setTargetRef
	});

	return <div className="sn-datagrid-avatar-group">
		<div className="group">
			{ [overflowAvatar, ...avatars] }
		</div>
	</div>;
};

export default view;
