export const handleExpandSubColumn = ({ payload, state, updateState }) => {
	const { column } = payload;
	const { columns, sectionsWidth } = state;
	const { section } = column;
	const sectionColumns = columns[section];
	const index = sectionColumns.findIndex(col => col.columnId === column.columnId);
	const col = Object.assign({}, sectionColumns[index], { expandSubColumns: !column.expandSubColumns });
	let updatedColumns = {
		...columns,
		[section]: [
			...sectionColumns.slice(0, index),
			col,
			...sectionColumns.slice(index + 1)
		]
	};

	if (!col.expandSubColumns) {
		updatedColumns = getWhiteSpaceAdjustedColumns({
			columns: updatedColumns,
			sectionsWidth,
			gridWidth: getCurrentGridWidth(state)
		});
	}

	updateState({
		columns: updatedColumns,
		...RESET_CONTEXT_MENU
	});
};

export const handleColumnConfigSubcolToggled = ({ payload, state, updateState }) => {
	const { column } = payload;
	const { section } = column;
	const { columns } = state;
	const sectionColumns = columns[section];
	const index = sectionColumns.findIndex(col => col.columnId === column.columnId);
	const updatedCol = Object.assign({}, get(columns, `${section}[${index}]`, {}));

	updatedCol.configExpanded = !column.configExpanded;

	updateState({
		path: `columns.${section}[${index}]`,
		value: updatedCol,
		operation: 'set'
	});
};

export const handleColumnMove = ({ state, action, updateState, dispatch }) => {
	const { column, moveTo } = action.payload;
	const { columns, sectionsWidth, bodySectionKeySuffix } = state;
	const section = column.section;
	const sectionColumns = columns[section];
	const index = sectionColumns.findIndex(col => col.columnId === column.columnId);
	const targetSectionColumns = columns[moveTo] || [];
	const columnToMove = {
		...column,
		section: moveTo
	};
	let updatedColumns = {
		...columns,
		[section]: [
			...sectionColumns.slice(0, index),
			...sectionColumns.slice(index + 1)
		],
		[moveTo]: [
			...targetSectionColumns,
			columnToMove
		]
	};

	updatedColumns = getWhiteSpaceAdjustedColumns({
		columns: updatedColumns,
		sectionsWidth,
		gridWidth: getCurrentGridWidth(state)
	});

	updateState({
		columns: updatedColumns,
		bodySectionKeySuffix: !bodySectionKeySuffix,
		...RESET_CONTEXT_MENU,
		// we will focus on the config section header after moving a column as the grid will be rebuilt
		selectedCell: {
			rowIndex: 0,
			columnId: CONFIG_SECTION_COLUMN_NAME,
			segment: PRIMARY
		}
	});

	dispatch(COLUMN_DEFINITION_ATTRIBUTE_UPDATED, { columns: updatedColumns });
};

export const handleUpdateColumnSelection = ({ state, payload, updateState, dispatch }) => {
	const { column, parentColumn } = payload;
	const { columns } = state;

	let index;
	let col;
	let sectionColumns;
	let section;
	const hidden = !column.hidden;
	let parentHidden;
	let updatedColumns;
	if (column.timeVariantGroup) {
		updatedColumns = Object.assign({}, columns);

		Object.keys(updatedColumns).forEach(function(section) {
			updatedColumns[section].forEach(col => {
				if (!isEmpty(col.subColumns)) {
					col.subColumns.map(subCol => {
						subCol.hidden = subCol.timeVariantGroup === column.timeVariantGroup ? hidden : subCol.hidden;

						return subCol;
					});
				}
			});
		});
	} else {
		if (parentColumn) {
			const { subColumns } = parentColumn;
			const subColIndex = subColumns.findIndex(col => col.columnId === column.columnId);
			const subCol = Object.assign({}, subColumns[subColIndex], { hidden });

			section = parentColumn.section;
			sectionColumns = columns[section];
			index = sectionColumns.findIndex(col => col.columnId === parentColumn.columnId);

			if (parentColumn.hidden !== hidden) {
				if (!hidden) {
					parentHidden = !parentColumn.hidden;
				} else {
					parentHidden = subColumns.filter((subCol) => {
						return subCol.columnId !== column.columnId && !subCol.hidden;
					}).length === 0;
				}
			} else {
				parentHidden = parentColumn.hidden;
			}

			col = Object.assign({}, sectionColumns[index], {
				subColumns: [
					...subColumns.slice(0, subColIndex),
					subCol,
					...subColumns.slice(subColIndex + 1)
				],
				hidden: parentHidden
			});
		} else {
			section = column.section;
			sectionColumns = columns[section];
			index = sectionColumns.findIndex(col => col.columnId === column.columnId);
			col = Object.assign({}, sectionColumns[index], { hidden });

			if (!isEmpty(col.subColumns)) {
				col.subColumns = col.subColumns.map(subCol => {
					if (!subCol.timeVariantGroup) {
						subCol.hidden = hidden;
					}

					return subCol;
				});
			}
		}

		updatedColumns = {
			...columns,
			[section]: [
				...sectionColumns.slice(0, index),
				col,
				...sectionColumns.slice(index + 1)
			]
		};
	}

	updateState({
		columns: updatedColumns
	});

	// Autofitting columns whose width has not been set manually
	dispatch(COLUMNS_INIT_WIDTH_AUTO_ADJUSTED);
	dispatch(COLUMN_DEFINITION_ATTRIBUTE_UPDATED, { columns: updatedColumns });
};

export const handleResizeColumn = ({ state, action, updateState }) => {
	const { column, width } = action.payload;
	const { columns, sectionsWidth } = state;
	let updatedColumns = updateColumns(columns, column, { width,
		autoWidth: 0 });

	updatedColumns = getWhiteSpaceAdjustedColumns({
		columns: updatedColumns,
		sectionsWidth,
		gridWidth: getCurrentGridWidth(state)
	});

	updateState({
		columns: updatedColumns
	});
};

export const handleResizeSection = ({ action, state, updateState }) => {
	const { section, newSectionWidth } = action.payload;
	const { columns, sectionsWidth } = state;

	updateState({
		columns: getWhiteSpaceAdjustedColumns({
			columns,
			gridWidth: getCurrentGridWidth(state),
			sectionsWidth: {
				...sectionsWidth,
				[section]: newSectionWidth
			}
		}),
		sectionsWidth: {
			...sectionsWidth,
			[section]: newSectionWidth
		}
	});
};

export const getNextSortOrder = (currentSort, nextSortColumnId) => {
	if (currentSort.name === nextSortColumnId) {
		switch (currentSort.order) {
			case ASCENDING_ORDER:
				return DESCENDING_ORDER;
			case DESCENDING_ORDER:
				return NO_SORT;
			default:
				return ASCENDING_ORDER;
		}
	} else {
		return ASCENDING_ORDER;
	}
};

const whiteSpaceAdjustHelper = ({
	columns,
	state,
	updateState,
	notifyConsumer = true,
	dispatch
}) => {
	const whiteSpaceAdjustedColumns = getWhiteSpaceAdjustedColumns({
		columns,
		sectionsWidth: state.sectionsWidth,
		gridWidth: getCurrentGridWidth(state)
	});

	updateState({
		columns: whiteSpaceAdjustedColumns
	});
	if (notifyConsumer) {
		dispatch(COLUMN_DEFINITION_ATTRIBUTE_UPDATED, { columns: whiteSpaceAdjustedColumns });
	}
};


export const handleColumnAutoSize = ({ state, action: { payload }, updateState, dispatch }) => {
	const { column, columns, notifyConsumer = true } = payload;
	const visibleTasks = getVisibleTasks(state);
	const updatedColumns = autoSizeColumn({ visibleTasks, columns, column });

	whiteSpaceAdjustHelper({
		updateState,
		dispatch,
		notifyConsumer,
		state,
		columns: updatedColumns
	});
};

export const handleAllColumnAutoSize = ({ state, updateState, dispatch }) => {
	const { columns } = state;
	const visibleTasks = getVisibleTasks(state);
	const updatedColumns = autoSizeAllColumns({ visibleTasks, columns });

	whiteSpaceAdjustHelper({
		updateState,
		dispatch,
		notifyConsumer: true,
		state,
		columns: updatedColumns
	});
};

export const handleColumnsWithoutWidthAutoSize = ({ state, action: { payload = {} }, dispatch, updateState }) => {
	const { columns } = state;
	const { scrollbarWidth } = payload;
	const visibleTasks = getVisibleTasks(state);
	// TODO: Make the setAutowidthForColumnsWithoutWidth  function signature similar to autoSizeAllColumns
	const updatedColumns = setAutowidthForColumnsWithoutWidth(columns, visibleTasks);
	const updatedState = {
		...state,
		scrollbarWidth: scrollbarWidth || state.scrollbarWidth
	};

	whiteSpaceAdjustHelper({
		updateState,
		dispatch,
		state: updatedState,
		columns: updatedColumns,
		notifyConsumer: false
	});
};

export const handleShowHeaderContextMenu = ({ updateState, action, state }) => {
	const { payload } = action;
	const { column, segment, contextMenuId } = payload;
	const headerConfig = get(payload, 'column.columnId', null) !== get(state.headerContextMenu, 'column.columnId', null)
		? { headerContextMenu: payload }
		: {};

	updateState({
		selectedCell: {
			rowIndex: 0,
			columnId: column.columnId,
			selectedContextMenu: contextMenuId,
			segment
		},
		...RESET_CONTEXT_MENU,
		...headerConfig
	});
};

export const handleShowCellContextMenu = ({ updateState, action, state, dispatch }) => {
	const { payload } = action;
	const { column, segment, task, contextMenuId } = payload;
	const { cellContextMenu, selectedCell: { selectedRowIds: previousSelectedRowIds = new Set() } } = state;
	const selectedRowIds = previousSelectedRowIds.has(task.id)
		? previousSelectedRowIds
		: new Set().add(task.id);
	const cellConfig = get(column, 'columnId', null) === get(cellContextMenu, 'column.columnId', null) &&
		get(task, 'id', null) === get(cellContextMenu, 'task.id', null)
		? {}
		: {
			cellContextMenu: payload,
			selectedCell: {
				rowIndex: task.index + 1,
				columnId: column.columnId,
				segment,
				selectedContextMenu: contextMenuId,
				selectedRowIds
			},
			isScrolling: false
		};

	updateState({
		...RESET_CONTEXT_MENU,
		...cellConfig
	});
	dispatch(ALL_ROWS_SELECTION_TOGGLED, { allTasksSelected: true });
};

export const handleContextmenuOptionClick = ({ updateState, action: { payload }, dispatch }) => {
	const { eventName, data, optionalPayload } = payload;

	dispatch(eventName, {
		...data,
		...optionalPayload
	});

	// To close contextmenu.
	updateState({
		rowContextMenu: null,
		cellContextMenu: null
	});
};

export const handleShowRowContextMenu = ({ updateState, action, state, dispatch }) => {
	const { rowContextMenu } = state;
	const { payload } = action;
	const { column, segment, task, contextMenuId } = payload;
	const cellConfig = get(payload, 'task.id', null) === get(rowContextMenu, 'task.id', null)
		? {}
		: {
			rowContextMenu: payload,
			selectedCell: {
				rowIndex: task.index + 1,
				columnId: column.columnId,
				segment,
				selectedContextMenu: contextMenuId
			}
		};
	if (!get(payload, 'task.selected')) {
		dispatch(ALL_ROWS_SELECTION_TOGGLED, { allTasksSelected: true });
		dispatch(ROWS_SELECTION_TOGGLED, { task });
	}

	updateState({
		...RESET_CONTEXT_MENU,
		...cellConfig
	});
};

export const handleRowSelectionToggle = ({ state, action, updateState, dispatch }) => {
	const { selectedTasksCount = 0, tree } = state;
	const { task } = action.payload;
	const newSelectedTasksCount = task.selected ? selectedTasksCount - 1 : selectedTasksCount + 1;
	const treeUtils = getTreeUtils(tree);

	treeUtils.updateTask(task, {
		selected: !task.selected
	});

	treeUtils.buildFlatList();
	updateState({
		tree: {
			...tree
		},
		selectedTasksCount: newSelectedTasksCount
	});

	dispatch(SN_DATAGRID_ROW_SELECTED_SET, {
		taskId: task.id,
		value: task.selected
	});
};

export const handleAllRowsSelectionToggle = ({ state, action, updateState, dispatch }) => {
	const { tree } = state;
	const { allTasksSelected } = action.payload;
	const treeUtils = getTreeUtils(tree);

	treeUtils.updateTasks({
		selected: !allTasksSelected
	});

	updateState({
		tree: {
			...tree
		},
		selectedTasksCount: allTasksSelected? 0 : tree.tasks.length
	});
	if (allTasksSelected) {
		dispatch(SN_DATAGRID_ALL_ROWS_UNSELECTED);
	} else {
		dispatch(SN_DATAGRID_ALL_ROWS_SELECTED);
	}
};

export const handleCellClicked = ({ action, state, updateState }) => {
	const { rowIndex, columnId, taskId, segment, appendSelection, includeCurrent } = action.payload;
	const { selectedCell: { selectedRowIds = new Set(), columnId: selectedColId } } = state;
	const updatedSet = appendSelection || selectedRowIds.has(taskId) && selectedColId === columnId
		? new Set(selectedRowIds)
		: new Set();
	if (includeCurrent) {
		if (selectedRowIds.has(taskId) && appendSelection) {
			updatedSet.delete(taskId);
			updateState({
				path: 'selectedCell.selectedRowIds',
				operation: 'set',
				value: updatedSet
			});

			return;
		} else if (!selectedRowIds.has(taskId)) {
			updatedSet.add(taskId);
		}
	}
	const selectedCell = {
		rowIndex,
		columnId,
		segment,
		selectedRowIds: updatedSet
	};

	updateState({
		selectedCell,
		...RESET_CONTEXT_MENU
	});
};

