export const handleCellBlurred = ({ state, updateState }) => {
	if (!isEmpty(state.selectedCell)) {
		updateState({
			selectedCell: {}
		});
	}
};
export const handleCopyCells = ({ updateState, action, state }) => {
	const { column: { name, type, isChoice }, task } = action.payload;
	const { tree, selectedCell: { selectedRowIds = new Set().add(task.id) } } = state;
	const treeUtils = getTreeUtils(tree);
	const clipBoard = [...selectedRowIds]
		.reduce((clipBoard, id) => {
			const taskNode = treeUtils.getNodeById(id);
			if (taskNode && !taskNode.temporary) {
				clipBoard.push(taskNode.taskData[name]);
			}

			return clipBoard;
		}, []);

	clipBoard.push({ name, type, isChoice });
	updateState({ clipBoard });
};
export const handlePasteCells = ({ state, dispatch, action }) => {
	const { clipBoard = [], tree } = state;
	const { column: { name }, task: taskData } = action.payload;
	const treeUtils = getTreeUtils(tree);
	let taskIndex = treeUtils.getNodeById(taskData.id).index || 0;
	const updatedData = {};

	for (let i = 0; i < clipBoard.length - 1; i++) {
		const taskNode = treeUtils.getTaskByIndex(taskIndex);
		if (taskNode && !taskNode.temporary) {
			updatedData[taskNode.id] = { [name]: clipBoard[i] };
			taskIndex++;
		} else {
			break;
		}
	}
	updatedData._sourceColumn = clipBoard[clipBoard.length - 1];

	dispatch(SN_DATAGRID_TASKS_PASTED, updatedData);
};
export const handleGridEditStarted = ({ state, dispatch, action }) => {
	const { task, column } = action.payload;
	const { tree: { tasks }, config, columns, selectedCell: { selectedRowIds = new Set().add(task.id) } } = state;
	const { editMode } = config;

	dispatch(SN_DATAGRID_ROW_EDIT_STARTED, {
		taskId: task.id,
		columnsConfig: getColConfigForEditing(columns, task, column, editMode),
		editMode,
		selectedTaskIds: editMode === CELL_EDIT_MODE
			? [...selectedRowIds]
			: getEditableSelectedTasks(tasks, task.id).map(({ id }) => id)
	});
};

export const handleCopyRows = ({ updateState, action, state }) => {
	const { tasks } = state.tree;
	const { task } = action.payload;
	const copiedRowIds = getEditableSelectedTasks(tasks, task.id).map(taskNode => taskNode.id);

	updateState({ copiedRowIds });
};
export const handlePasteRows = ({ state, dispatch, action }) => {
	const { copiedRowIds = [], tree } = state;
	const treeUtils = getTreeUtils(tree);
	const { task: taskData } = action.payload;
	let taskIndex = treeUtils.getNodeById(taskData.id).index || 0;
	const updatedData = {};

	for (const item of copiedRowIds) {
		const taskNode = treeUtils.getTaskByIndex(taskIndex);
		if (taskNode && !taskNode.temporary) {
			updatedData[taskNode.id] = treeUtils.getTaskColumnData(item);
			taskIndex++;
		} else {
			break;
		}
	}

	dispatch(SN_DATAGRID_TASKS_PASTED, updatedData);
};

export const handleInsertRows = ({ state, dispatch, action }) => {
	const { copiedRowIds = [], tree } = state;
	const treeUtils = getTreeUtils(tree);
	const { task, mode } = action.payload;
	const updatedData = {
		mode,
		targetTask: task,
		copiedTasks: copiedRowIds.map(id => treeUtils.getTaskColumnData(id))
	};

	dispatch(SN_DATAGRID_INSERT_COPIED_ROWS, updatedData);
};

export const handleColumnReset = ({ updateState, state, dispatch, action: { payload } }) => {
	const { columns } = payload;
	const { config, sortBy, groupBy, columns: oldColumns } = state;

	const gridColumns = getColumnModels({ columns, config });
	let allColumns = getAllVisibleBottomLevelCols(oldColumns);
	const isColumnFiltered = allColumns.reduce((prevVal, col) => prevVal || col.filter, false);

	allColumns = allColumns.map(column => ({
		...column,
		filter: null
	}));

	if (isColumnFiltered) { // one of the columns if filtered, removing all filters
		dispatch(GRID_COLUMN_FILTERED, {
			filter: {},
			column: allColumns[0],
			notifyConsumer: false
		});
	}

	if (groupBy.length > 0) { // one of the columns is grouped, removing all grouping
		dispatch(GRID_GROUP_UNGROUP, {
			groupByValue: [],
			notifyConsumer: false
		});
	}

	const sortColName = get(sortBy, 'name', null);
	if (sortColName) { // one of the columns is sorted, removing the sorting
		dispatch(COLUMN_SORTED, {
			name: sortColName,
			order: NO_SORT,
			notifyConsumer: false
		});
	}
	// Resetting copied cells/rows
	dispatch(COPY_RESET_REQUESTED);
	// we are not rendering here as COLUMNS_INIT_WIDTH_AUTO_ADJUSTED will render the grid anyway
	updateState([
		{
			path: 'columns',
			value: gridColumns.columns,
			operation: 'set',
			shouldRender: false
		},
		{
			path: 'displaySubColumnRow',
			value: gridColumns.displaySubColumnRow,
			operation: 'set',
			shouldRender: false
		}
	]);

	dispatch(COLUMNS_INIT_WIDTH_AUTO_ADJUSTED);
};

export const handleOverflowPopover = ({
	action: { payload: { taskId } },
	state: { overflowPopover },
	updateState
}) => {
	updateState({
		overflowPopover: overflowPopover === taskId ? null : taskId
	});
};

export const handlePopoverDisplayToggle = ({ action, updateState }) => {
	if (!get(action, 'payload.value', true)) {
		updateState({ ...RESET_CONTEXT_MENU });
	}
};

export const handleCopyResetRequested = ({ updateState }) => {
	updateState([
		{
			path: 'clipBoard',
			value: [],
			operation: 'set',
			shouldRender: false
		},
		{
			path: 'copiedRowIds',
			value: [],
			operation: 'set',
			shouldRender: false
		}
	]);
};
