/* eslint-disable max-len */
import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import rtlBehavior from '@servicenow/behavior-rtl';
import view from './view';
import {
	CONFIG_CLIENT,
	SN_DATAGRID_COLUMNS_UPDATED,
	SN_DATAGRID_COLUMNS_RESET,
	SN_DATAGRID_SORT_CHANGED,
	SN_DATAGRID_FILTER_CHANGED,
	SN_DATAGRID_ROW_GROUP_CHANGED,
	SN_DATAGRID_ROW_ORDER_CHANGED,
	SN_DATAGRID_ROW_EDIT_STARTED,
	SN_DATAGRID_PREVIEW_RECORD_ICON_CLICKED,
	SN_DATAGRID_ROW_SELECTED_SET,
	SN_DATAGRID_ALL_ROWS_SELECTED,
	SN_DATAGRID_ALL_ROWS_UNSELECTED,
	SN_DATAGRID_ROW_EXPAND_SET,
	SN_DATAGRID_ALL_ROWS_EXPANDED,
	SN_DATAGRID_ALL_ROWS_COLLAPSED,
	SN_DATAGRID_TASKS_PASTED,
	SN_DATAGRID_INSERT_COPIED_ROWS
} from '../constants';

/**
 * sn-datagrid is an UI component that accepts data and configuration to render grid. The primary features of data-grid component is to display tasks in a grid
 *
 * ```
 * <sn-datagrid
 * 	columns={[{name: 'text', label: 'Name' type:'text', width:  300}, {name:  'startDate', label:  'Start Date', type: 'date_time'}, {name:  'endDate', label:  'End Date' type: 'date_time'}]}
 * 	config={{reorderColumns: true, reorderRows: { enable: true, labelSourceColumn: 'text'}, freezeColumns: true}}
 * 	tasks={[{id:  1,  text: {value: 'Project #1', displayValue: 'Project #1'} , startDate: {value: '2019-07-11 02:30:00', displayValue: '2019-07-11 02:30:00'}, endDate: {value: '2019-07-15 11:30:00', displayValue: '2019-07-15 11:30:00'}}, {id:  2, text: {value: 'Task #2', displayValue: 'Task #2'}, parent:  1, startDate: {value: '2019-07-11 02:30:00' displayValue: '2019-07-11 02:30:00'}, endDate: {value: '2019-07-15 11:30:00', displayValue: '2019-07-15 11:30:00'}}, {id:  3, text:  {value: 'Task #3', displayValue: 'Task #3'}, parent:  2, startDate: {value: '2019-07-11 02:30:00', displayValue: '2019-07-11 02:30:00'}, endDate: {value: '2019-07-15 11:30:00', displayValue: '2019-07-15 11:30:00'}}]}>
 * </sn-datagrid>
 * ```
 *
 * @seismicElement sn-datagrid
 * @summary sn-datagrid is an UI component that accepts data and configuration to render grid. The primary features of data-grid component is to display tasks in a grid
 */
createCustomElement('sn-datagrid', {
	renderer: { type: snabbdom },
	view,
	initialState: {},
	behaviors: [
		{ behavior: rtlBehavior }
	],
	properties: {
		/**
		 * An object with properties to configure the grid
		 * @type {{rowHeight: number, rowActionsConfig: boolean, rowGroup: boolean, filter: boolean, sort: boolean, freezeColumns: boolean, reorderColumns: boolean, reorderRows: object, readonly: boolean, rowSelectionCheckbox: boolean, previewRecordIcon: boolean, defaultSortBy: object, sectionWidth: Object, hasHierarchy: boolean, hierarchyPadding: number, smartRendering: boolean, mode: string, toolbar: Object, displayTotalRow: boolean, alternateRowHighlight: boolean, rowContextMenuRenderer: function, cellContextMenuRenderer: function, editSlotsRenderer: function}}
		 */
		config: {
			default: CONFIG_CLIENT,
			schema: {
				type: 'object',
				properties: {
					rowHeight: { type: 'number' },
					rowActionsConfig: { type: 'boolean' },
					rowGroup: { type: 'boolean' },
					filter: { type: 'boolean' },
					sort: { type: 'boolean' },
					freezeColumns: { type: 'boolean' },
					reorderColumns: { type: 'boolean' },
					reorderRows: {
						type: 'object',
						properties: {
							enable: { type: 'boolean' },
							labelSourceColumn: { type: 'string' }
						}
					},
					readonly: { type: 'boolean' },
					rowSelectionCheckbox: { type: 'boolean' },
					previewRecordIcon: { type: 'boolean' },
					defaultSortBy: {
						type: 'object',
						properties: {
							name: { type: 'string' },
							order: { type: 'string', enum: ['asc', 'desc'] }
						}
					},
					// TODO: evaluate the usage of it
					sectionWidth: { type: 'object' },
					hasHierarchy: { type: 'boolean' },
					hierarchyPadding: { type: 'number' },
					smartRendering: { type: 'boolean' },
					mode: { type: 'string' },
					toolbar: {
						type: 'object',
						properties: {
							display: { type: 'boolean' },
							rowCount: { type: 'boolean' },
							title: { type: 'string' },
							subtitle: { type: 'string' },
							displayHelpButton: { type: 'boolean' }
						}
					},
					displayTotalRow: { type: 'boolean' },
					alternateRowHighlight: { type: 'boolean' },
					resetColumns: { type: 'boolean' }
				}
			}
		},
		/**
		 * An array of column objects that describe each column header in the grid.
		 * @type {{name: string, label: string, width: number, minWidth: number, section: 'center'|'left'|'right', type:'string'|'text'|'number'|'date'|'datetime', subColumns: column[], timeVariantGroup: string, hidden: boolean, cellRenderer: function, sortable: boolean, hideable: boolean, groupable: boolean, editable: boolean, expandSubColumns: boolean, renderer: function, getValue: function, getDisplayValue: function, setValue: function, getSortComparator: function}[]}
		 */
		columns: {
			default: [],
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						name: { type: 'string' },
						label: { type: 'string' },
						width: { type: 'number' },
						minWidth: { type: 'number' },
						section: { type: 'string', enum: ['center', 'left', 'right'] },
						type: { type: 'string', enum: ['string', 'text', 'number', 'date', 'date_time', 'reference', 'avatar', 'avatar_group'] },
						subColumns: { type: 'array', items: { type: 'object' } },
						timeVariantGroup: { type: 'string' },
						hidden: { type: 'boolean' },
						sortable: { type: 'boolean' },
						hideable: { type: 'boolean' },
						groupable: { type: 'boolean' },
						editable: { type: 'boolean' },
						expandSubColumns: { type: 'boolean' },
						choices: { type: 'array', items: { type: 'object' } }
					}
				}
			}
		},
		/**
		 * An array of task objects that displays rows in the grid.
		 * @type {Object[]}
		 */
		tasks: {
			default: [],
			schema: {
				type: 'array',
				items: { type: 'object' }
			}
		},
		/**
		 * An object to sort the column with the order
		 * @type {{name: string, order: 'asc'|'desc'}}
		 */
		sortBy: {
			default: {},
			schema: {
				type: 'object',
				properties: {
					name: { type: 'string' },
					order: { type: 'string', enum: ['asc', 'desc'] }
				}
			}
		},
		/**
		 * An array of column names to apply the group by in order.
		 * @type {string[]}
		 */
		groupBy: {
			default: [],
			schema: {
				type: 'array',
				items: { type: 'string' }
			}
		},
		/**
		 * An object to set the page number, records limit
		 * @type {{limit: number, page: number, visibleCount: number}}
		 */
		pagination: {
			default: {
				limit: 10,
				page: 1,
				visibleCount: 10
			},
			schema: {
				type: 'object',
				properties: {
					limit: { type: 'number' },
					page: { type: 'number' },
					visibleCount: { type: 'number' }
				}
			}
		}
	},
	dispatches: {
		/**
		 * Dispatched when the one of the properties width, hidden, section, order of one or more than one column is changed.
		 * Payload contains an array of columns.
		 * @type {columns: Object[]}
		 */
		[SN_DATAGRID_COLUMNS_UPDATED]: {},

		/**
		 * Dispatched when sort order or sorting column changes
		 * Payload contains the sortBy object with name and order property.
		 * @type {{sortBy: Object}}
		 */
		[SN_DATAGRID_SORT_CHANGED]: {},

		/**
		 * Dispatched when column filter changes
		 * @type {{filterExpression: String}}
		 */
		[SN_DATAGRID_FILTER_CHANGED]: {},

		/**
		 * Dispatched when grouped column(s) changes
		 * Payload contains the array of columnId.
		 * @type {{groupBy: String[]}}
		 */
		[SN_DATAGRID_ROW_GROUP_CHANGED]: {},

		/**
		 * Fired when rows are reordered
		 */
		[SN_DATAGRID_ROW_ORDER_CHANGED]: {},

		/**
		 * Dispatched when editing is enabled with rowEdit editMode and cell is double-clicked
		 * @type {{taskId: String, columnsConfig: Object[]}}
		 */
		[SN_DATAGRID_ROW_EDIT_STARTED]: {},

		/**
		 * Dispatched when Preview icon is clicked.
		 * @type {{taskId: String}}
		 */
		[SN_DATAGRID_PREVIEW_RECORD_ICON_CLICKED]: {},

		/**
		 * Dispatched when a row is selected or unselected
		 * @type {{taskId: String, value: Boolean}}
		 */
		[SN_DATAGRID_ROW_SELECTED_SET]: {},

		/**
		 * Dispatched when all rows are selected
		 * @type {{}}
		 */
		[SN_DATAGRID_ALL_ROWS_SELECTED]: {},

		/**
		 * Dispatched when all rows are unselected
		 * @type {{}}
		 */
		[SN_DATAGRID_ALL_ROWS_UNSELECTED]: {},

		/**
		 * Dispatched when a row is expanded or collapsed
		 * @type {{taskId: String, value: Boolean}}
		 */
		[SN_DATAGRID_ROW_EXPAND_SET]: {},

		/**
		 *  Dispatched when expand all button is clicked
		 * @type {{}}
		 */
		[SN_DATAGRID_ALL_ROWS_EXPANDED]: {},

		/**
		 *  Dispatched when collapse all button is clicked
		 * @type {{}}
		 */
		[SN_DATAGRID_ALL_ROWS_COLLAPSED]: {},

		/**
		 * Dispatched when reset column button is clicked
		 * @type {{}}
		 */
		[SN_DATAGRID_COLUMNS_RESET]: {},
		/**
		 * Dispatched after pasting copied values
		 * @type {{}}
		 */
		[SN_DATAGRID_TASKS_PASTED]: {},
		/**
		 * Dispatched on clicking `Insert copied rows` after copying rows.
		 * The `targetTask` attribute contains the details of the task on which this action was dispatched.
		 * the `copiedTasks` attribute is an array of copied task objects
		 * @type {{}}
		 */
		[SN_DATAGRID_INSERT_COPIED_ROWS]: {}
	},
	slots: {
		/**
		 * 'toolbar-icons' slot can be used to add icons in toolbar.
		 * @toolbar-icons
		 */
		'toolbar-icons': {},
		/**
		 * empty-state slot can be used to customize the empty state (No tasks) view
		 */
		'empty-state': {}
	}
});
