import { actionTypes } from '@servicenow/ui-core';
import { getVisibleHeight } from '../../core/helpers/smartRendering';
import { getTasksAndScrollheight } from '../../core/helpers/gridStateParams';
import getGridConfig from '../../core/helpers/transformConfig';
import {
	GRID_GROUP_UNGROUP,
	COLUMNS_RESET,
	COLUMN_SORTED,
	COMPONENT_HOST_RESIZED,
	COLUMNS_INIT_WIDTH_AUTO_ADJUSTED,
	GRID_BORDER,
	VIRTUAL_SCROLLER_HEIGHT,
	NOW_PAGINATION_HEIGHT,
	TOOLBAR_HEIGHT,
	GROUP_PILL_CONTAINER_HEIGHT,
	SERVER_MODE
} from '../../constants';
import {
	getColumnModels,
	validateProperties
} from '../../core/helpers/gridStateParams';

import { getBrowserScrollbarWidth } from '../../core/helpers/elements';
import {
	getWhiteSpaceAdjustedColumns,
	getCurrentGridWidth,
	columnsPropHasSubColumns
} from '../../core/helpers/sections';
import {
	observedElementsMap,
	resizeObserver
} from '../../core/helpers/componentResize';
import debounce from 'lodash/debounce';

const {
	COMPONENT_CONNECTED,
	COMPONENT_DISCONNECTED,
	COMPONENT_DOM_READY,
	COMPONENT_PROPERTY_CHANGED
} = actionTypes;

import { getUpdatedTasksWithCurrentState } from '../../core/helpers/tasks';

export default {
	[COMPONENT_PROPERTY_CHANGED]: ({
		action: {
			payload: { name, value }
		},
		state,
		updateState,
		dispatch,
		host
	}) => {
		if (name === 'tasks') {
			const {
				config,
				columns,
				sortBy,
				groupBy,
				tree
			} = state;
			/**
			 * TODO: rename getTasksAndScrollheight
			 */
			const gridTasks = getTasksAndScrollheight({
				columns,
				tasks: value,
				config,
				dispatch,
				sortBy,
				groupBy,
				taskExpandMap: tree.taskExpandMap
			});
			const stateTasks = tree.tasks || [];
			const selectedTasks = stateTasks.filter(task => task.selected);
			const updatedTasks = getUpdatedTasksWithCurrentState({
				...state,
				tree: gridTasks.tree,
				selectedTasks
			});
			const newTask = Array.isArray(value) && value.length > 0 && value[value.length - 1].id === -1;
			if (newTask) {
				const gridWrapper = host.shadowRoot.querySelector('.sn-datagrid-body-wrapper');

				setTimeout(() => {
					gridWrapper.scrollTo({
						top: gridWrapper.scrollHeight,
						left: 0,
						behavior: 'smooth'
					});
				}, 100);
			}
			updateState({
				...gridTasks,
				...updatedTasks
			});
		} else if (name === 'groupBy') {
			dispatch(GRID_GROUP_UNGROUP, { groupByValue: value, notifyConsumer: false });
		} else if (name === 'sortBy') {
			const { name, order } = value;

			dispatch(COLUMN_SORTED, { name, order, notifyConsumer: false });
		} else if (name === 'columns') {
			/**
			 * When the column property is changed, the grid resets
			 * sorting, filtering, grouping will be removed
			 * column arrangement and width changes will also be reset
			 */
			dispatch(COLUMNS_RESET, { columns: value });
		}
	},
	[COMPONENT_DOM_READY]: ({ host, updateState, state, dispatch }) => {
		const { tasks, sortBy, groupBy, columns, config } = validateProperties(state.properties);
		const updatedConfig = getGridConfig(config);

		const gridColumns = getColumnModels({ columns, config: {
			readonly: updatedConfig.readonly,
			editMode: updatedConfig.editMode
		}
		});
		const partialState = getTasksAndScrollheight({
			columns: gridColumns.columns,
			config: updatedConfig,
			groupBy,
			tasks,
			sortBy,
			dispatch
		});

		const { rowHeight, displayTotalRow, toolbar, rowGroup } = updatedConfig;
		const gridBodyWrapperElem = host.shadowRoot.querySelector('.sn-datagrid-container');
		const gridWidth = gridBodyWrapperElem.getBoundingClientRect().width - GRID_BORDER;
		const scrollbarWidth = getBrowserScrollbarWidth(host);
		let visibleHeight = getVisibleHeight(gridBodyWrapperElem);

		visibleHeight = visibleHeight - VIRTUAL_SCROLLER_HEIGHT - rowHeight;
		if (updatedConfig.mode === SERVER_MODE) { // server mode has pagination at the bottom
			visibleHeight = visibleHeight - NOW_PAGINATION_HEIGHT;
		}
		if (toolbar.display) {
			visibleHeight -= TOOLBAR_HEIGHT;
		}
		if (displayTotalRow) {
			visibleHeight -= rowHeight;
		}
		if (rowGroup) {
			visibleHeight -= GROUP_PILL_CONTAINER_HEIGHT;
		}
		if (columnsPropHasSubColumns(columns)) {
			visibleHeight -= rowHeight; // Header sub-column's row height
		}

		updateState({
			visibleHeight,
			gridWidth,
			scrollbarWidth,
			...gridColumns,
			...partialState,
			config: updatedConfig
		});
		dispatch(COLUMNS_INIT_WIDTH_AUTO_ADJUSTED);
	},
	[COMPONENT_HOST_RESIZED]: {
		effect: ({ updateState, host, state }) => {
			const { columns, sectionsWidth } = state;
			const gridBodyWrapperElem = host.shadowRoot.querySelector(
				'.sn-datagrid-body-wrapper'
			);
			const gridWidth = gridBodyWrapperElem.getBoundingClientRect().width;
			const visibleHeight = getVisibleHeight(gridBodyWrapperElem);
			const scrollbarWidth = getBrowserScrollbarWidth(host);
			const updatedColumns = getWhiteSpaceAdjustedColumns({
				columns,
				sectionsWidth,
				gridWidth: getCurrentGridWidth({
					...state,
					gridWidth
				})
			});

			updateState({
				columns: updatedColumns,
				gridWidth,
				visibleHeight,
				scrollbarWidth
			});
		},
		stopPropagation: true
	},
	[COMPONENT_CONNECTED]: {
		effect: ({
			dispatch,
			action: {
				payload: { host }
			}
		}) => {
			const dispatchHostResized = debounce(() => {
				dispatch(COMPONENT_HOST_RESIZED);
			}, 300);

			resizeObserver.observe(host);
			observedElementsMap.set(host, dispatchHostResized);
		},
		stopPropagation: true
	},
	[COMPONENT_DISCONNECTED]: {
		effect: ({ action: { payload } }) => {
			resizeObserver.unobserve(payload.host);
			observedElementsMap.delete(payload.host);
		},
		stopPropagation: true
	}
};
