import { createLog, isDebugMode } from './logger';

/**
 * Measures execution time of the given function. 
 * Most of the code is from now-calendar.
 * @param {*} f Function
 */
export function measure(f, bindingObject) {
	if (isDebugMode()) {
		const LOG = createLog('perf');

		return function mesureProxy(...args) {
			const t1 = performance.now();
			let ret;

			if (bindingObject) {
				ret = f.apply(bindingObject, args);
			} else {
				ret = f.apply(this, args);
			}

			const t2 = performance.now();

			LOG.debug('%cTime taken by "' + f.name + '": %c' + (t2-t1) + ' ms', 'color:green; font-style:italics;', 'color:red; font-weight:bold;'); // eslint-disable-line no-console

			return ret;
		};
	} else {
		if (bindingObject) {
			return f.bind(bindingObject);
		}

		return f;
	}
}

export default measure;
