import { createTreeObject } from './IndexTree';
import { getAggregationConfigs } from '../../columnModel';

// eslint-disable-next-line max-len
export function buildTree(data, parentField, sortBy = {}, taskExpandMap, columns, generateTotalTaskNode, groupBy) {
	const { name, order } = sortBy;
	const aggregateConfigs = getAggregationConfigs(columns);
	const initObject = {
		parentColumn: parentField,
		sortByColumnId: name,
		sortOrder: order,
		aggregateConfigs,
		generateTotalTaskNode,
		columns,
		groupBy
	};

	// eslint-disable-next-line prefer-const
	let { tree, treeUtils } = createTreeObject(data, taskExpandMap, columns);

	tree = treeUtils.init(initObject);

	return tree;
}


