import '@servicenow/now-icon';
import { renderSection, renderConfigSection } from './section';
import { Fragment, createRef } from '@servicenow/ui-renderer-snabbdom';
import renderLayover from '../layovers/rowDragDropLayover';
import isEmpty from 'lodash/isEmpty';
import get from 'lodash/get';
import { NEW_ROW, EMPTY_STATE_MESSAGE, SECTION, AGGREGATION, PRIMARY, SN_DATAGRID_NEW_TASK_REQUESTED, NOW_GLOBAL_SPACE_SM } from '../../../constants';
import '../../../sn-datagrid-context-menu';
import { getRowContextMenuItems } from '../../helpers/editingHelper';

const renderGridBody = ({
	visibleTasks,
	state,
	dispatch,
	bodyWrapperRef,
	setRef,
	syncScroll,
	height,
	segment,
	configSectionWidth,
	allowRowsReordering
}) => {
	const refName = 'gridBodyContainerRef';
	const {
		config,
		rowContextMenu,
		isReorderingRows,
		isScrolling,
		scrollHeight,
		visibleHeight,
		selectedTasksCount
	} = state;

	if (isEmpty(config)) {
		return;
	}
	const { rowContextMenuRenderer, rowHeight } = config;
	const hasScroll = scrollHeight > visibleHeight;
	const gridBodyContainerRef = createRef();

	setRef(refName, gridBodyContainerRef);
	const bodyClassName = !isReorderingRows && isScrolling
		? 'sn-datagrid-body disable-pointer-events'
		: 'sn-datagrid-body';
  const handlePopoverPosition = ({ elm }) => {
		elm.positionTarget = rowContextMenu.currentTarget;
	};


	return (
		<div
			style={{
				position: 'relative',
				height
			}}
			ref={gridBodyContainerRef}
		>
			{
				rowContextMenu && rowContextMenu.segment === segment
					? <now-popover
						hide-tail
						manage-opened
						opened={true}
						positionTarget={rowContextMenu.currentTarget}>
						<sn-datagrid-context-menu
							slot="content"
							parentState={state}
							defaultItems={getRowContextMenuItems(state, rowContextMenu)}
							contextMenu={rowContextMenu}
							contextMenuRenderer={rowContextMenuRenderer}
						/>
					</now-popover>
					: <Fragment />
			}
			<div
				className={bodyClassName}
			>
				{ hasScroll
					? <div
						className="horizontal-lines horizontal-lines-image"
						style={{
							backgroundSize: `10px ${rowHeight}px`
						}}
					/>
					: <Fragment/>
				}
				{ configSectionWidth > 0
					? renderConfigSection({
						visibleTasks,
						state,
						dispatch,
						segment,
						gridBodyContainerRef,
						configSectionWidth,
						allowRowsReordering,
						bodyWrapperRef
					})
					: <Fragment/>
				}

				{ Object.values(SECTION).map(section => renderSection({
					visibleTasks,
					state,
					dispatch,
					section,
					setRef,
					syncScroll,
					segment,
					bodyWrapperRef
				})) }
				{ segment !== AGGREGATION && isReorderingRows ? renderLayover({
					state,
					dispatch,
					bodyWrapperRef
				}) : <Fragment/> }
			</div>
			{ !visibleTasks.length
				? <Fragment>
					<div className="sn-datagrid-empty-grid">
						<now-icon icon="list-outline" size="xl"/>
						<div>
							{EMPTY_STATE_MESSAGE}
						</div>
					</div>
					<div className="sn-datagrid-empty-grid sn-datagrid-empty-grid--slot">
						<slot name="empty-state">
						</slot>
					</div>
				</Fragment>
				: <Fragment></Fragment>
			}
		</div>
	);
};

export default renderGridBody;
