export const DEFAULT_TAB_MAP = {
	SETTINGS_PANEL_TOGGLE: COLUMNS,
	HELP_PANEL_TOGGLE: LEGEND
};

const hideEditingShortcuts = state => get(state, 'properties.readonly', false);
const hideRowCopyShortcut = state => hideEditingShortcuts(state) || !get(state, 'properties.rowCopyPaste', true);

