import {
	SECTION,
	CONFIG_SECTION_ICON_WIDTH,
	MIN_SECTION_WIDTH,
	NOW_GLOBAL_SPACE_SM,
	DISPLAY_TOTAL_ROW_CONFIG_WIDTH,
	SECTION_RESIZER_WIDTH,
	CONFIG_SECTION_BORDER_WIDTH,
	CONFIG_SECTION_COLUMN_NAME
} from '../../constants';
import isEmpty from 'lodash/isEmpty';
import last from 'lodash/last';
import get from 'lodash/get';
import { updateColumns } from '../columnModel';
import { getColumnAutoWidth } from './tasks';

export const getVisibleCols = (columns = []) => columns.filter(column => !column.hidden);
export const getVisibleSubCols = (column) => {
	const visibleSubCols = column.subColumns
		.filter(subCol => !subCol.hidden && (column.expandSubColumns || !subCol.collapsed));
	if (!column.hidden && isEmpty(visibleSubCols)) {
		return column.subColumns
			.filter(subCol => !subCol.hidden).slice(0, 1);
	} else {
		return visibleSubCols;
	}
};

export const getAllLeafColumns = (columns) => {
	const { left, center, right } = columns;
	const allMainColumns = [...left, ...center, ...right];

	return allMainColumns.reduce((allColumns, column) => {
		column.subColumns.length > 0 ? allColumns.push(...column.subColumns) : allColumns.push(column);

		return allColumns;
	}, []);
};

export const getAllVisibleBottomLevelCols = sectionColumns => {
	const columns = getAllLeafColumns(sectionColumns);

	return getVisibleCols(columns);
};

// returns all the columns in the grid with the config section column name if it exists
export const getKeyboardNavigableColumns = (sectionColumns, config)=> {
	const {
		allowRowsReordering,
		rowSelectionCheckbox,
		setRowContextMenu,
		previewRecordIcon
	} = config;

	const doesConfigSectionExist = allowRowsReordering ||
	rowSelectionCheckbox || setRowContextMenu || previewRecordIcon;
	let gridCols = getAllVisibleBottomLevelCols(sectionColumns);

	if (doesConfigSectionExist) {
		gridCols = [
			{ columnId: CONFIG_SECTION_COLUMN_NAME },
			...gridCols
		];
	}

	return gridCols;
};

export const getColumnsWidth = (columns = [], parentColumn) => {
	const visibleColumns = parentColumn ? getVisibleSubCols(parentColumn) : getVisibleCols(columns);

	return visibleColumns
		.reduce((width, column) => {
			width += column.subColumns.length > 0
				? getColumnsWidth(column.subColumns, column)
				: column.autoWidth || column.width;

			return width;
		}, 0);
};

export const getGroupedColumns = (columns = [], groupedColumns = {}) => {
	return columns
		.reduce((groupedColumns, column) => {
			const timeVariantGroup = column.timeVariantGroup;
			const groupedColumnsEntry = groupedColumns[timeVariantGroup];
			if (column.subColumns.length > 0) {
				getGroupedColumns(column.subColumns, groupedColumns);
			} else if (timeVariantGroup && isEmpty(groupedColumnsEntry)) {
				groupedColumns[timeVariantGroup] = Object.assign({}, column);
			} else if (groupedColumnsEntry && groupedColumnsEntry.hidden !== column.hidden) {
				groupedColumns[timeVariantGroup].hidden = false;
			}

			return groupedColumns;
		}, groupedColumns);
};

export const getColumnWidth = ({
	width,
	autoWidth
}) => {
	return autoWidth ? autoWidth : width;
};

export const canReorderRows = (state) => {
	const { groupBy, config } = state;
	const { reorderRows = {} } = config;
	if (reorderRows.enable && isEmpty(groupBy)) {
		return true;
	}

	return false;
};

// Gets the width of the config section, which includes padding given to class ' header-section--config'
export const getConfigSectionWidth = (state) => {
	const {
		rowSelectionCheckbox,
		setRowContextMenu,
		previewRecordIcon,
		displayTotalRow
	} = state.config;
	const allowRowsReordering = canReorderRows(state);
	let configSectionWidth = 0;
	let configSectionCount = 0;
	if (rowSelectionCheckbox) {
		configSectionWidth += CONFIG_SECTION_ICON_WIDTH;
		configSectionCount += 1;
	}
	if (allowRowsReordering) {
		configSectionWidth += CONFIG_SECTION_ICON_WIDTH;
		configSectionCount += 1;
	}

	if (setRowContextMenu) {
		configSectionWidth += CONFIG_SECTION_ICON_WIDTH;
		configSectionCount += 1;
	}

	if (previewRecordIcon) {
		configSectionWidth += CONFIG_SECTION_ICON_WIDTH;
		configSectionCount += 1;
	}

	if (displayTotalRow && configSectionCount < 2) {
		configSectionCount += 1;
		configSectionWidth = DISPLAY_TOTAL_ROW_CONFIG_WIDTH;
	}

	return configSectionCount > 0
		? NOW_GLOBAL_SPACE_SM + configSectionWidth + CONFIG_SECTION_BORDER_WIDTH
		: 0;
};
/*
 * The gridWidth on the state is the width of the entire grid area including config section and scrollbar
 * This method returns the current gridWidth excluding config and scrollbar, which are not fixed
 * Use it whenever gridWidth excluding config and scrollbar is required
 */
export const getCurrentGridWidth = (state) => {
	const { gridWidth, scrollHeight, visibleHeight, scrollbarWidth } = state;
	const scrollWidth = scrollHeight > visibleHeight
		? scrollbarWidth
		: 0;

	return gridWidth - getConfigSectionWidth(state) - scrollWidth;
};
export const getMaxPinnedSectionWidth = ({
	columns,
	sectionsWidth,
	gridWidth,
	section,
	oppSectionActualWidth
}) => {
	const { RIGHT, LEFT, CENTER } = SECTION;
	if (section === CENTER) {
		// Center section has width: auto. Use this method for LEFT and RIGHT section

		return;
	}
	const centerSectionMinWidth = MIN_SECTION_WIDTH;
	const oppSection = section === LEFT ? RIGHT : LEFT;
	let oppSectionWidth = 0;
	if (columns[oppSection].length > 0) {
		oppSectionWidth = oppSectionActualWidth
			? oppSectionActualWidth		// Section resizer width is already included in actual width
			: (sectionsWidth[oppSection] || MIN_SECTION_WIDTH) + SECTION_RESIZER_WIDTH;
	}
	const maxWidth = gridWidth - centerSectionMinWidth - oppSectionWidth - SECTION_RESIZER_WIDTH;

	return maxWidth;
};
export const getPinnedSectionWidth = (
	{
		columns,
		sectionsWidth = [],
		gridWidth,
		columnsWidth,
		section
	},
	oppSectionActualWidth
) => {
	const { CENTER } = SECTION;
	if (section === CENTER) {
		// Center section has width: auto. Use this method for LEFT and RIGHT section

		return MIN_SECTION_WIDTH;
	}
	if (columns[section].length === 0 || columnsWidth === 0) {
		return 0;
	}
	const width = sectionsWidth[section] ? sectionsWidth[section] : columnsWidth;

	return Math.min(
		getMaxPinnedSectionWidth({ columns, sectionsWidth, gridWidth, section, oppSectionActualWidth }),
		width
	) + SECTION_RESIZER_WIDTH;
};

export const getSectionWidthStyle = ({ section, columnsWidth, state }) => {
	const { LEFT, RIGHT } = SECTION;
	const { columns, sectionsWidth } = state;
	const gridWidth = getCurrentGridWidth(state);
	let minWidth;
	let width;
	const sectionWidthParams = { columns, sectionsWidth, gridWidth, section, columnsWidth };

	if (section === LEFT) {
		const leftSectionWidth = getPinnedSectionWidth(sectionWidthParams);

		width = `${leftSectionWidth}px`;
		minWidth = `${leftSectionWidth}px`;
	} else if (section === RIGHT) {
		// Right section width should the remaining width after left section, so providing left section width
		const leftSectionActualWidth = getPinnedSectionWidth({
			...sectionWidthParams,
			section: LEFT,
			columnsWidth: getColumnsWidth(columns[LEFT])
		});
		const rightSectionWidth = getPinnedSectionWidth(sectionWidthParams, leftSectionActualWidth);

		width = `${rightSectionWidth}px`;
		minWidth = `${MIN_SECTION_WIDTH + SECTION_RESIZER_WIDTH}px`;
	} else {
		width = 'auto';
		minWidth = `${MIN_SECTION_WIDTH}px`;
	}

	return {
		width,
		minWidth
	};
};

export const getLastVisibleColumn = columns => {
	const visibleCols = getVisibleCols(columns);
	const lastColumn = last(visibleCols);
	if (lastColumn) {
		const visibleSubCols = getVisibleSubCols(lastColumn);

		return isEmpty(visibleSubCols) ? lastColumn : last(visibleSubCols);
	}
};

export const shouldHighlightRow = (alternateRowHighlight, index) => alternateRowHighlight && index % 2 === 1;

export const emptyConfigItemMarkup = (width) => {
	return <div style={{ width: `${width}px`, height: `${width}px` }}></div>;
};

export const getWhiteSpaceAdjustedColumns = ({ columns, sectionsWidth = [], gridWidth }) => {
	const { LEFT, CENTER, RIGHT } = SECTION;
	const columnWidths = {
		[LEFT]: getColumnsWidth(columns[LEFT]),
		[CENTER]: getColumnsWidth(columns[CENTER]),
		[RIGHT]: getColumnsWidth(columns[RIGHT])
	};

	Object.keys(columns)
		.filter(section => !isEmpty(columns[section]))
		.forEach(section => {
			const sectionColumnsWidth = columnWidths[section];
			let sectionWidth;

			if (section === CENTER) {
				const sectionWidthParams = { columns, sectionsWidth, gridWidth };
				const leftSectionWidth = getPinnedSectionWidth({
					...sectionWidthParams,
					columnsWidth: columnWidths[LEFT],
					section: LEFT
				});
				// Right section width should the remaining width after left section, so providing left section width
				const rightSectionWidth = getPinnedSectionWidth(
					{
						...sectionWidthParams,
						columnsWidth: columnWidths[RIGHT],
						section: RIGHT
					},
					leftSectionWidth
				);

				sectionWidth = Math.max(gridWidth - leftSectionWidth - rightSectionWidth, MIN_SECTION_WIDTH);
			} else {
				sectionWidth = sectionsWidth[section] || MIN_SECTION_WIDTH;
			}

			if (sectionWidth && sectionWidth > sectionColumnsWidth) {
				const lastColumn = getLastVisibleColumn(columns[section]);
				if (lastColumn) {
					const extraSpace = sectionWidth - sectionColumnsWidth;

					columns = updateColumns(columns, lastColumn, {
						autoWidth: getColumnWidth(lastColumn) + extraSpace
					});
				}
			}
		});

	return columns;
};

export const isEmptySection = (columns, section, groupBy) => {
	const { LEFT } = SECTION;

	switch (section) {
		case LEFT:
			return groupBy.length === 0 ? columns.length === 0 : columns.length === 1;
		default:
			return columns.length === 0;
	}
};

/*
	Returns column position object based on the given cases. the following special scenarios are handled
	1) If the given index is a negative number, the column is moved to the last position of the section before the
	 current section i.e LEFT is returned for a CENTER section column, provided the LEFT section isnt empty
	2) If the given index is greater than the length of the current section, the column is moved to the first position
	 of the section after the current section i.e RIGHT is returned for a CENTER section column, provided the RIGHT
	 section isnt empty

	TODO: this functionality should be replaced by standard drag drop keyboard version
	hence disabling test coverage for this
*/


export const getColumnShiftPosition = /* istanbul ignore next */ (columns, column, index, groupBy) => {
	const { CENTER, LEFT, RIGHT } = SECTION;
	const { section } = column;

	const isBeforeSection = index < 0;
	const isAfterSection = index > columns[section].length;
	const getSectionStartPosition = (section) => {
		return {
			section,
			targetIndex: 0
		};
	};
	const getSectionEndPosition = (section) => {
		return {
			section,
			targetIndex: columns[section].length
		};
	};
	let updatedPosition = { section, targetIndex: index };

	if (isBeforeSection) {
		switch (section) {
			case LEFT:
				updatedPosition = getSectionStartPosition(LEFT);
				break;
			case CENTER: {
				if (isEmptySection(columns[LEFT], LEFT, groupBy)) {
					updatedPosition = getSectionStartPosition(CENTER);
				} else {
					updatedPosition = getSectionEndPosition(LEFT);
				}
				break;
			}
			case RIGHT: {
				updatedPosition = getSectionEndPosition(CENTER);
				break;
			}
		}
	} else if (isAfterSection) {
		switch (section) {
			case LEFT:
				updatedPosition = getSectionStartPosition(CENTER);
				break;
			case CENTER: {
				if (isEmptySection(columns[RIGHT], RIGHT)) {
					updatedPosition = getSectionEndPosition(CENTER);
				} else {
					updatedPosition = getSectionStartPosition(RIGHT);
				}
				break;
			}
			case RIGHT: {
				updatedPosition = getSectionEndPosition(RIGHT);
				break;
			}
		}
	}

	return updatedPosition;
};

export const columnsPropHasSubColumns = (columns) => {
	return Object.values(columns).find(col => col.subColumns && col.subColumns.length > 0);
};

export const setAutowidthForColumnsWithoutWidth = (columns, tasks) => {
	const columnsWithoutWidth = getAllLeafColumns(columns).filter(column => !column.width || (get(column, 'autoWidth', 0) > column.width));
	let updatedColumns = Object.assign({}, columns);

	columnsWithoutWidth.forEach(column => {
		const autoWidth = getColumnAutoWidth(tasks, column);

		updatedColumns = updateColumns(updatedColumns, column, { autoWidth });
	});

	return updatedColumns;
};

export const getGridFirstVisibleColumn = columns => {
	const { LEFT, CENTER } = SECTION;
	const firstSectionVisibleCol = columns[LEFT].find(col => !col.hidden) ||
		columns[CENTER].find(col => !col.hidden);

	const firstVisibleCol = isEmpty(firstSectionVisibleCol.subColumns)
		? firstSectionVisibleCol
		: firstSectionVisibleCol.subColumns.find(subCol => !subCol.hidden);

	return firstVisibleCol;
};

export const isColumnNonHidable = (columnHideable, parentColumn) => {
	return !columnHideable || parentColumn && !parentColumn.hideable;
};
