import { createRef } from '@servicenow/ui-renderer-snabbdom';
import { Fragment } from '@seismic/snabbdom-renderer';
import isEmpty from 'lodash/isEmpty';
import {
	getSectionWidthStyle,
	getColumnsWidth,
	getVisibleCols
} from '../../helpers/sections';
import { SECTION_MEMBER } from '../../../constants';

export const renderSection = ({ state, section, setRef, syncScroll }) => {
	const {
		columns: {
			[section]: columns
		},
		sectionsWidth: {
			[section]: sectionWidth
		}
	} = state;
	const visibleCols = getVisibleCols(columns);

	if (isEmpty(visibleCols)) {
		setRef('gridScrollerRef', null, section);

		return <Fragment/>;
	}
	const { VIRTUAL_SCROLLBAR } = SECTION_MEMBER;
	const columnsWidth = getColumnsWidth(visibleCols);
	const gridScrollerRef = createRef();

	setRef('gridScrollerRef', gridScrollerRef, section);
	const sectionWidthStyle = getSectionWidthStyle({ section, sectionWidth, columnsWidth, state });

	return (
		<div
			style={{ ...sectionWidthStyle }}
			className={`virtual-scroller-section virtual-scroller-section--${section}`}
			ref={gridScrollerRef}
			on-scroll={e => syncScroll(e, section, VIRTUAL_SCROLLBAR)}
		>
			<div
				className="virtual-scroller-section-content"
				style={{
					width: `${columnsWidth}px`
				}}
			/>
		</div>
	);
};

export const renderConfigSection = ({ configSectionWidth }) => {
	return (
		<div className="virtual-scroller-section virtual-scroller-section--config"
			style={{
				minWidth: `${configSectionWidth}px`
			}}
		/>
	);
};
