import renderRow from './row';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import {
	SECTION,
	SECTION_MEMBER,
	ALL_ROWS_SELECTION_TOGGLED,
	SECTION_RESIZER_WIDTH,
	CONFIG_SECTION_ICON_WIDTH,
	UNSELECT_ALL_TASKS,
	SELECT_ALL_TASKS,
	SELECTED_CELL_CLASS,
	ARROW_UP_KEY,
	ARROW_DOWN_KEY,
	ARROW_RIGHT_KEY,
	ARROW_LEFT_KEY,
	KEYBOARD_NAVIGATED,
	PRIMARY,
	CELL_CLICKED,
	CONFIG_SECTION_COLUMN_NAME,
	COLUMN_AUTOFITTED
} from '../../../constants';
import isEmpty from 'lodash/isEmpty';
import {
	getColumnsWidth,
	getVisibleCols,
	getVisibleSubCols,
	getSectionWidthStyle,
	emptyConfigItemMarkup,
	getCurrentGridWidth
} from '../../helpers/sections';
import { resizeSection } from '../../helpers/resizer';
import { isComponentRTL } from '../../helpers/gridStateParams';
import last from 'lodash/last';


const renderResizer = ({ section, gridHeaderRef, isRTL, dispatch, state }) => {
	const { columns, sectionsWidth } = state;

	return (
		<div
			style={{ minWidth: `${SECTION_RESIZER_WIDTH}px` }}
			className="column-resizer-container section-resizer"
		>
			<div
				className="column-resizer"
				ondblclick={() => {
					const allVisibleCols = getVisibleCols(state.columns[section]);
					const lastColumn = last(allVisibleCols);
					const hasSubCols = lastColumn.subColumns.length > 0;
					const visibleSubCols = getVisibleSubCols(lastColumn);

					dispatch(COLUMN_AUTOFITTED, {
						column: hasSubCols ? last(visibleSubCols): lastColumn,
						columns: state.columns
					});
				}}
				on-mousedown={() => {
					const { LEFT } = SECTION;
					const { left, right } = gridHeaderRef.current.getBoundingClientRect();

					let sectionHeaderStart;

					if (section === LEFT) {
						sectionHeaderStart = isRTL ? right : left;
					} else {
						sectionHeaderStart = isRTL ? left : right;
					}

					resizeSection({
						section,
						dispatch,
						sectionHeaderStart,
						columns,
						sectionsWidth,
						gridWidth: getCurrentGridWidth(state)
					});
				}}
			>
			</div>
		</div>
	);
};

export const renderSection = ({
	state,
	dispatch,
	section,
	refs,
	setRef,
	syncScroll,
	isRTL
}) => {
	const {
		config,
		columns: {
			[section]: columns
		},
		sectionsWidth: {
			[section]: sectionWidth
		},
		bodySectionKeySuffix: headerSectionKey // body and header should be in sync
	} = state;
	let { gridHeaderRef } = refs;
	const visibleCols = getVisibleCols(columns);
	if (isEmpty(visibleCols)) {
		gridHeaderRef = null;
		setRef('gridHeaderRef', null, section);

		return <Fragment/>;
	}
	const { LEFT, RIGHT } = SECTION;
	const { HEADER } = SECTION_MEMBER;
	const { reorderColumns, sort } = config;
	const columnsWidth = getColumnsWidth(visibleCols);

	setRef('gridHeaderRef', gridHeaderRef, section);

	const sectionWidthStyle = getSectionWidthStyle({ section, sectionWidth, columnsWidth, state });

	if (section === LEFT || section === RIGHT) {
		sectionWidthStyle.width = `${parseFloat(sectionWidthStyle.width) - SECTION_RESIZER_WIDTH}px`;
		sectionWidthStyle.minWidth = `${parseFloat(sectionWidthStyle.minWidth) - SECTION_RESIZER_WIDTH}px`;
	}

	return (
		<Fragment>
			{section === RIGHT
				? renderResizer({ section, gridHeaderRef, isRTL, dispatch, state })
				: <Fragment/>
			}
			<div
				style={{ ...sectionWidthStyle }}
				className={`header-section header-section--${section}`}
				ref={gridHeaderRef}
				role="row"
				on-scroll={e => syncScroll(e, section, HEADER)}
				key={`${section}_${headerSectionKey}`}
			>
				{ renderRow({
					state,
					columns,
					dispatch,
					reorderColumns,
					sort,
					refs,
					section,
					isRTL
				}) }
			</div>
			{section === LEFT
				? renderResizer({ section, gridHeaderRef, isRTL, dispatch, state })
				: <Fragment/>
			}
		</Fragment>
	);
};

export const renderConfigSection = ({
	configSectionWidth,
	allowRowsReordering,
	dispatch,
	state,
	bodyWrapperRef
}) => {
	const {
		tree: { tasks },
		selectedTasksCount,
		config: { rowSelectionCheckbox, setRowContextMenu, previewRecordIcon },
		selectedCell,
		properties: { dir }
	} = state;

	const isRTL = isComponentRTL(dir);
	const allTasksSelected = tasks.length !== 0 && selectedTasksCount === tasks.length;
	const configCellSelected = selectedCell.columnId === CONFIG_SECTION_COLUMN_NAME &&
	selectedCell.rowIndex === 0;
	const selectedCellClassName = configCellSelected ? ` ${SELECTED_CELL_CLASS}`: '';
	// const tabIndex = configCellSelected ? '0': '-1';
	const checkboxIconName = allTasksSelected ? 'checkbox-checked-fill' : 'checkbox-unchecked-fill';

	return (
		<div
			className={`header-section header-section--config ${selectedCellClassName}`}
			style={{
				minWidth: `${configSectionWidth}px`
			}}
			on-click={(e) => {
				e.stopPropagation();
				dispatch(CELL_CLICKED, {
					rowIndex: 0,
					columnId: CONFIG_SECTION_COLUMN_NAME,
					segment: PRIMARY
				});
			}}
			on-focus={() => {
				dispatch(CELL_CLICKED, {
					rowIndex: 0,
					columnId: CONFIG_SECTION_COLUMN_NAME,
					segment: PRIMARY
				});
			}}
			tabindex="0"
			on-keydown={(e) => {
				switch (e.keyCode) {
					case ARROW_UP_KEY:
					// we have to scroll the grid body when the user navigates from header to the body
					// eslint-disable-next-line no-fallthrough
					case ARROW_DOWN_KEY: bodyWrapperRef && bodyWrapperRef.current.scrollTo({ top: 0 });
					// eslint-disable-next-line no-fallthrough
					case ARROW_RIGHT_KEY:
					case ARROW_LEFT_KEY:
						e.preventDefault();
						dispatch(KEYBOARD_NAVIGATED, {
							keyCode: e.keyCode,
							isRTL,
							segment: PRIMARY
						});
				}
			}}
		>
			{
				allowRowsReordering ? emptyConfigItemMarkup(CONFIG_SECTION_ICON_WIDTH) : <Fragment/>
			}
			{
				rowSelectionCheckbox
					? <div className='sn-datagrid-cell-config-item button-action'>
						<now-button-iconic
							size="md"
							variant="tertiary"
							title={allTasksSelected ? UNSELECT_ALL_TASKS : SELECT_ALL_TASKS}
							icon={checkboxIconName}
							bare={true}
							on-click={() => {
								dispatch(ALL_ROWS_SELECTION_TOGGLED, { allTasksSelected });
							}}
							ariaAttributes={{ 'aria-label': allTasksSelected ? UNSELECT_ALL_TASKS : SELECT_ALL_TASKS }}
						>
						</now-button-iconic>
					</div>
					: <Fragment/>
			}
			{
				setRowContextMenu ? emptyConfigItemMarkup(CONFIG_SECTION_ICON_WIDTH) : <Fragment/>
			}
			{
				previewRecordIcon ? emptyConfigItemMarkup(CONFIG_SECTION_ICON_WIDTH) : <Fragment/>
			}
		</div>
	);
};

