/* eslint-disable no-case-declarations */
import {
	findTabbableNodes,
	getActiveElement
} from '@servicenow/library-enhanced-utils';
import inRange from 'lodash/inRange';
import isEmpty from 'lodash/isEmpty';
import { SELECTED_CELL_CLASS } from '../../constants';

// JS `%` doesn't work with negative numbers :(
function mod(n, m) {
	return (n % m + m) % m;
}

/**
 * Reduces text length. Kind of text-overflow: ellipsis.
 * @param {String} label
 */
export const trimLabel = (label, maxLength = 30) => {
	if (!isEmpty(label) && label.length > maxLength) {
		return `${label.substring(0, maxLength - 6)}...`;
	}

	return label;
};

export const findNextFocusTarget = ({
	within = document.body,
	current = getActiveElement(),
	wrap = true,
	shiftKey = false
}) => {
	if (!within) {
		return null;
	}

	const delta = shiftKey ? -1 : 1;
	const tabbableNodes = findTabbableNodes(within);
	const currentIndex = tabbableNodes.indexOf(current);
	const needsWrap = !inRange(currentIndex + delta, 0, tabbableNodes.length);
	if (needsWrap && !wrap) {
		return null;
	}

	const nextIndex = mod(currentIndex + delta, tabbableNodes.length);
	const next = tabbableNodes[nextIndex];

	return next || null;
};

// tab event handler for all the popovers and panels
export const handleTabPress = ({ event, host }) => {
	event.preventDefault();
	const focusTarget = findNextFocusTarget({
		within: host,
		shiftKey: event.shiftKey
	});

	if (focusTarget) {
		focusTarget.focus();
	}
};
export const focusFirstElement = ({
	host
}) => {
	const firstFocusableElement = findNextFocusTarget({
		within: host,
		current: null
	});

	firstFocusableElement && firstFocusableElement.focus();
};

// handles cell selection on cell click and focus after the closing the popovers
export const handleFocus = ({ host, state }) => {
	const { selectedCell, isScrolling, cellContextMenu, sidePanel = {}, isReorderingRows } = state;
	const { selectedContextMenu } = selectedCell;
	let currentElement = host;

	// it is required to bring the events to the row drag and drop layover
	if (isReorderingRows) {
		currentElement = currentElement.shadowRoot.querySelector('.drag-drop-layover');
		currentElement && currentElement.focus();

		return;
	}

	if (!selectedCell) {
		return; // nothing to focus
	}

	// if selectedContextMenu is set then we will focus on it first
	if (selectedContextMenu) {
		currentElement = currentElement.shadowRoot.querySelector(`[data-context-menu-id="${selectedContextMenu}"]`);
		// selectedContextMenu element is not found. so searching inside the header toolbar
		if (!currentElement && !sidePanel.display) {
			// we will focus on the element only when the panel is closed
			currentElement = host.shadowRoot.querySelector('sn-datagrid-header-toolbar');
			currentElement = currentElement.shadowRoot.querySelector(`[data-context-menu-id="${selectedContextMenu}"]`);
		}
	} else if (isEmpty(cellContextMenu)) {
		currentElement = host.shadowRoot.querySelector(`.${SELECTED_CELL_CLASS}`);
	}

	if (isScrolling) { // user has already scrolled so we should prevent scrolling while focussing
		currentElement && currentElement.focus({ preventScroll: true });
	} else {
		currentElement && currentElement.focus();
	}
};

