import noop from 'lodash/noop';
import ResizeObserver from 'resize-observer-polyfill';
/**
 * We are adding our own custom resize observer as the servicenow's resizeObserver is causing issues with sidepanels
 * https://now-servicenow.slack.com/archives/CDXA5TRLP/p1593690496295300
 * TODO: replace the custom implementation after this issue is fixed
 */
export const observedElementsMap = new WeakMap();
export const resizeObserver = new ResizeObserver((entries) => {
	window.requestAnimationFrame(() => entries.forEach(entry => {
		const { target } = entry;

		(observedElementsMap.get(target) || noop)();
	}));
});
