const handleGroupUnGroup = ({
	action: {
		payload = {}
	},
	state,
	dispatch,
	updateState
}) => {
	const { groupByValue, notifyConsumer } = payload;
	const {	groupBy } = state;
	const updatedGroupBy = groupBy;
	// TODO: create new group column after fetching from grouped tasks from server
	// groupBy value will be array of columns in case of prop change
	const { columnId } = groupByValue;
	if (!allowGrouping({ groupBy, groupColumnName: columnId })) {
		return;
	}
	if (Array.isArray(groupByValue)) {
		updateState({
			groupBy: groupByValue
		});

		return;
	}

	if (updatedGroupBy.indexOf(columnId) === -1) {
		updatedGroupBy.splice(updatedGroupBy.indexOf(columnId), 1);
	} else {
		updatedGroupBy.push(columnId);
	}
	updateState({
		// we will focus on the config section header after grouping/ungrouping as the grid will be rebuilt
		selectedCell: {
			rowIndex: 0,
			columnId: CONFIG_SECTION_COLUMN_NAME,
			segment: PRIMARY
		}
	});
	if (notifyConsumer) {
		dispatch(SN_DATAGRID_ROW_GROUP_CHANGED, updatedGroupBy);
	}
};

const handleSortByColumn = ({
	action: {
		payload
	},
	dispatch,
	state,
	updateState
}) => {
	const { name, order, notifyConsumer } = payload;
	const { sortBy } = state;
	const sortOrder = order || getNextSortOrder(sortBy, name);

	if (notifyConsumer) {
		dispatch(SN_DATAGRID_SORT_CHANGED, {
			name,
			order: sortOrder
		});
	} else {
		updateState({
			sortBy: {
				name: name,
				order: sortOrder
			}
		});
	}
};

/** TODO: handle the filter property change */
const handleColumnFilter = ({
	action: {
		payload
	},
	dispatch,
	state,
	updateState
}) => {
	// placeholder for filter column
	// should be implemented with connected component implementaion
	const { columns } = state;
	const { column: filteredColumn, filter: { text, operator }, notifyConsumer } = payload;
	const trimmedText = text? text.trim(): '';
	let updatedColumnModels;

	if (!trimmedText) {
		if (!filteredColumn.filter) {
			updateState({
				...RESET_CONTEXT_MENU
			});

			return;
		}
		updatedColumnModels = updateColumns(columns, filteredColumn, { filter: null });
	} else {
		updatedColumnModels = updateColumns(columns, filteredColumn,
			{
				filter: {
					text: trimmedText,
					operator
				}
			});
	}

	const columnList = [
		...updatedColumnModels.left,
		...updatedColumnModels.center,
		...updatedColumnModels.right
	];

	const filterExpression = makeFilterExpression(columnList);

	updateState({
		columns: updatedColumnModels,
		...RESET_CONTEXT_MENU
	});
	if (notifyConsumer) {
		dispatch(SN_DATAGRID_FILTER_CHANGED, {
			filterExpression
		});
	}
};

export const gridServerActionHandlers = {
	[COLUMN_SORTED]: {
		effect: handleSortByColumn,
		stopPropagation: true
	},
	[GRID_COLUMN_FILTERED]: {
		effect: handleColumnFilter,
		stopPropagation: true
	},
	[GRID_GROUP_UNGROUP]: {
		effect: handleGroupUnGroup,
		stopPropagation: true
	},
	[GRID_TASK_EXPANDED]: {
		effect: handleExpandCollapse,
		stopPropagation: true
	},
	[GRID_ALL_TASKS_EXPANDED]: {
		effect: handleExpandCollapseAll,
		stopPropagation: true
	}
};
