/**
 *	Generates mark up for popover for addional assignees.
 * @param {Array} arr Array of Overflow List.
 */
const generateOverflowContent = (assignments) => {
	const content = [];
	for (let index = 0; index < assignments.length; index++) {
		const { displayValue } = assignments[index];

		if (index !== assignments.length - 1) {
			content.push(<div className="item">
				{`${displayValue}, `}
			</div>);
		} else {
			content.push(<div className="item">
				{ displayValue }
			</div>);
		}
	}

	return content;
};

export const view = (state) => {
	const { assignments } = state.properties;

	return <div className="overflow-popover">
		{generateOverflowContent(assignments)}
	</div>;
};
