import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-checkbox';
import '@servicenow/now-button';
import '@servicenow/now-button';

import { ALL, APPLY_LABEL, CHOICE_FILTER_SELECT_ALL, CHOICE_FILTER_SELECT_NONE, CLEAR_LABEL, FILTER_LABEL, FILTER_NONE, SELECT } from '../constants';

const renderSelection = () => {
	return <div className="selection">
		<span className="select">{SELECT}</span>
		<now-button-bare
			size="md"
			variant="secondary"
			label={ALL}
			append-to-payload={{
				type: CHOICE_FILTER_SELECT_ALL
			}}
			configAria={{
				'aria-label': SELECT
			}}
		/>
		<span className="vertical-separator">|</span>
		<now-button-bare
			size="md"
			variant="secondary"
			label={FILTER_NONE}
			append-to-payload={{
				type: CHOICE_FILTER_SELECT_NONE
			}}
			configAria={{
				'aria-label': FILTER_NONE
			}}
		/>
	</div>;
};

const renderItem = (value, displayValue, selected) => <now-checkbox
	className="checkbox"
	label={displayValue}
	name={displayValue}
	checked={selected}
	append-to-payload={
		{
			choiceValue: value
		}
	}
/>;

const renderChoiceListItems = (choices) => {
	const items = [];

	choices.forEach((value, key) => {
		const { displayValue, selected } = value;

		items.push(renderItem(key, displayValue, selected));
	});

	return items;
};

const renderChoiceFilterBox = (choices) => {
	return <div className="choicelist">
		<ul
			className="listbox"
			tab-index="0"
			aria-multiselectable="true"
			role="listbox">
			{ renderChoiceListItems(choices) }
		</ul>
	</div>;
};

const renderActionButtons = () => {
	return <div className="buttons">
		<now-button
			className="margin-right-sm clear-filter"
			label={CLEAR_LABEL}
			size="sm"
			append-to-payload={{
				filter: false
			}}
			variant="secondary"
			configAria={{
				'aria-label': CLEAR_LABEL
			}}
		/>
		<now-button
			label={APPLY_LABEL}
			size="sm"
			className="apply-filter"
			append-to-payload={{
				filter: true
			}}
			variant="primary"
			configAria={{
				'aria-label': APPLY_LABEL
			}}
		/>
	</div>;
};

const view = (state) => {
	const { choices } = state;

	return <Fragment>
		<span className="label">{FILTER_LABEL}</span>
		{ renderSelection() }
		<div className="divider top"></div>
		{ renderChoiceFilterBox(choices) }
		<div className="divider bottom"></div>
		{ renderActionButtons() }
	</Fragment>;
};

export default view;
