import { createColumnModel } from '../../columnModel';
import {
	GROUP_COLUMN
} from '../../../constants';

const doesGroupColumnExist = (columns) => {
	const { left = [] } = columns;

	return left[0] && left[0].columnDefinition.groupColumn;
};

export const addRemoveGroupColumn = (groupBy, columns) => {
	let updatedColumns = columns;
	if (groupBy.length > 0) {
		if (!doesGroupColumnExist(columns)) { // add group column
			const groupColumnModel = createColumnModel([GROUP_COLUMN])[0];

			updatedColumns = {
				...columns,
				left: [
					groupColumnModel,
					...columns.left
				]
			};
		}
	} else { // remove the group column from left section
		updatedColumns = {
			...columns,
			left: [
				...columns.left.slice(1)
			]
		};
	}

	return updatedColumns;
};
