import Column from './Column';
import {
	ASCENDING_ORDER,
	DESCENDING_ORDER
} from '../../constants';

export default class Number extends Column {
	renderer({ displayValue }) {
		return (
			<div className="sn-datagrid-content" style={{ textAlign: 'right' }}>
				{displayValue}
			</div>
		);
	}


	setValue(columnModel, task, value = 0) {
		task.setValue(`${columnModel.columnDefinition.name}`, {
			value: parseFloat(value),
			displayValue: String(value)
		});
	}

	getSortComparator(columnModel, sortOrder) {
		const sortComparator = (task1, task2) => {
			// we are avoiding `this` to make it easier for the consumer component to override these functions
			const task1Val = parseFloat(columnModel.getValue(columnModel, task1));
			const task2Val = parseFloat(columnModel.getValue(columnModel, task2));
			const task1ValAdjusted = task1Val || task1Val === 0 ? task1Val : -Infinity;
			const task2ValAdjusted = task2Val || task2Val === 0 ? task2Val : -Infinity;
			switch (sortOrder) {
				default:
				case ASCENDING_ORDER: return task1ValAdjusted < task2ValAdjusted
					? -1 : task1ValAdjusted > task2ValAdjusted ? 1 : 0;
				case DESCENDING_ORDER: return task1ValAdjusted < task2ValAdjusted
					? 1 : task1ValAdjusted > task2ValAdjusted ? -1 : 0;
			}
		};

		return sortComparator;
	}
}
