import { EDITOR_CLASSES, ROW_EDITED } from "../constants";
import { containsClassInPath } from "../../../core/helpers/elements";

export const editEventHandlers = [
	{
		events: ['click'],
		capture: true,
		effect({ state: { rowEditing }, dispatch, action }) {
			// comes out of editing mode when the user clicks anywhere else on the grid
			if (rowEditing && !containsClassInPath(action.payload.event, EDITOR_CLASSES)) {
				dispatch(`${rowEditing.actionPrefix}_${ROW_EDITED}`);
			}
		}
	}
];
