import '../sn-datagrid-client';
import '../sn-datagrid-server';
import { CLIENT_MODE } from '../constants';

const view = (state) => {
	const { properties } = state;
	const { mode, editSlotsRenderer } = properties.config;

	return mode === CLIENT_MODE
		? <sn-datagrid-client {...properties}>
			{ editSlotsRenderer && editSlotsRenderer() }
			<slot slot='toolbar-icons' name="toolbar-icons"/>
			<slot slot='empty-state' name="empty-state"/>
		</sn-datagrid-client>
		: <sn-datagrid-server {...properties}>
			<slot slot='toolbar-icons' name="toolbar-icons"/>
			<slot slot='empty-state' name="empty-state"/>
		</sn-datagrid-server>;
};

export default view;
