import { actionTypes } from '@servicenow/ui-core';
import { NOW_CHECKBOX_CHECKED_SET, BUTTON_BARE_CLICKED, CHOICE_FILTER_SELECT_ALL, CHOICE_FILTER_SELECT_NONE, BUTTON_CLICKED, IN, GRID_COLUMN_FILTERED } from '../constants';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const generateFilterMetadata = (choices) => {
	let selectedAll = true;
	const filterChoiceList = {
		operator: IN,
		selectedChoices: {}
	};

	choices.forEach((value, key) => {
		filterChoiceList.selectedChoices[key] = value;
		if (!value.selected) {
			selectedAll = value.selected
		}
	});

	return { filterChoiceList, selectedAll };
};

const changeSelectedAll = (choices, selected) => {
	const newChoices = new Map();

	choices.forEach((value, key) => {
		newChoices.set(key, { ...value, selected });
	});

	return newChoices;
};

const dispatchClearFilter = (dispatch, column, choices) => {
	dispatch(GRID_COLUMN_FILTERED,
		{ column, filter: { selectedChoices: {} }, choices: changeSelectedAll(choices, true) }
	);
}

const handleCheckBoxSet = ({ state: { choices }, action: { payload: { value, choiceValue } }, updateState }) => {
	choices.set(choiceValue, { ...choices.get(choiceValue), selected: value });

	updateState({ choices: new Map(choices) });
};

const handleButtonClick = ({ state: { choices, properties: { column } }, action, dispatch }) => {
	const { payload: { filter } } = action;

	if (!filter) {
		dispatchClearFilter(dispatch, column, choices);
		return;
	}

	const { filterChoiceList, selectedAll } = generateFilterMetadata(choices);

	if (selectedAll) {
		dispatchClearFilter(dispatch, column, choices);
		return;
	}

	dispatch(GRID_COLUMN_FILTERED, { choices, column, filter: filterChoiceList });
};

const handleButtonBareClicked = ({ state: { choices }, action: { payload: { type } }, updateState }) => {
	let updatedChoices;

	switch (type) {
		case CHOICE_FILTER_SELECT_ALL:
			updatedChoices = changeSelectedAll(choices, true);
			break;
		case CHOICE_FILTER_SELECT_NONE:
			updatedChoices = changeSelectedAll(choices, false);
			break;
		default:
			break;
	}

	updateState({ choices: updatedChoices });
};

const handleComponentBootstrapped = ({ state: { properties: { choices } }, updateState }) => {
	const newChoices = new Map();

	choices.forEach((value, key) => {
		newChoices.set(key, {
			...value
		});
	});

	updateState({
		choices: newChoices
	});
};

export const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: {
		effect: handleComponentBootstrapped,
		stopPropagation: true
	},
	[NOW_CHECKBOX_CHECKED_SET]: {
		effect: handleCheckBoxSet,
		stopPropagation: true
	},
	[BUTTON_CLICKED]: {
		effect: handleButtonClick,
		stopPropagation: true
	},
	[BUTTON_BARE_CLICKED]: {
		effect: handleButtonBareClicked,
		stopPropagation: true
	}
};
