import {
	ROW_EDIT_ENDED,
	CELL_EDITED,
	ROW_EDITED,
	TASK_UPDATED,
	EDIT_CELL_TABBED,
	DATE_PICKER_VALUE_CHANGED,
	DATE_PICKER_STAGED_VALUE_CHANGED,
	CHOICE_OR_REF_VALUE_CHANGED,
	COLUMN_CHOICE_FETCH_REQUESTED,
	COLUMN_CHOICE_FETCHED,
	COLUMN_CHOICE_FETCH_SUCCEEDED,
	COLUMN_CHOICE_FETCH_FAILED,
	CHOICE_LIST_FETCH_FAILED_MSG,
	ROW_EDIT_STARTED
} from '../constants';

export const getPrefixedActionNames = (actionPrefix = 'SN_DATAGRID_CONNECTED') => {
	const actions = {
		ROW_EDIT_STARTED: `${actionPrefix}_${ROW_EDIT_STARTED}`,
		ROW_EDIT_ENDED: `${actionPrefix}_${ROW_EDIT_ENDED}`,
		CELL_EDITED: `${actionPrefix}_${CELL_EDITED}`,
		ROW_EDITED: `${actionPrefix}_${ROW_EDITED}`,
		TASK_UPDATED: `${actionPrefix}_${TASK_UPDATED}`,
		EDIT_CELL_TABBED: `${actionPrefix}_${EDIT_CELL_TABBED}`,
		CHOICE_OR_REF_VALUE_CHANGED: `${actionPrefix}_${CHOICE_OR_REF_VALUE_CHANGED}`,
		COLUMN_CHOICE_FETCH_REQUESTED: `${actionPrefix}_${COLUMN_CHOICE_FETCH_REQUESTED}`,
		COLUMN_CHOICE_FETCHED: `${actionPrefix}_${COLUMN_CHOICE_FETCHED}`,
		COLUMN_CHOICE_FETCH_SUCCEEDED: `${actionPrefix}_${COLUMN_CHOICE_FETCH_SUCCEEDED}`,
		COLUMN_CHOICE_FETCH_FAILED: `${actionPrefix}_${COLUMN_CHOICE_FETCH_FAILED}`,
		CHOICE_LIST_FETCH_FAILED_MSG: `${actionPrefix}_${CHOICE_LIST_FETCH_FAILED_MSG}`,
		// We can't use prefixes with the date picker actions
		DATE_PICKER_VALUE_CHANGED,
		DATE_PICKER_STAGED_VALUE_CHANGED
	};

	return actions;
};
