import { Fragment } from '@seismic/snabbdom-renderer';
import { isComponentRTL } from '../core/helpers/gridStateParams';
import '@servicenow/now-tabs';
import '@servicenow/now-dropdown';
import { t } from 'sn-translate';
import {
	GROUP_ACTION_LABEL,
	UNGROUP_ACTION_LABEL,
	FREEZE_TO_LEFT,
	FREEZE_TO_RIGHT,
	UNFREEZE,
	GROUP_UNGROUP,
	AUTOFIT_COLUMN_LABEL,
	AUTOFIT_ALL_COLUMNS_LABEL,
	COLUMN_AUTOFITTED,
	ALL_COLUMN_AUTOFITTED,
	SECTION,
	PERSONALIZE_COLUMNS_LABEL,
	COLUMN_SETTINGS_DISPLAYED,
	FREEZE_COLUMN_LABEL,
	GROUP_LABEL,
	HIDE_COLUMN_LABEL,
	CONTEXT_MENU_COLUMN_HIDDEN,
	RESET_COLUMNS_LABEL,
	SN_DATAGRID_COLUMNS_RESET,
	CONTAINER_SELECTED
} from '../constants';


import {
	TAB_ITEMS,
	COL_PIN_DROPDOWN_CHANGED
} from './constants';

import { getVisibleCols, isColumnNonHidable } from '../core/helpers/sections';
import isEmpty from 'lodash/isEmpty';

const renderLayoutTabContent = ({
	showFreezeDropdown,
	dropdownItems,
	selectedDropdownItems,
	column,
	columns,
	parentColumn,
	emptySubCols,
	groupColumn,
	hideColumnOptionDisabled,
	resetColumns,
	dispatch
}) => {
	return <Fragment>
		{
			showFreezeDropdown
				? <Fragment>
					<div className="header-context-menu-dropdown">
						<span className="header-context-menu-label">{FREEZE_COLUMN_LABEL}</span>
						<now-dropdown
							bare
							items={dropdownItems}
							select="single"
							selected-items={selectedDropdownItems}
							size="md"
							variant="tertiary"
							disabled={column.section === SECTION.CENTER && columns.center.length <= 1}
							append-to-payload={{
								column,
								type: COL_PIN_DROPDOWN_CHANGED
							}}
							config-aria={{ 'aria-label': t('Select an option to freeze the column') }}
						></now-dropdown>
					</div>
					<div className="header-context-menu-divider header-context-menu-divider--secondary"></div>
				</Fragment>
				: <Fragment/>
		}
		{
			emptySubCols
				? <Fragment>
					<div className="header-context-menu-column-actions">
						<now-button-bare
							hide-padding
							className="autofit-column-action header-context-menu-column-action"
							size="md"
							variant="secondary"
							label={AUTOFIT_COLUMN_LABEL}
							append-to-payload={{
								type: COLUMN_AUTOFITTED,
								column,
								columns
							}}
							config-aria={{ 'aria-label': AUTOFIT_COLUMN_LABEL }}
						/>
						<now-button-bare
							hide-padding
							className="autofit-all-columns-action header-context-menu-column-action"
							size="md"
							variant="secondary"
							label={AUTOFIT_ALL_COLUMNS_LABEL}
							append-to-payload={{
								type: ALL_COLUMN_AUTOFITTED
							}}
							config-aria={{ 'aria-label': AUTOFIT_ALL_COLUMNS_LABEL }}
						/>
					</div>
					<div className="header-context-menu-divider header-context-menu-divider--secondary"></div>
				</Fragment>
				: <Fragment />
		}
		<div className="header-context-menu-column-actions">
			{
				!groupColumn
					? <Fragment>
						<now-button-bare
							hide-padding
							className="autofit-all-columns-action header-context-menu-column-action"
							size="md"
							variant="secondary"
							label={HIDE_COLUMN_LABEL}
							append-to-payload={{
								type: CONTEXT_MENU_COLUMN_HIDDEN,
								column,
								parentColumn
							}}
							disabled={hideColumnOptionDisabled}
							config-aria={{ 'aria-label': HIDE_COLUMN_LABEL }}
						/>
						<now-button-bare
							hide-padding
							className="personalize-columns-action header-context-menu-column-action"
							size="md"
							variant="secondary"
							label={PERSONALIZE_COLUMNS_LABEL}
							append-to-payload={{
								type: COLUMN_SETTINGS_DISPLAYED
							}}
							config-aria={{ 'aria-label': PERSONALIZE_COLUMNS_LABEL }}
						/>

					</Fragment>
					: <Fragment />
			}
			{
				resetColumns
					? <now-button-bare
						hide-padding
						className="personalize-columns-action header-context-menu-column-action"
						size="md"
						variant="secondary"
						label={RESET_COLUMNS_LABEL}
						config-aria={{ 'aria-label': RESET_COLUMNS_LABEL }}
						on-click={() => {
							dispatch(SN_DATAGRID_COLUMNS_RESET);
							dispatch(CONTAINER_SELECTED);
						}}
					/>
					: <Fragment />
			}
		</div>
	</Fragment>;
};

const renderFilterTabContent = ({
	column,
	groupBy,
	showGroupBy,
	showFilter
}) => {
	const label= groupBy.includes(column.columnId)
		? `${UNGROUP_ACTION_LABEL} ${column.columnDefinition.label}`
		: `${GROUP_ACTION_LABEL} ${column.columnDefinition.label}`;

	return <Fragment>
		{
			showGroupBy
				? <Fragment>
					<div className="header-context-menu-group">
						<span className="header-context-menu-label">{GROUP_LABEL}</span>
						<now-button-bare
							hide-padding
							className="group-column-action"
							size="md"
							title={label}
							variant="secondary"
							append-to-payload={{
								type: GROUP_UNGROUP,
								groupByValue: [column.columnId]
							}}
							config-aria={{ 'aria-label': GROUP_LABEL + column.columnId }}
						>
							<span className="header-context-menu-text-link"> {label} </span>
						</now-button-bare>
					</div>
					<div className="header-context-menu-divider header-context-menu-divider--secondary"></div>
				</Fragment>
				: <Fragment/>
		}
		{
			showFilter && !column.columnDefinition.isChoice
				? <sn-datagrid-filter className="header-context-menu-filter" column={column}/>
				: <Fragment />
		}
		{
			showFilter && column.columnDefinition.isChoice
				? <sn-datagrid-client-choice-filter
					column={column}
					choices={column.choices} >
				</sn-datagrid-client-choice-filter> : <Fragment />
		}
	</Fragment>;
};

const view = (state, { dispatch }) => {
	const { tab } = state;
	const {
		filter,
		rowGroup,
		freezeColumns,
		hasHierarchy,
		column,
		parentColumn,
		section,
		groupBy,
		columns,
		dir,
		resetColumns
	} = state.properties;

	if (isEmpty(column) || isEmpty(columns)) { // should not render anything for empty columns
		return <Fragment/>;
	}
	const emptySubCols = isEmpty(column.subColumns);
	const { LEFT, CENTER, RIGHT } = SECTION;
	const { columnDefinition: { groupColumn }, parent } = column;
	const dropdownItems = [
		{
			id: LEFT,
			label: isComponentRTL(dir) ? FREEZE_TO_RIGHT : FREEZE_TO_LEFT
		},
		{
			id: RIGHT,
			label: isComponentRTL(dir) ? FREEZE_TO_LEFT : FREEZE_TO_RIGHT
		},
		{
			id: CENTER,
			label: UNFREEZE
		}
	];
	const selectedDropdownItems = [];

	selectedDropdownItems.push(column.section);
	// show freeze column dropdown when freezing is enabled and is not a sub-columns and is not a group column
	const showFreezeDropdown = freezeColumns && !parent && !groupColumn;
	// show groupby when grid is not in hierarchy mode, grouping is enabled, column is groupable,
	// and column doesn't have subcolumns
	const showGroupBy = !hasHierarchy && rowGroup && column.columnDefinition.groupable && emptySubCols;
	// show filter when filter is enabled and column doesn't have subcolumns and is not a group column
	const showFilter = filter && emptySubCols && !groupColumn;
	const hideFilterTab = !showGroupBy && !showFilter;

	const visibleCols = parentColumn ? getVisibleCols(parentColumn.subColumns) : getVisibleCols(columns[CENTER]);
	const hideColumnOptionDisabled = isColumnNonHidable(column.hideable, parentColumn) ||
									section === CENTER && visibleCols.length === 1;

	return (
		<div
			on-click={(event => {
				event.stopImmediatePropagation();
			})}
			className="header-context-menu"
		>
			{
				hideFilterTab
					? <Fragment/>
					: <now-tabs
						items={TAB_ITEMS}
						fixedWidth
					></now-tabs>
			}
			{
				tab === TAB_ITEMS[0].id
					? renderLayoutTabContent({
						showFreezeDropdown,
						dropdownItems,
						selectedDropdownItems,
						column,
						columns,
						parentColumn,
						hideColumnOptionDisabled,
						emptySubCols,
						groupColumn,
						resetColumns,
						dispatch
					})
					: renderFilterTabContent({
						column,
						groupBy,
						showGroupBy,
						showFilter
					})
			}

		</div>
	);
};

export default view;
