import Column from './Column';
import '../../sn-datagrid-avatar-group';
import { MAX_AVATAR_DISPLAY_COUNT } from '../../constants';

/**
 * This is the default avatar group type column model
 * It is expected that the data will be an array of objects containing `value` and `displaValue` fields
 * with an optional imageSrc attribute
 */
export default class AvatarGroup extends Column {
	renderer({ column, task, overflowPopover }) {
		const columnName = column.columnDefinition.name;
		const taskData = task.taskData[columnName];

		return <div className="sn-datagrid-content">
			<sn-datagrid-avatar-group
				maxAssignmentCount={MAX_AVATAR_DISPLAY_COUNT}
				assignmentGroup={taskData}
				opened={overflowPopover === task.taskData.id}
				taskId={task.taskData.id} />
		</div>;
	}
}

