import { SCROLL_DIRECTION, OVERSCAN_COUNT } from '../../constants';

export const getVisibleHeight = element => {
	return parseFloat(
		window.getComputedStyle(element, null).getPropertyValue('height')
	);
};

export const getIndex = (height, rowHeight) => {
	return height / rowHeight;
};

export const getScrollWindow = ({ totalCount, scrollTop, rowHeight, visibleHeight, scrollDirection }) => {
	let start = Math.floor(getIndex(scrollTop, rowHeight));
	let end = Math.ceil(getIndex(visibleHeight + scrollTop, rowHeight));
	const visibleRowsCount = Math.ceil(visibleHeight / rowHeight);
	if (scrollDirection === SCROLL_DIRECTION.TOP) {
		end = start + visibleRowsCount + OVERSCAN_COUNT;
		end = end > totalCount ? totalCount : end;
	} else {
		start = end - visibleRowsCount - OVERSCAN_COUNT;
		start = start < 0 ? 0 : start;
	}

	return {
		start,
		end
	};
};

export const getVisibleTasks = ({
	tree: { tasks },
	scrollTop,
	scrollDirection,
	visibleHeight,
	config: {
		rowHeight,
		smartRendering = false
	}
}) => {
	if (smartRendering) {
		const indices = getScrollWindow({
			totalCount: tasks.length,
			scrollDirection,
			scrollTop,
			visibleHeight,
			rowHeight
		});

		return tasks.slice(indices.start, indices.end);
	}

	return tasks;
};
