import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './panel.scss';
import { CLOSE_LABEL, CLOSE, PERSONALIZE_LABEL } from '../constants';
import { actionHandlers } from './actions';
import { eventHandlers } from './events';
import rtlBehavior from '@servicenow/behavior-rtl';

const view = (state) => {
	const { panelTitle, hideHeader } = state.properties;

	return (
		<div className="sn-datagrid-config-panel"
			on-click={(event => {
				event.stopImmediatePropagation();
			})}>
			{!hideHeader
				? <div className="sn-datagrid-config-panel-header">
					<now-heading variant="header-tertiary" label={panelTitle} hasNoMargin/>
					<now-button-iconic
						className="close-btn"
						bare
						config-aria={{
							'aria-label': CLOSE_LABEL
						}}
						icon="close-fill"
						size="md"
						append-to-payload={{
							type: CLOSE
						}}
					/>
				</div>
				: <Fragment/>}
			<div className="sn-datagrid-config-panel-body">
				<slot name="sn-datagrid-panel-content"></slot>
			</div>
		</div>
	);
};

createCustomElement('sn-datagrid-config-panel', {
	renderer: {
		type: snabbdom,
		view
	},
	behaviors: [
		{ behavior: rtlBehavior }
	],
	properties: {
		hideHeader: {
			default: false
		},
		panelTitle: {
			default: PERSONALIZE_LABEL
		}
	},
	styles,
	actionHandlers: {
		...actionHandlers
	},
	eventHandlers: [
		...eventHandlers
	]
});
