import { CONFIG_CLIENT, ROW_EDIT_MODE } from '../../constants';

const contextMenuFlagResolver = (config, renderer) => {
	return Object.prototype.hasOwnProperty.call(config, renderer) && typeof config[renderer] === 'function' || config.readonly === false;
};
const rowContextMenuFlagResolver = (config) => {
	const renderer = 'rowContextMenuRenderer';
	const hasCustomOptions = Object.prototype.hasOwnProperty.call(config, renderer) &&
		typeof config[renderer] === 'function';
	const hasDefaultOptions = config.readonly === false && (config.editMode === ROW_EDIT_MODE || config.rowCopyPaste);

	return hasCustomOptions || hasDefaultOptions;
};
const transformConfig = config => {
	const updatedConfig = {
		...CONFIG_CLIENT,
		...config
	};

	return {
		...updatedConfig,
		setCellContextMenu: contextMenuFlagResolver(updatedConfig, 'cellContextMenuRenderer'),
		setRowContextMenu: rowContextMenuFlagResolver(updatedConfig),
		rowSelectionCheckbox: config.rowSelectionCheckbox && !config.readonly
	};
};

export default transformConfig;
