import { handleTabPress } from '../core/helpers/accessibilityHelpers';
import { ESC_KEY, CLOSE_SIDE_PANEL, TAB_KEY } from '../constants';

export const handleClickEvent = ({ dispatch }) => {
	dispatch(CLOSE_SIDE_PANEL);
};

export const handleKeydownEvent = ({
	action,
	dispatch
}) => {
	const { payload } = action;

	action.stopPropagation();
	if (payload) {
		const { event, host } = payload;
		switch (event.keyCode) {
			case ESC_KEY: dispatch(CLOSE_SIDE_PANEL);
				break;
			case TAB_KEY: handleTabPress({
				event,
				host
			});
		}
	}
};

