/**
 * validate the callback data internal use for aggregateBaseFunction and average
 * @param result
 * @param currentValue
 * @param callback
 * @returns {*}
 */
const getValidatedCallbackData = (result, currentValue, callback) => {
	return result || result === 0
		? currentValue || currentValue === 0
			? callback(result, currentValue)
			: result
		: currentValue;
};

/**
 *
 * @param task
 * @param aggregateConfig
 * @param callback
 * @returns {number}
 */

const aggregateBaseFunction = (task, aggregateConfig, callback) => {
	const { getter, columnModel } = aggregateConfig;

	return task.children.reduce((result, task) => {
		if (!task.visible) {
			return result;
		}
		const currentValue = getter(columnModel, task);

		return getValidatedCallbackData(result, currentValue, callback);
	}, undefined);
};
/**
 *
 * @param task
 * @param aggregateConfig
 * @returns {number}
 */
const sum = (task, aggregateConfig) => {
	const callback = (a, b) => {
		return parseFloat(a) + parseFloat(b);
	};

	return aggregateBaseFunction(task, aggregateConfig, callback);
};

/**
 *
 * @param task
 * @param aggregateConfig
 * @returns {number}
 */
const average = (task, aggregateConfig) => {
	const { getter, columnModel } = aggregateConfig;
	const callback = (a, b) => {
		return {
			value: parseFloat(a.value) + parseFloat(b.value) * b.taskCount,
			taskCount: a.taskCount + b.taskCount
		};
	};
	const totalSum = task.children.reduce((result, task) => {
		if (!task.visible) {
			return result;
		}
		const currentValue = getter(columnModel, task);
		const current = currentValue
			? { value: currentValue, taskCount: task._meta.leafNodeCount || 1 }
			: undefined;

		return getValidatedCallbackData(result, current, callback);
	}, { value: 0, taskCount: 0 });

	task._meta.leafNodeCount = totalSum.taskCount;

	return parseFloat((totalSum.value/totalSum.taskCount).toFixed(2));
};

/**
 *
 * @param task
 * @param aggregateConfig
 * @returns {number}
 */
const min = (task, aggregateConfig) => {
	return aggregateBaseFunction(task, aggregateConfig, Math.min);
};

/**
 *
 * @param task
 * @param aggregateConfig
 * @returns {number}
 */
const max = (task, aggregateConfig) => {
	return aggregateBaseFunction(task, aggregateConfig, Math.max);
};

/**
 * Default function
 * @returns {undefined}
 */
const defaultFunction = () => {
	return undefined;
};

export const aggregateFunctions = {
	SUM: sum,
	AVERAGE: average,
	MAX: max,
	MIN: min,
	DEFAULT: defaultFunction
};
