import { createRef } from '@servicenow/ui-renderer-snabbdom';
import { COLUMN_DRAG_ENDED, SECTION, COLUMN_CONFIG_ROW_HEIGHT } from '../../../constants';
import { isEmptySection } from '../../helpers/sections';
import isEmpty from 'lodash/isEmpty';
import throttle from 'lodash/throttle';
const scrollBodyContainer = throttle((bodyWrapperElem, top) => bodyWrapperElem.scrollTo({ top }), 50);

const handleOnDragging = throttle(({
	totalElements,
	bodyWrapperRef,
	draggableRowRef,
	dropTargetRef,
	rowHeight,
	getIndex,
	offsetY
}) => {
	const rowElem = draggableRowRef.current;
	const bodyWrapperElem = bodyWrapperRef.current;
	const dropTargetElem = dropTargetRef.current;

	const visibleHeight = bodyWrapperElem.clientHeight;
	const scrollTop = bodyWrapperElem.scrollTop;

	
	const isOutsideBody = offsetY < 0 || offsetY > totalElements * rowHeight;

	if (isOutsideBody) {
		return;
	}

	// update the dragged row and drop target position
	const index = getIndex(Math.round(offsetY / rowHeight));
	if (index < 0) {
		return;
	}

	/*
	 we have to stop moving the element beyond the layover boundary
	 hence dragged element should be within the last element itself
	 here we get the last row top position by reducing height equivalen to 2 elements
	 because totalElements are always 1 more than the real elements
	*/
	const lastElemTop = (totalElements - 2) * rowHeight;
	const rowElemPos = Math.min(offsetY - 0.5 * rowHeight, lastElemTop);

	rowElem.style.top = `${rowElemPos}px`;
	dropTargetElem.style.top = `${index * rowHeight}px`;
	const shouldScrollDown = offsetY > scrollTop + visibleHeight - rowHeight;
	const shouldScrollUp = offsetY < scrollTop + rowHeight;

	if (shouldScrollDown) {
		// scroll down when we are near to bottom of visible body
		scrollBodyContainer(bodyWrapperElem, scrollTop + rowHeight);
	} else if (shouldScrollUp) {
		// scroll up when we are near to top of visible body
		scrollBodyContainer(bodyWrapperElem, scrollTop - rowHeight);
	}
}, 15);

const handleOnDragEnd = ({
	rowHeight,
	dispatch,
	dropTargetRef,
	getTargetPayload
}) => {
	const dropTargetElem = dropTargetRef.current;
	const top = parseInt(dropTargetElem.style.top, 0);

	dispatch(COLUMN_DRAG_ENDED, getTargetPayload(top/ rowHeight));
};

const renderLayover = ({
	dispatch,
	bodyWrapperRef,
	rowHeight,
	draggedElementLabel,
	offsetY,
	totalElements,
	getTargetPayload,
	getIndex
}) => {
	const draggableRowRef = createRef();
	const dropTargetRef = createRef();
	const initialPosition = Math.round(offsetY/rowHeight);

	return (
		<div
			className='drag-drop-layover'
			on-mousemove={event => {
				const rect = event.currentTarget.getBoundingClientRect();
				const offsetY = event.clientY - rect.top;
				handleOnDragging({
					totalElements,
					dispatch,
					bodyWrapperRef,
					draggableRowRef,
					dropTargetRef,
					rowHeight,
					getIndex,
					offsetY
				});
			}}
			on-mouseup={() => {
				handleOnDragEnd({ dispatch, dropTargetRef, rowHeight, getTargetPayload });
			}}
			on-mouseleave={() => {
				dispatch(COLUMN_DRAG_ENDED, {}, {}, true);
			}}
		>
			<div
				className='draggable-row'
				ref={draggableRowRef}
				style={{
					height: `${rowHeight}px`,
					lineHeight: `${rowHeight}px`,
					top: `${initialPosition*rowHeight}px`
				}}
			>
				{ draggedElementLabel }
			</div>
			<div
				className='row-drop-target'
				ref={dropTargetRef}
				style={{ top: `${initialPosition*rowHeight}px` }}
			/>
		</div>
	);
};

/*
	returns an arrayMap which maps indices to an object which has the section and position in the section
	Some indices are invalid and map to an empty object. Eg: subcolumn indices, first section heading row
*/
const getIndexMaps = (columns, groupBy) => {
	const { LEFT, CENTER, RIGHT } = SECTION;
	const columnsExist = {
		[LEFT]: !isEmptySection(columns[LEFT], LEFT, groupBy),
		[CENTER]: !isEmptySection(columns[CENTER], CENTER),
		[RIGHT]: !isEmptySection(columns[RIGHT], RIGHT)
	};
	const validIndices = [];
	const trackColumns = (columns, section) => {
		columns.forEach((column, index) => {
			const { configExpanded, subColumns, columnDefinition: { groupColumn } } = column;
			// Group column isnt tracked here
			if (groupColumn) {
				return;
			}
			validIndices.push({ targetIndex: index, section });
			if (configExpanded) {
				subColumns.forEach(() => {
					validIndices.push({});
				});
			}
		});
		validIndices.push({ targetIndex: columns.length, section });
	};

	if (columnsExist[LEFT] || columnsExist[RIGHT]) {
		validIndices.push({});
	}
	Object.entries(columns).forEach(([section, columns]) => {
		if (columnsExist[section]) {
			trackColumns(columns, section);
		}
	});

	return {
		validIndices,
		totalElements: validIndices.length
	};
};

export const renderColumnLayover = ({ state, dispatch, bodyWrapperRef }) => {
	const { columnReorder: { column, offsetY }, columns, groupBy } = state;
	const { validIndices, totalElements } = getIndexMaps(columns, groupBy);
	const getTargetPayload = index => validIndices[index];
	const getIndex = index => isEmpty(validIndices[index]) ? -1 : index;

	return renderLayover({
		dispatch,
		bodyWrapperRef,
		rowHeight: COLUMN_CONFIG_ROW_HEIGHT,
		draggedElementLabel: column.columnDefinition.label,
		offsetY,
		totalElements,
		getIndex,
		getTargetPayload
	});
};


export const renderSubColumnLayover = ({ state, dispatch, bodyWrapperRef }) => {
	const { columnReorder: { column, parentColumn, offsetY } } = state;
	const getTargetPayload = index => {
		return { targetIndex: index };
	};
	const getIndex = index => index;

	/*
	 to handle the scenario of dropping element after last element
	 we keep the totalElements one more than the total elements
	 e.g. if we have 10 elements array, then available indices are 0-9
	 but for dropping after last element we send the target index as 10
	*/
	return renderLayover({
		dispatch,
		bodyWrapperRef,
		rowHeight: COLUMN_CONFIG_ROW_HEIGHT,
		draggedElementLabel: column.columnDefinition.label,
		offsetY,
		totalElements: parentColumn.subColumns.length + 1,
		getTargetPayload,
		getIndex
	});
};

