import { COLUMN_DRAG_EVENTS } from '../../../constants';

const updateColumnDragState = (updateState, state) => {
	updateState({
		columnDragState: state
	});
};

export const columnDragEventStartHandler = ({ updateState }) => {
	updateColumnDragState(updateState, COLUMN_DRAG_EVENTS.START);
};

export const columnDragEventEndHandler = ({ updateState }) => {
	updateColumnDragState(updateState, undefined);
};

export const columnDragEventOverHandler = ({ updateState }) => {
	updateColumnDragState(updateState, COLUMN_DRAG_EVENTS.OVER);
};

export const columnDragEventLeaveHandler = ({ updateState }) => {
	updateColumnDragState(updateState, COLUMN_DRAG_EVENTS.START);
};
