import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-pill';
import {
	GRID_GROUP_UNGROUP,
	UNGROUP_ALL,
	GROUP_BY_TITLE,
	GROUP_BY_PLACEHOLDER,
	COLUMN_DRAG_EVENTS,
	ENTER_KEY,
	CLEAR_LABEL,
	GROUP_PILL_CONTAINER_HEIGHT
} from '../../constants';
import { getColumnModelFromId } from '../columnModel';

const performColGrouping = ({
	event,
	dispatch,
	groupBy
}) => {
	event.preventDefault();
	const columnName = event.dataTransfer.getData('columnName');
	if (groupBy.includes(columnName)) {
		return; // can't group again on the same column
	}

	dispatch(GRID_GROUP_UNGROUP, { groupByValue: [columnName], notifyConsumer: true });
};

const renderGroupByPill = ({
	groupColName,
	columns,
	index,
	groupByLength,
	isRTL
}) => {
	const groupColModel = getColumnModelFromId(columns, groupColName);
	const { columnDefinition: { label } } = groupColModel;
	const icon = isRTL ? 'arrow-left-fill' : 'arrow-right-fill';

	return (
		<Fragment>
			<now-pill
				can-dismiss
				label={label}
				size="sm"
				append-to-payload={{
					groupByValue: [groupColName],
					notifyConsumer: true
				}}
				manageSelected="true"
			>
			</now-pill>
			{
				index < groupByLength - 1
					? <now-icon className="groupby-container-next-icon" icon={icon} size="sm"></now-icon>
					: <Fragment/>
			}
		</Fragment>
	);
};

const renderGroupByPills = ({
	groupBy,
	columns,
	isRTL
}) => {
	let groupbyContainerBody;
	if (groupBy.length > 0) {
		groupbyContainerBody = groupBy.map((groupColName, index) =>
			renderGroupByPill({
				groupColName,
				columns,
				index,
				groupByLength: groupBy.length,
				isRTL
			})
		);
	} else {
		groupbyContainerBody = <span className="groupby-container-placeholder">{GROUP_BY_PLACEHOLDER}</span>;
	}

	return (
		<Fragment>
			<span className="groupby-container-title">
				{ GROUP_BY_TITLE } :
			</span>
			{ groupbyContainerBody }
		</Fragment>
	);
};

const renderGroupByPillsContainer = ({
	state,
	groupBy,
	columns,
	dispatch,
	isRTL
}) => {
	const { columnDragState } = state;
	let dragStateClass = ' ';
	if (columnDragState === COLUMN_DRAG_EVENTS.OVER ||
		columnDragState === COLUMN_DRAG_EVENTS.START && groupBy.length > 0) {
		dragStateClass = ' groupby-container--drag-over';
	} else if (columnDragState === COLUMN_DRAG_EVENTS.START) {
		dragStateClass = ' groupby-container--drag-started';
	}

	return (
		<div
			className={`header groupby-container${dragStateClass}`}
			style={{
				height: `${GROUP_PILL_CONTAINER_HEIGHT}px`
			}}
			ondrop={(event) => performColGrouping({
				event,
				dispatch,
				groupBy
			})}
			ondragover={(event) => {
				event.preventDefault();
				dispatch(COLUMN_DRAG_EVENTS.OVER);
			}}
			ondragleave={() => {
				dispatch(COLUMN_DRAG_EVENTS.LEAVE);
			}}
		>
			{
				renderGroupByPills({
					groupBy,
					columns,
					isRTL
				})
			}
			<div
				className="groupby-container-buttons"
			>
				{
					groupBy.length > 0
						? <now-button-bare
							size="md"
							variant="secondary"
							configAria={{
								'aria-label': CLEAR_LABEL
							}}
							label={CLEAR_LABEL}
							on-click={(event) => event.stopPropagation()}
							on-keydown={(event) => {
								if (event.keyCode === ENTER_KEY) {
									event.stopPropagation();
								}
							}}
							append-to-payload={{
								type: UNGROUP_ALL
							}}
						></now-button-bare>
						: <Fragment/>
				}
			</div>
		</div>
	);
};

export default renderGroupByPillsContainer;
