import isEmpty from 'lodash/isEmpty';
import get from 'lodash/get';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { CELL_EDIT_MODE, DATA_TYPES, NOW_BUTTON } from '../../../constants';
import { ROW_EDIT_STARTED, NOW_INPUT_VALUE_CHANGED } from '../constants';
import { getPrefixedActionNames } from '../helpers/editingHelper';
import { columnChoiceQuery } from '../schemas/choicQuery';

const fetchColumnChoiceHandler = ({ action: { payload }, dispatch }) => {
	const {
		table,
		column,
		actionPrefix
	} = payload;
	const actions = getPrefixedActionNames(actionPrefix);

	dispatch(actions.COLUMN_CHOICE_FETCHED, {
		table,
		column
	}, {
		column,
		actions
	});
};

const fetchColumnChoiceSuccessHandler = ({
	action: { payload, meta: { column: columnName, actions } },
	state: { rowEditing, columnChoiceCache = {} },
	updateState,
	dispatch
}) => {
	try {
		const choices = get(payload, 'data.GlideChoiceColumn_Query.choices', []).map(choice => {
			return {
				value: choice.rawValue,
				displayValue: choice.displayValue
			};
		});
		const { columnsConfig } = rowEditing;
		const choiceColumnIndex = columnsConfig.findIndex(
			column => column.dbColumnName === columnName|| column.name === columnName
		);
		const newColumnConfig = {
			...columnsConfig[choiceColumnIndex],
			choices
		};

		updateState({
			dataUpdating: false,
			rowEditing: {
				...rowEditing,
				columnsConfig: [
					...columnsConfig.slice(0, choiceColumnIndex),
					newColumnConfig,
					...columnsConfig.slice(choiceColumnIndex + 1)
				]
			},
			columnChoiceCache: {
				...columnChoiceCache,
				[columnName]: choices
			}
		});
	} catch (err) {
		dispatch(actions.COLUMN_CHOICE_FETCH_FAILED, {
			error: err.stack
		});
	}
};

/**
 * getMaskedEditActionHandlers masks the ROW_EDIT_STARTED action
 * It is used in gantt to mask the 'SN_DATAGRID...' action and dispatch the `NOW_GANTT...` action
 * @param {String} maskPrefix
 */
export const getMaskedEditActionHandlers = (
	maskPrefix = 'SN_DATAGRID'
) => {
	const rowEditStartedAction = `${maskPrefix}#${ROW_EDIT_STARTED}`;
	/**
	 * getPrefixedEditActionHandlers returns the editing events prefixed by the actionPrefix
	 * This avoids the name collision in the action handlers of the connected component
	 * @param {String} actionPrefix
	 */
	const getPrefixedEditActionHandlers = (actionPrefix) => {
		const actions = getPrefixedActionNames(actionPrefix);

		return {
			/**
			 * To override this action, dispatch `${actionPrefix}_ROW_EDIT_STARTED` with the required payload
			 */
			[rowEditStartedAction]: {
				stopPropagation: true,
				effect({ action: { payload }, dispatch, properties: { table, tableName } }) {
					dispatch(actions.ROW_EDIT_STARTED, {
						...payload,
						table: table || tableName
					});
				}
			},
			/**
			 * Used to start the editing lifecycle, can be dispatched in the consumer component directly
			 *
			 * Required payload parameters
			 * 1) taskId: id of the task to be edited
			 * 2) editMode: cellEdit/rowEdit
			 * 3) table: table name, for reference and choice type fields
			 * 4) columnsConfig: array of objects for the columns to be edited for a task
			 *  1 item in cellEdit mode, more in rowEdit mode
			 * 	columnConfig Item format:
			 * 	{
			 * 		// Required
			 * 		name, // 'name' property of column as provided to UI component
					type, // 'type' property of column as provided to UI component
					isChoice, // boolean - mandatory true for choice type columns

					// To prepopulate form
					displayValue, // task's displayValue for the given column
					value, // task's value for the given column

					// Customization
					choices, // array of { value, displayValue } items for choice type columns, default: fetched from db
					dictionary, // dictionary prop for sn-record-reference-connected
			 *		dbColumnName // add this property if the db name of the field is different from the name property
			 *			for choice and reference type column
			 * 	}
			 *
			 * Optional parameters
			 * 5) populateDefaultValues: populates the initial edited state with the values from columnConfig items
			 * 		Useful in usecases where initial edited state has to start with default values
			 */
			[actions.ROW_EDIT_STARTED]: {
				stopPropagation: true,
				effect({ action: { payload = {} }, updateState, state, dispatch }) {
					if (!isEmpty(state.rowEditing)) { // row is already being edited so we will just return
						return;
					}
					let columnsConfig = payload.columnsConfig;
					const { columnChoiceCache = {} } = state;
					let isChoiceFetchRequested = false;
					//  fetching choice list for the choice columns

					columnsConfig = columnsConfig.map(colConfig => {
						const { isChoice, name, dbColumnName, choices } = colConfig;
						const columnName = dbColumnName || name;
						if (isChoice && isEmpty(choices)) {
							if (isEmpty(columnChoiceCache[columnName])) {
								dispatch(actions.COLUMN_CHOICE_FETCH_REQUESTED, {
									table: payload.table,
									column: columnName,
									actionPrefix
								});
								isChoiceFetchRequested = true;

								return colConfig;
							}

							return {
								...colConfig,
								choices: columnChoiceCache[columnName]
							};
						}

						return colConfig;
					});
					let editValues = {};
					const editDisplayValues = {};
					if (payload.populateDefaultValues) {
						editValues = columnsConfig.reduce((editValues, colConfig) => {
							const { name, type, isChoice, displayValue, value } = colConfig;
							if (isChoice || type === DATA_TYPES.REFERENCE_TYPE) {
								if (value) {
									editValues[name] = value;
									editDisplayValues[name] = displayValue;
								}
							} else if (type === DATA_TYPES.NUMBER_TYPE && value) {
								editValues[name] = value;
							} else if (displayValue) {
								editValues[name] = displayValue;
							}

							return editValues;
						}, {});
					}
					updateState({
						dataUpdating: isChoiceFetchRequested,
						rowEditing: {
							...payload,
							columnsConfig,
							editValues,
							editDisplayValues,
							actionPrefix
						}
					});
				}
			},
			[actions.ROW_EDIT_ENDED]: {
				stopPropagation: true,
				effect({ updateState }) {
					updateState({
						rowEditing: null,
						dataUpdating: false
					});
				}
			},
			[NOW_INPUT_VALUE_CHANGED]: {
				stopPropagation: true,
				effect({
					action: { payload = {} },
					updateState
				}) {
					updateState({
						path: 'rowEditing.editValues',
						value: { [payload.name]: payload.fieldValue },
						operation: 'merge',
						shouldRender: false
					});
				}
			},
			[actions.ROW_EDITED]: {
				stopPropagation: true,
				effect({ dispatch, state: { rowEditing: { editValues } } }) {
					if (isEmpty(editValues)) {
						dispatch(actions.ROW_EDIT_ENDED);
					} else {
						dispatch(actions.TASK_UPDATED, {
							...editValues
						});
					}
				}
			},
			[actions.DATE_PICKER_STAGED_VALUE_CHANGED]: {
				stopPropagation: true,
				effect({ action: { payload = {} }, updateState }) {
					updateState({
						path: 'rowEditing.editValues',
						value: { [payload.name]: payload.value },
						operation: 'merge',
						shouldRender: false
					});
				}
			},
			[actions.DATE_PICKER_VALUE_CHANGED]: {
				stopPropagation: true,
				effect({ dispatch, state: { rowEditing } }) {
					if (rowEditing.editMode === CELL_EDIT_MODE && !isEmpty(rowEditing.editValues)) {
						dispatch(actions.ROW_EDITED);
					}
				}
			},
			[actions.CHOICE_OR_REF_VALUE_CHANGED]: {
				stopPropagation: true,
				effect({ action: { payload }, state: { rowEditing }, dispatch, updateState }) {
					if (payload && payload.name) {
						const { name, value, displayValue } = payload;

						updateState({
							path: 'rowEditing.editValues',
							value: { [name]: value },
							operation: 'merge',
							shouldRender: false
						});
						updateState({
							path: 'rowEditing.editDisplayValues',
							value: { [name]: displayValue },
							operation: 'merge',
							shouldRender: false
						});
						if (rowEditing.editMode === CELL_EDIT_MODE && value!== null && value !== undefined) {
							dispatch(actions.ROW_EDITED);
						}
					}
				}
			},
			[actions.COLUMN_CHOICE_FETCH_REQUESTED]: {
				stopPropagation: true,
				effect: fetchColumnChoiceHandler
			},
			[actions.COLUMN_CHOICE_FETCHED]: {
				...createGraphQLEffect(columnChoiceQuery, {
					variableList: ['table', 'column'],
					successActionType: actions.COLUMN_CHOICE_FETCH_SUCCEEDED,
					errorActionType: actions.COLUMN_CHOICE_FETCH_FAILED
				}),
				stopPropagation: true
			},
			[actions.COLUMN_CHOICE_FETCH_SUCCEEDED]: {
				stopPropagation: true,
				effect: fetchColumnChoiceSuccessHandler
			},
			[actions.COLUMN_CHOICE_FETCH_FAILED]: {
				stopPropagation: true,
				effect({ action: { payload }, updateState }) {
				// eslint-disable-next-line no-console
					console.log(actions.CHOICE_LIST_FETCH_FAILED_MSG, payload);
					updateState({
						dataUpdating: false
					});
				}
			},
			[actions.EDIT_CELL_TABBED]: {
				stopPropagation: true,
				effect({ // ends the editing mode when a user tabs out
					state: { rowEditing: { columnsConfig } },
					dispatch,
					action: { payload: { name, event } }
				}) {
					let shouldEndEditing = false;
					const { DATE_TYPE, DATE_TIME_TYPE } = DATA_TYPES;
					const isEditingColumnHasIcon = [DATE_TYPE, DATE_TIME_TYPE].indexOf(columnsConfig[0].type) > -1;
					const path = event.path || (event.composedPath && event.composedPath());
					if (isEditingColumnHasIcon && path.length > 0) {
						const isIconFocused = path[0].className.indexOf(NOW_BUTTON) > -1;
						if (isIconFocused) {
							shouldEndEditing = true;
						}
					} else {
						const isLastColumn = columnsConfig[columnsConfig.length - 1].name === name;
						const isFirstColumn = columnsConfig[0].name === name;
						const tabbingOutOfFirstColumn = isFirstColumn && event.shiftKey;
						const tabbingOutOfLastColumn = isLastColumn && !event.shiftKey;
						shouldEndEditing = tabbingOutOfFirstColumn || tabbingOutOfLastColumn;
					}

					if (shouldEndEditing) {
						dispatch(actions.ROW_EDITED);
					}
				}
			}
		};
	};

	return getPrefixedEditActionHandlers;
};

