import renderCell from './cell';
import { createRef, Fragment } from '@servicenow/ui-renderer-snabbdom';
import {
	getColumnsWidth,
	getColumnWidth,
	getVisibleCols,
	getVisibleSubCols
} from '../../helpers/sections';
import { COLUMN_RESIZER_WIDTH,
	COLUMN_AUTOFITTED } from '../../../constants';
import { resizeColumn } from '../../helpers/resizer';
import last from 'lodash/last';

const renderMainCol = ({
	state,
	columns,
	column,
	dispatch,
	hasSubCols,
	reorderColumns,
	sort,
	refs,
	section,
	isRTL
}) => {
	const { displaySubColumnRow } = state;
	const className = `header-main-col-container${displaySubColumnRow ? ' header-col-parent' : ''}`;

	return (
		<div className={className} >
			{displaySubColumnRow
				? hasSubCols
					? renderCell({
						state,
						columns,
						column,
						dispatch,
						columnWidth: getColumnsWidth([], column),
						reorderColumns,
						sort,
						refs,
						section,
						isRTL
					})
					: <Fragment/>
				: renderCell({
					state,
					columns,
					column,
					dispatch,
					columnWidth: getColumnWidth(column),
					reorderColumns,
					sort,
					refs,
					section,
					isRTL
				})
			}
		</div>
	);
};

const renderSubCols = ({
	state,
	columns,
	column,
	dispatch,
	hasSubCols,
	visibleSubCols,
	reorderColumns,
	sort,
	refs,
	section,
	isRTL
}) => {
	const lastVisibleSubCol = last(visibleSubCols);

	return (
		<div className="header-sub-col-container" >
			{ hasSubCols
				? visibleSubCols.map(subCol => {
					return renderCell({
						state,
						columns,
						column: subCol,
						dispatch,
						parentColumn: column,
						columnWidth: getColumnWidth(subCol),
						reorderColumns,
						sort,
						refs,
						section,
						isRTL,
						shouldCreateResizer: subCol !== lastVisibleSubCol
					});
				})
				: renderCell({
					state,
					columns,
					column,
					dispatch,
					columnWidth: getColumnWidth(column),
					reorderColumns,
					sort,
					refs,
					section,
					isRTL
				})
			}
		</div>
	);
};

const renderRow = ({
	state,
	columns,
	dispatch,
	refs,
	reorderColumns,
	sort,
	section,
	isRTL
}) => {
	const visbileCols = getVisibleCols(columns);
	const { displaySubColumnRow } = state;

	return visbileCols.map(column => {
		const visibleSubCols = getVisibleSubCols(column);
		const hasSubCols = column.subColumns.length > 0;
		const columnRef = createRef();
		const containerRef = createRef();

		return <Fragment>
			<div
				className="header-col-container"
				ref={containerRef}
			>
				{renderMainCol({
					state,
					columns,
					column,
					dispatch,
					hasSubCols,
					reorderColumns,
					sort,
					refs: {
						...refs,
						columnRef
					},
					section,
					isRTL
				})}
				{ displaySubColumnRow
					? renderSubCols({
						state,
						columns,
						column,
						dispatch,
						hasSubCols,
						visibleSubCols,
						reorderColumns,
						sort,
						refs: {
							...refs,
							parentColumnRef: columnRef
						},
						isRTL,
						section
					})
					: <Fragment/>
				}
			</div>
			{
				<div
					style={{ minWidth: `${COLUMN_RESIZER_WIDTH}px` }}
					className="column-resizer-container"
				>
					<div
						className="column-resizer"
						ondblclick={() => {
							dispatch(COLUMN_AUTOFITTED, {
								column: hasSubCols ? last(visibleSubCols) : column,
								columns: state.columns
							});
						}}
						on-mousedown={() => {
							const { left, right } = containerRef.current.getBoundingClientRect();
							const lastCol = hasSubCols ? last(visibleSubCols) : column;
							const lastColWidth = lastCol.autoWidth || lastCol.width;

							// gridCellHeaderStart is equal to start of main column in case of no sub columns
							// or is equal to start of last sub column of main column in case of sub columns
							const gridCellHeaderStart = isRTL ? left + lastColWidth : right - lastColWidth;

							// it will resize the main column in absence of sub columns
							// if sub columns are present then last sub column will be resized
							resizeColumn({
								column: hasSubCols ? last(visibleSubCols) : column,
								dispatch,
								gridCellHeaderStart,
								isRTL
							});
						}}
					>
					</div>
				</div>
			}
		</Fragment>;
	});
};

export default renderRow;
