import {
	SN_DATAGRID_ROW_ORDER_CHANGED,
	RESET_CONTEXT_MENU
} from '../../../constants';
import { getSelectedCellFromTasksToReorder } from '../../helpers/tasks';

export const handleRowDragStart = ({ tasksToReorder, draggedTaskPos, updateState }) => {
	updateState({
		tasksToReorder,
		draggedTaskPos,
		isReorderingRows: true,
		selectedCell: {},
		...RESET_CONTEXT_MENU
	});
};

export const handleRowDragEnd = ({ state, updateState, dispatch, targetIndex, parentSourceIndex, error }) => {
	const updatedParams = {
		isReorderingRows: false,
		draggedTaskPos: null
	};

	const { tasksToReorder, tree } = state;
	const { tasks } = tree;
	// error is set to true when button to drag row is clicked instead of drag (mousedown) or if esc is pressed
	if (error) {
		updateState({
			...updatedParams,
			tasksToReorder: null,
			selectedCell: getSelectedCellFromTasksToReorder({
				tasksToReorder,
				tree
			})
		});

		return;
	}
	updateState(updatedParams);
	const sourceTasks = tasksToReorder.map(task => task.taskData);
	const taskBeforeTargetIndex = targetIndex === 0 ? null : tasks[targetIndex-1].taskData;
	const parentTask = tasks[parentSourceIndex].taskData.parent;

	dispatch(SN_DATAGRID_ROW_ORDER_CHANGED, { sourceTasks, taskBeforeTargetIndex, targetIndex, parentTask });
};
