import { createRef } from '@servicenow/ui-renderer-snabbdom';
import {
	ROW_DRAG_ENDED,
	ROWS_SELECTED,
	SECTION,
	ARROW_DOWN_KEY,
	ARROW_UP_KEY,
	ESC_KEY,
	ENTER_KEY
} from '../../../constants';
import { getColumnModelFromId } from '../../columnModel';
import throttle from 'lodash/throttle';
const scrollBodyContainer = throttle((bodyWrapperElem, top) => bodyWrapperElem.scrollTo({ top }), 100);
const handleOnDragging = throttle(({
	offsetY,
	isKeyEvent,
	state,
	bodyWrapperRef,
	draggableRowRef,
	dropTargetRef
}) => {
	const { visibleHeight, config, tree } = state;
	const { rowHeight } = config;
	const rowElem = draggableRowRef.current;
	const bodyWrapperElem = bodyWrapperRef.current;
	const scrollTop = bodyWrapperElem.scrollTop;
	const dropTargetElem = dropTargetRef.current;
	const totalElemsHeight = tree.tasks.length * rowHeight;

	let isOutsideBody = offsetY < 0 || offsetY > totalElemsHeight;
	// in case of mouse event, bring element to center
	let offset = 0.5 * rowHeight;
	if (isKeyEvent) {
		isOutsideBody = offsetY < 0 || offsetY > totalElemsHeight;
		offset = offsetY === totalElemsHeight
			? rowHeight
			: 0;
	}

	if (isOutsideBody) {
		return;
	}

	const lastElemTop = totalElemsHeight - rowHeight;
	const rowElemPos = Math.min(offsetY - offset, lastElemTop);

	// update the dragged row and drop target position
	rowElem.style.top = `${rowElemPos}px`;
	dropTargetElem.style.top = `${Math.round(offsetY / rowHeight) * rowHeight}px`;
	const shouldScrollDown = offsetY > scrollTop + visibleHeight - rowHeight;
	const shouldScrollUp = offsetY < scrollTop + rowHeight;

	if (shouldScrollDown) {
		// scroll down when we are near to bottom of visible body
		scrollBodyContainer(bodyWrapperElem, scrollTop + rowHeight);
	} else if (shouldScrollUp) {
		// scroll up when we are near to top of visible body
		scrollBodyContainer(bodyWrapperElem, scrollTop - rowHeight);
	}
}, 15);

const handleOnDragEnd = ({ offsetY, state, dispatch }) => {
	const { config: { rowHeight }, tree } = state;
	const index = offsetY / rowHeight;
	let parentSourceIndex = Math.floor(index);
	const targetIndex = Math.round(index);

	// To handle special case where parentSourceIndex goes out of bounds in ROW_DRAG_ENDED action handler
	if (parentSourceIndex === tree.tasks.length) {
		parentSourceIndex--;
	}

	dispatch(ROW_DRAG_ENDED, { targetIndex, parentSourceIndex });
};

const handleMouseMove = (params = {}) => {
	const { event } = params;
	const rect = event.currentTarget.getBoundingClientRect();
	const offsetY = event.clientY - rect.top;

	handleOnDragging({
		offsetY,
		...params
	});
};

const handleMouseUp = (params = {}) => {
	const { event } = params;
	const rect = event.currentTarget.getBoundingClientRect();
	const offsetY = event.clientY - rect.top;

	handleOnDragEnd({
		offsetY,
		...params
	});
};

const handleKeyPress = (params = {}) => {
	const { event, state, dispatch, dropTargetRef } = params;
	const { rowHeight } = state.config;
	const top = parseInt(dropTargetRef.current.style.top);
	let offsetY = top;

	switch (event.keyCode) {
		case ARROW_DOWN_KEY:
			offsetY = offsetY + rowHeight;
			handleOnDragging({
				offsetY,
				isKeyEvent: true,
				...params
			});
			break;
		case ARROW_UP_KEY:
			offsetY = offsetY - rowHeight;
			handleOnDragging({
				offsetY,
				isKeyEvent: true,
				...params
			});
			break;
		case ESC_KEY:
			dispatch(ROW_DRAG_ENDED, {}, {}, true);
			break;
		case ENTER_KEY:
			handleOnDragEnd({
				offsetY,
				state,
				dispatch
			});
			break;
		default:
			break;
	}
};

const renderLayover = ({ state, dispatch, bodyWrapperRef }) => {
	const { LEFT, CENTER } = SECTION;
	const { config, tasksToReorder, draggedTaskPos, columns } = state;
	const { rowHeight, reorderRows } = config;
	const { labelSourceColumn } = reorderRows;
	const draggableRowRef = createRef();
	const dropTargetRef = createRef();
	const tasksLen = tasksToReorder.length;

	// we show label for the reorderRows.label column if defined
	// else we show the label for first column of first section
	const firstSectionColumns = columns[LEFT].length > 0 ? columns[LEFT] : columns[CENTER];
	const firstColumn = firstSectionColumns[0];
	const firstLeafColumn = firstColumn.subColumns.length > 0
		? firstColumn.subColumns[0]
		: firstColumn;
	const leafColumnName = firstLeafColumn.columnDefinition.name;
	const column = getColumnModelFromId(columns, labelSourceColumn) || getColumnModelFromId(columns, leafColumnName);

	const draggedRowLabel = tasksLen === 1
		? column.getDisplayValue(column, tasksToReorder[0])
		: `${tasksLen} ${ROWS_SELECTED}`;

	return (
		<div
			className='drag-drop-layover'
			on-mousemove={event => {
				handleMouseMove({
					event,
					state,
					dispatch,
					bodyWrapperRef,
					draggableRowRef,
					dropTargetRef
				});
			}}
			on-mouseup={event => {
				handleMouseUp({ event, state, dispatch });
			}}
			on-mouseleave={() => {
				dispatch(ROW_DRAG_ENDED, {}, {}, true);
			}}
			on-keydown={event => {
				event.preventDefault();
				handleKeyPress({
					event,
					state,
					dispatch,
					bodyWrapperRef,
					draggableRowRef,
					dropTargetRef
				});
			}}
			tabindex="0"
		>
			<div
				className='draggable-row'
				ref={draggableRowRef}
				style={{
					height: `${rowHeight}px`,
					lineHeight: `${rowHeight}px`,
					top: `${draggedTaskPos}px`
				}}
			>
				{ draggedRowLabel }
			</div>
			<div
				className='row-drop-target'
				ref={dropTargetRef}
				style={{ top: `${draggedTaskPos}px` }}
			/>
		</div>
	);
};

export default renderLayover;
