import '@servicenow/now-button';
import {
	APPLY_LABEL,
	CLEAR_LABEL,
	FILTER_UPDATED,
	COLUMN_FILTERED,
	GRID_COLUMN_FILTERED,
	LIKE,
	ENTER_KEY,
	FILTER_LABEL
} from '../constants';
import get from 'lodash/get';
import '../sn-datagrid-client-choice-filter';

const filterKeyUp = (event, dispatch)=> {
	const currentValue = event.target.value;

	if (event.keyCode !== ENTER_KEY) {
		dispatch(FILTER_UPDATED, {
			text: currentValue,
			operator: LIKE
		});
	}
};

const filterColumn = (column, dispatch, currentValue)=> {
	dispatch(GRID_COLUMN_FILTERED, {
		column: column,
		filter: {
			text: currentValue,
			operator: LIKE
		}
	});
};

const view = (state, dispatch) => {
	const { filter, properties: { column } } = state;

	return (
		<div className="filter">
			<form on-submit={(e) => {
				e.preventDefault();
				filterColumn(column, dispatch, filter.text);
			}}>
				<span className="filter-text">{FILTER_LABEL}</span>
				<input
					className="filter-input"
					type="text"
					value={get(filter, 'text', '')}
					aria-label='FILTER'
					on-keyup={event => filterKeyUp(event, dispatch)} />
				<div className="filter-buttons">
					<now-button
						className="margin-right-sm clear-filter"
						label={CLEAR_LABEL}
						size="sm"
						variant="secondary"
						append-to-payload={{
							type: COLUMN_FILTERED,
							column,
							filter: {}
						}}
						configAria={{
							'aria-label': CLEAR_LABEL
						}}
					/>
					<now-button
						label={APPLY_LABEL}
						size="sm"
						className="apply-filter"
						append-to-payload={{
							type: COLUMN_FILTERED,
							column,
							filter
						}}
						variant="primary"
						configAria={{
							'aria-label': APPLY_LABEL
						}}
					/>
				</div>
			</form>
		</div>
	);
};

export default view;
