import { DEFAULT_TAB_MAP, RESET_CONTEXT_MENU } from '../../../constants';

export const handlePanelToggle = ({
	state: { sidePanel },
	updateState,
	action: { payload }
}) => {
	let updateObj = {};
	if (payload && payload.type) {
		let selectedPanel = '';
		let mode = '';
		let display = !sidePanel.display;
		if (sidePanel.display === true && sidePanel.selectedPanel !== payload.type) {
			display = true;
		}
		selectedPanel = payload.type;
		mode = DEFAULT_TAB_MAP[payload.type];
		updateObj = {
			...RESET_CONTEXT_MENU,
			sidePanel: {
				selectedPanel,
				mode,
				display
			},
			selectedCell: {
				selectedContextMenu: payload.contextMenuId
			}
		};
	} else {
		updateObj = {
			...RESET_CONTEXT_MENU
		};
	}
	updateState(updateObj);
};

export const handleClosePanel = ({
	updateState
}) => {
	const updateObj = {
		...RESET_CONTEXT_MENU
	};

	updateState(updateObj);
};
