import {
	NO_SORT,
	SN_DATAGRID_SORT_CHANGED,
	SN_DATAGRID_ROW_GROUP_CHANGED,
	ASCENDING_ORDER,
	GROUP_COLUMN,
	RESET_CONTEXT_MENU,
	GRID_COLUMN_AUTOFITTED,
	CONFIG_SECTION_COLUMN_NAME,
	PRIMARY,
	SCROLL_DIRECTION,
	COLUMN_SORTED,
	SN_DATAGRID_FILTER_CHANGED,
	SN_DATAGRID_ALL_ROWS_EXPANDED,
	SN_DATAGRID_ALL_ROWS_COLLAPSED,
	SN_DATAGRID_ROW_EXPAND_SET,
	GRID_ALL_TASKS_EXPANDED
} from '../../../constants';
import { getNextSortOrder } from './grid';
import { addRemoveGroupColumn } from './actionHelpers';
import { countSelectedTasks } from '../../helpers/tasks';
import { updateColumns, getAggregationConfigs } from '../../columnModel';
import measurePerformance from '../../../util/performanceLogger';
import { allowGrouping } from '../../helpers/gridStateParams';
import isEmpty from 'lodash/isEmpty';
import { getWhiteSpaceAdjustedColumns, getCurrentGridWidth } from '../../helpers/sections';
import { getTreeUtils } from '../../store/tree/IndexTree';
import { makeFilterExpression } from '../../store/tree/filterEvaluator';

const getSortedTasksAndSortBy = ({
	sortByColumnId,
	sortOrder,
	columns,
	tree
}) => {
	const treeUtils = getTreeUtils(tree);

	treeUtils.sort({
		sortByColumnId,
		sortOrder,
		columns
	});

	return {
		tree: {
			...tree
		},
		sortBy: {
			name: sortByColumnId,
			order: sortOrder
		}
	};
};

export const handleSortByColumn = ({
	action: { payload },
	state,
	updateState,
	dispatch
}) => {
	const {
		columns,
		sortBy,
		config: { defaultSortBy },
		tree
	} = state;
	const { name, order, notifyConsumer = true } = payload;
	let sortOrder = order || getNextSortOrder(sortBy, name);
	let sortByColumnId = name;

	if (sortOrder === NO_SORT) {
		if (name === defaultSortBy.name) {
			sortOrder = ASCENDING_ORDER;
		} else {
			sortByColumnId = defaultSortBy.name || null;
			sortOrder = defaultSortBy.order || sortOrder;
		}
	}


	updateState({
		...getSortedTasksAndSortBy({
			sortByColumnId,
			sortOrder,
			columns,
			tree
		})
	});

	const newSortBy = {
		name: sortByColumnId,
		order: sortOrder
	};

	if (notifyConsumer) {
		dispatch(SN_DATAGRID_SORT_CHANGED, newSortBy);
	}
};

export const handleColumnFilter = ({
	state,
	updateState,
	action: { payload },
	dispatch
}) => {
	const aggregateConfig = getAggregationConfigs(state.columns);
	const {
		columns,
		config: {
			rowHeight
		},
		tree,
		choiceListValueMaps
	} = state;
	const {
		column: filteredColumn,
		filter,
		notifyConsumer = true
	} = payload;

	if (!filter) {
		updateState({
			...RESET_CONTEXT_MENU
		});

		return;
	}

	const treeUtils = getTreeUtils(tree);
	let updatedColumnModels;

	if (filter.text && filter.text.trim().length !== 0) {
		const { text, operator } = filter;
		const trimmedText = text ? text.trim() : '';

		updatedColumnModels = updateColumns(columns, filteredColumn, {
			filter: {
				text: trimmedText,
				operator
			}
		});
	} else if (filter.hasOwnProperty('selectedChoices') && Object.keys(filter.selectedChoices).length !== 0) {
		const { choices } = payload;
		const { selectedChoices, operator } = filter;

		updatedColumnModels = updateColumns(columns, filteredColumn, {
			choices,
			filter: {
				selectedChoices,
				operator
			}
		});
	} else {
		const updatedProps = { filter: null };

		if (filter.hasOwnProperty('selectedChoices')) {
			updatedProps.choices = payload.choices;
		}

		updatedColumnModels = updateColumns(columns, filteredColumn, updatedProps);
	}

	const columnList = [
		...updatedColumnModels.left,
		...updatedColumnModels.center,
		...updatedColumnModels.right
	];

	const filterExpression = makeFilterExpression(columnList);
	const measureFilterPerformance = measurePerformance(treeUtils.filter, tree);

	if (filter.hasOwnProperty('selectedChoices') && Object.keys(filter.selectedChoices).length !== 0) {
		choiceListValueMaps[filteredColumn.columnId] = {
			selectedChoices: filter.selectedChoices,
			operator: filter.operator
		}
	}

	measureFilterPerformance(filterExpression,
		aggregateConfig,
		updatedColumnModels,
		true,
		choiceListValueMaps
	);

	updateState({
		tree: {
			...tree
		},
		columns: updatedColumnModels,
		...RESET_CONTEXT_MENU,
		scrollHeight: tree.tasks.length * rowHeight,
		selectedTasksCount: countSelectedTasks(tree.tasks),
		choiceListValueMaps
	});
	if (notifyConsumer) {
		dispatch(SN_DATAGRID_FILTER_CHANGED, {
			filterExpression
		});
	}
};

export const handleGroupUnGroup = ({
	action: {
		payload
	},
	state,
	dispatch,
	updateState
}) => {
	const { groupByValue = [], notifyConsumer = true } = payload;
	const {
		config: { rowHeight, defaultSortBy = {} },
		columns,
		groupBy,
		selectedTasksCount,
		bodySectionKeySuffix,
		sortBy,
		sectionsWidth,
		tree,
		choiceListValueMaps
	} = state;
	let updatedGroupBy = [];
	const treeUtils = getTreeUtils(tree);
	const allTasksSelected = selectedTasksCount === tree.tasks.length;

	if (groupByValue.length === 0) { // clearing all the groups
		treeUtils.removeAllGroups(columns);
	} else if (groupByValue.length > 0 && !notifyConsumer) {
		/**
		 * 	notifyConsumer will be false when groupByValue needs to be reapplied because of a prop change
		 *  so clearing previous groups and grouping again for prop change
		*/
		treeUtils.cleanNodes();
		updatedGroupBy = [...groupByValue];
		groupByValue.forEach(columnId => {
			const measureGroupByPerformance = measurePerformance(treeUtils.groupWithColumnId, tree);

			measureGroupByPerformance(columnId, columns, choiceListValueMaps);
		});
	} else if (groupByValue.length === 1) {
		const columnId = groupByValue[0];
		if (treeUtils.getGroupByLevel(columnId) >= 0) { // already grouped so clearing the grouping
			const measureRemoveGroupByPerformance = measurePerformance(treeUtils.removeGroup, tree);

			measureRemoveGroupByPerformance(columnId, Array.from(groupBy), columns, choiceListValueMaps);
			const index = groupBy.indexOf(columnId);

			updatedGroupBy = [
				...groupBy.slice(0, index),
				...groupBy.slice(index + 1)
			];
		} else { // grouping on a single column
			if (!allowGrouping({ groupBy, groupColumnName: columnId })) {
				return;
			}
			const measureGroupByPerformance = measurePerformance(treeUtils.groupWithColumnId, tree);

			measureGroupByPerformance(columnId, columns, choiceListValueMaps);
			updatedGroupBy = [
				...groupBy,
				columnId
			];
		}
	}

	let updatedColumns = addRemoveGroupColumn(updatedGroupBy, columns);
	const newbodySectionKeySuffix = !bodySectionKeySuffix;
	// we toggle bodySectionKeySuffix on every group action to rebuild the header and body
	// so that they will remain in sync


	if (allTasksSelected) { // maintaining the previous selection
		treeUtils.updateTasks({
			selected: allTasksSelected
		});
	}

	if (isEmpty(updatedGroupBy)) {
		// grid is ungrouping so we have to fallback to the previous sort
		if (sortBy.name === GROUP_COLUMN.name) {
			dispatch(COLUMN_SORTED, {
				name: defaultSortBy.name,
				order: defaultSortBy.order,
				notifyConsumer: true
			});
		}
	} else {
		dispatch(COLUMN_SORTED, {
			name: GROUP_COLUMN.name,
			order: ASCENDING_ORDER,
			notifyConsumer: false
		});
	}

	const tasks = tree.tasks;
	const scrollHeight = tasks.length * rowHeight;

	updatedColumns = getWhiteSpaceAdjustedColumns({
		columns: updatedColumns,
		sectionsWidth,
		gridWidth: getCurrentGridWidth({
			...state,
			groupBy: updatedGroupBy,
			scrollHeight
		})
	});

	updateState({
		groupBy: updatedGroupBy,
		columns: updatedColumns,
		scrollHeight,
		...RESET_CONTEXT_MENU,
		bodySectionKeySuffix: newbodySectionKeySuffix,
		selectedTasksCount: countSelectedTasks(tasks),
		// we will focus on the config section header after grouping/ungrouping as the grid will be rebuilt
		selectedCell: {
			rowIndex: 0,
			columnId: CONFIG_SECTION_COLUMN_NAME,
			segment: PRIMARY
		},
		tree: {
			...tree
		}
	});


	if (!isEmpty(updatedGroupBy)) {
		dispatch(GRID_COLUMN_AUTOFITTED, { // autofitting the group column
			column: updatedColumns.left[0],
			columns: updatedColumns,
			notifyConsumer: false
		});
		dispatch(GRID_ALL_TASKS_EXPANDED, {
			expandAll: false
		})
	}
	if (notifyConsumer) {
		dispatch(SN_DATAGRID_ROW_GROUP_CHANGED, updatedGroupBy);
	}
};

export const handleExpandCollapse = ({
	action: {
		payload
	},
	state,
	updateState,
	dispatch
}) => {
	const { config: { rowHeight }, tree } = state;
	const treeUtils = getTreeUtils(tree);

	treeUtils.expandCollapseNode(payload);
	const scrollHeight = tree.tasks.length * rowHeight;

	updateState({
		tree: {
			...tree
		},
		scrollHeight,
		selectedTasksCount: countSelectedTasks(tree.tasks)
	});
	dispatch(SN_DATAGRID_ROW_EXPAND_SET, {
		taskId: payload.id,
		value: payload.expanded
	});
};

export const handleExpandCollapseAll = ({
	action: {
		payload
	},
	state,
	updateState,
	dispatch
}) => {
	const { config: { rowHeight }, tree } = state;
	const treeUtils = getTreeUtils(tree);

	treeUtils.expandCollapseAll(payload.expandAll);
	const scrollHeight = tree.tasks.length * rowHeight;

	updateState({
		tree: {
			...tree
		},
		scrollHeight,
		selectedTasksCount: countSelectedTasks(tree.tasks)
	});
	if (payload.expandAll) {
		dispatch(SN_DATAGRID_ALL_ROWS_EXPANDED);
	} else {
		dispatch(SN_DATAGRID_ALL_ROWS_COLLAPSED);
	}
};

export const handleScrollPositionUpdated = ({
	action: { payload: { scrollTop: scrollPos } },
	updateState,
	state: { scrollTop }
}) => {
	const { TOP, BOTTOM } = SCROLL_DIRECTION;

	if (scrollPos < 0) {
		return;
	}

	updateState({
		scrollTop: scrollPos,
		scrollDirection: scrollPos >= scrollTop ? TOP : BOTTOM,
		isScrolling: true
	});
};

export const handleScrollEnded = ({
	action: { payload: { scrollTop: scrollPos } },
	updateState
}) => {
	updateState({
		scrollTop: scrollPos,
		isScrolling: false
	});
};
