/* eslint-disable no-use-before-define */
import orderBy from 'lodash/orderBy';
import isEmpty from 'lodash/isEmpty';
import get from 'lodash/get';
import TaskNode from './TaskNode';
import { createLog } from '../../../util/logger';
import { ASCENDING_ORDER, EMPTY, EMPTY_CELL, NO_SORT } from '../../../constants';
import { getColumnModelFromId, getAggregationConfigs } from '../../columnModel';
import { ExpressionSolver, ExpressionValidator } from './filterEvaluator';
import partialRight from 'lodash/partialRight';
import { getAggregator } from './helper';

const LOG = createLog('IndexTree');

/**
 * generates choices based on column definition.
 * @param {Object} columns Column Object
 */
const bootstrapChoicesForFilter = (columns) => {
	const columnsForChoiceFilterArr = [];
	const columnsForChoiceFilters = {};

	Object.values(columns).map((cols) => {
		for (let index = 0; index < cols.length; index++) {
			const column = cols[index];

			if (column.columnDefinition.isChoice) {
				if (column.columnDefinition.choices && column.columnDefinition.choices.length > 0) {
					const { columnDefinition: { choices } } = column;

					column.choices = new Map();

					for (let index = 0; index < choices.length; index++) {
						const { value, displayValue } = choices[index];

						column.choices.set(value, { displayValue, selected: true });
					}
				} else {
					columnsForChoiceFilters[column.columnId] = {
						columnModel: column,
						choices: new Map()
					};
					columnsForChoiceFilterArr.push(column.columnId);
				}
			}
		}
	});

	return {
		columnsForChoiceFilterArr,
		columnsForChoiceFilters
	};
};

/**
 * @param {Array} taskList
 * @param {Object} taskExpandMap
 */
export const createTreeObject = (taskList = [], taskExpandMap = {}, columns = {}) => {
	const tree = {
		_referenceTable: {},
		_root: new TaskNode({}),
		_grouped: false,
		_groupedBys: [],
		_groupedData: {},
		_filterExpression: '',
		_isDirty: false,
		_sortedBy: null,
		_sortingOrder: null,
		/**
		 * Though the tree object sits on the state we should only access the following 4 properties
		 * from the view and action handlers. Rest are the internal properties of tree object
		 */
		expanded: false,
		taskExpandMap: taskExpandMap,
		totalTaskNode: null,
		tasks: []
	};

	const { columnsForChoiceFilterArr, columnsForChoiceFilters } = bootstrapChoicesForFilter(columns);

	for (let index = 0; index < taskList.length; index++) {
		if (taskList[index].hasOwnProperty('_expanded')) {
			tree.taskExpandMap[taskList[index].id] = taskList[index]._expanded;
			delete taskList[index]._expanded; // No need to store this inside task node
		}

		const taskNode = new TaskNode(
			taskList[index],
			false,
			index
		);

		tree._referenceTable[taskNode.id] = taskNode;

		if (tree.taskExpandMap.hasOwnProperty(taskNode.id)) {
			taskNode.expanded = tree.taskExpandMap[taskNode.id];
		}

		for (let i = 0; i < columnsForChoiceFilterArr.length; i++) {
			const element = columnsForChoiceFilterArr[i];
			const { columnModel, choices } = columnsForChoiceFilters[element];
			let displayValue = columnModel.getDisplayValue(columnModel, taskNode);
			let value = columnModel.getValue(columnModel, taskNode);

			if (!value) {
				value = null;
				displayValue = `(${EMPTY})`;
			}

			if (!choices.has(value)) {
				choices.set(value, { displayValue, selected: true });
			}
		}
	}

	for (let i = 0; i < columnsForChoiceFilterArr.length; i++) {
		const { choices, columnModel } = columnsForChoiceFilters[columnsForChoiceFilterArr[i]];

		columnModel.choices = choices;
	}

	// Clears old tasks which are not available in the ref table
	Object.values(tree.taskExpandMap).forEach(id => {
		if (!tree._referenceTable.hasOwnProperty(id)) {
			delete tree.taskExpandMap[id];
		}
	});
	const treeUtils = getTreeUtils(tree);

	treeUtils.buildFlatList();

	return {
		tree,
		treeUtils
	};
};

export const getTreeUtils = (tree) => {
	const LOG = createLog('Tree');

	/**
	 *
	 * @param parentColumn
	 * @param sortByColumnId
	 * @param groupBy
	 * @param filterCallback
	 * @param sortOrder
	 * @param onBuildFlatList
	 * @param aggregateConfigs
	 * @return {IndexTree}
	 */
	const init = ({
		parentColumn,
		sortByColumnId,
		groupBy,
		filterCallback,
		sortOrder,
		aggregateConfigs,
		columns,
		generateTotalTaskNode = false
	}) => {
		if (!isEmpty(groupBy)) {
			groupBy.forEach(groupByColumnId => {
				groupWithColumnId(groupByColumnId, columns);
			});
		} else if (parentColumn) {
			hierarchy(parentColumn, columns);
			tree.heirarchyField = parentColumn;
		}

		if (sortByColumnId) {
			sort({
				sortByColumnId,
				sortOrder,
				columns
			});
		} else {
			unSort();
		}

		if (filterCallback) {
			filter(filterCallback, aggregateConfigs);
		}

		if (generateTotalTaskNode) {
			_generateTotalNode(aggregateConfigs);
		}
		tree._isDirty = true;

		return tree;
	};

	/**
	 * Build tree hierarchy from parent children relation
	 * @param parentColumn
	 * @return {IndexTree}
	 */
	const hierarchy = (parentColumn, columns) => {
		if (tree._isDirty) {
			cleanNodes();
		}

		for (const key in tree._referenceTable) {
			if (tree._referenceTable.hasOwnProperty(key)) {
				const node = tree._referenceTable[key];

				if (node.getParentId(parentColumn) && tree._referenceTable[node.getParentId(parentColumn)]) {
					tree._referenceTable[node.getParentId(parentColumn)].pushChild(node);
				} else {
					tree._root.pushChild(node);
				}
			}
		}

		if (tree._sortedBy) {
			sort({ sortByColumnId: tree._sortedBy, columns });
		}

		return tree;
	};

	const cleanNodes = () => {
		tree._grouped = false;
		tree._groupedBys = [];
		tree.tasks = [];
		tree._root.replaceChild([]);

		for (const key in tree._referenceTable) {
			if (tree._referenceTable.hasOwnProperty(key)) {
				const node = tree._referenceTable[key];

				if (node.temporary) {
					delete tree._referenceTable[key];
				} else {
					node.replaceChild([]);
				}
			}
		}
	};

	/**
	 *
	 * @param totalAggregateConfigs
	 * @private
	 */
	const _generateTotalNode = (totalAggregateConfigs) => {
		tree.totalTaskNode = _getNewTaskNode('total_node', '', {});
		tree.totalTaskNode.temporary = true;
		tree.totalTaskNode.index = 0;
		tree.totalTaskNode.children = Object.values(tree._referenceTable)
			.filter(task => !task.temporary && task.visible);
		tree.totalTaskNode._meta.leafNodeCount = tree.totalTaskNode.children.length;
		aggregateTaskColumns({
			task: tree.totalTaskNode,
			aggregateConfigs: totalAggregateConfigs,
			isTemporaryCheck: true
		});
	};

	/**
	 * Sort tree using column or callback
	 * @param {String} sortByColumnId
	 * @param {function} callback
	 * @param {String} sortOrder
	 * @return {*}
	 */
	const sort = ({
		sortByColumnId,
		sortOrder = ASCENDING_ORDER,
		columns = {},
		shouldBuildFlatList = true
	}) => {
		if (!sortByColumnId) { // sortByColumnId is null for unsort
			return unSort();
		}

		const columnModel = getColumnModelFromId(columns, sortByColumnId);

		if (!columnModel) {
			LOG.warn(`Can not sort on the column ${sortByColumnId}. It is not in view.`);

			return;
		}
		const { getSortComparator } = columnModel;
		const sortComparator = getSortComparator(columnModel, sortOrder);
		const _sort = (root) => {
			if (root && root.children.length > 0) {
				root.children.forEach(child => {
					_sort(child);
				});
				root.children = root.children.sort(sortComparator);
			}
		};

		tree._sortedBy = sortByColumnId;
		tree._sortingOrder = sortOrder;
		_sort(tree._root);

		if (shouldBuildFlatList) {
			buildFlatList();
		}
		tree.tasks = _recalculateTreeIndices(tree.tasks);

		return tree;
	};
	/**
	 * remove sorting on tree or say going back to initial order of task passed.
	 * @returns {IndexTree}
	 */
	const unSort = () => {
		const _sort = (root) => {
			if (root && root.children.length > 0) {
				root.children.forEach(child => {
					_sort(child);
				});
				root.children = orderBy(root.children, (node) => node.initialOrder, [ASCENDING_ORDER]);
			}
		};

		tree._sortedBy = null;
		tree._sortingOrder = NO_SORT;
		_sort(tree._root);
		buildFlatList();

		return tree;
	};
	/**
	 * Recalculated the index property of each tasknode
	 * @param {[]} tasks
	 */
	const _recalculateTreeIndices = (tasks = []) => {
		return tasks.map((task, index) => {
			task.index = index;

			return task;
		});
	};

	const getTaskByIndex = (index = 0) => {
		if (index < 0 || index > tree.tasks.length) {
			LOG.error(`index ${index} is out of bounds for tasks array`);

			return null;
		}

		return tree.tasks.find(task => task.index === index);
	};

	/**
	 * Filter nodes using callback function
	 * @param filterExpression
	 * @param aggregateConfigs
	 * @param columns
	 */
	const filterHelper = (filterExpression, aggregateConfigs, columns, choiceListValueMaps) => {
		const callback = partialRight(ExpressionValidator, ExpressionSolver(filterExpression), choiceListValueMaps);
		const _filter = (root, callback) => {
			let rootVisible = callback(root, columns);

			if (root && root.children.length > 0) {
				root.children.forEach(element => {
					_filter(element, callback, columns);

					if (element.visible) {
						rootVisible = true;
					}
				});
				aggregateTaskColumns({
					task: root,
					aggregateConfigs,
					isTemporaryCheck: true
				});
			}
			if (root) {
				root.visible = rootVisible;
			}
		};

		tree._root.children.forEach((child) => {
			_filter(child, callback);
		});

		return tree;
	};

	/**
	 * Filter data and update nodes
	 * @param filterExpression
	 * @param aggregateConfigs
	 * @param columns
	 * @param shouldBuildFlatList
	 */
	const filter = (filterExpression, aggregateConfigs, columns, shouldBuildFlatList = true, choiceListValueMaps = {}) => {
		filterHelper(filterExpression, aggregateConfigs, columns, choiceListValueMaps);
		tree._filterExpression = filterExpression;

		// Filter changes all nodes child count if there's group by.
		if (tree._grouped) {
			updateChildCount();
		}

		if (shouldBuildFlatList) {
			buildFlatList();
		}

		if (tree.totalTaskNode) {
			_generateTotalNode(aggregateConfigs);
		}

		return tree;
	};
	/**
	  * Builds Grouped Data from given Ref table.
	  * @param {Object} taskMap
	  * @param {String} groupByKey
	  * @param {Object} Column Column Details
	  * @param {String} parentId
	  * @param {String} keyPattern
	  * @param {Boolean} aggregateConfigs
	  */
	const buildGroupedDataFromRefTable = (taskMap, groupByKey, { columnModel }, parentId, keyPattern='', aggregateConfigs) => {
		let groupedDataKey = tree._groupedBys.length;

		if (parentId) {
			groupedDataKey = `${groupedDataKey}__${parentId}`;
		}

		const tempTaskMap = tree._groupedData[groupedDataKey] = {};

		for (const key in taskMap) {
			if (taskMap.hasOwnProperty(key)) {
				const node = taskMap[key];
				const id = columnModel.getDisplayValue(columnModel, node);
				let nodeId;

				if (keyPattern && parentId) {
					nodeId = `${keyPattern}__${parentId}__${id}`;
				} else {
					nodeId = id;
				}

				if (id) {
					if (!tempTaskMap[nodeId]) {
						tempTaskMap[nodeId.toString()] = _getNewTaskNode(
							nodeId,
							`${id}`,
							{}
						);
					}
					tempTaskMap[nodeId].pushChild(node);
					aggregateTaskColumns({
						task: tempTaskMap[nodeId],
						aggregateConfigs,
						isTemporaryCheck: true
					});
					tempTaskMap[nodeId].visible = tempTaskMap[nodeId].visible || node.visible;
				} else {
					let notDefinedCategoryNode = tempTaskMap.NOT_DEFINED_CATEGORY;

					if (!notDefinedCategoryNode) {
						let notDefinedCategory = 'NOT_DEFINED_CATEGORY';

						if (nodeId) {
							notDefinedCategory = `${nodeId}${notDefinedCategory}`;
						}
						tempTaskMap[notDefinedCategory.toString()] = _getNewTaskNode(
							notDefinedCategory,
							`${EMPTY_CELL}`,
							{}
						);
						notDefinedCategoryNode = tempTaskMap[notDefinedCategory];
						notDefinedCategoryNode.visible = false;
					}
					notDefinedCategoryNode.pushChild(node);
					aggregateTaskColumns({
						task: notDefinedCategoryNode,
						aggregateConfigs,
						isTemporaryCheck: true
					});
					notDefinedCategoryNode.visible = notDefinedCategoryNode.visible || node.visible;
				}
			}
		}

		return tempTaskMap;
	};

	/**
	 * Builds Ref tables from Task array.
	 * @param {Array} taskArray Array of the tasks
	 */
	const _buildRefTableFromArray = (taskArray) => {
		return taskArray.reduce((acc, cur) => {
			acc[cur.id] = cur;

			return acc;
		}, {});
	};

	/**
	 * Applies Group by with provided key on children of the given TaskNode.
	 * @param {TaskNode} parent Parent of the children
	 * @param {String} groupByKey Group By Key
	 * @param aggregateConfigs
	 * @param {Object} columnDetails Column Details like label and models
	 */
	const buildGroupedDataFromArray = (parent, groupByKey, { columnLabel, columnModel }, aggregateConfigs) => {
		if (!Array.isArray(parent.children)) {
			LOG.error(`Parent type should be array, received: ${typeof parent}`);

			return;
		}
		// Build Map from Array.
		const taskMap = _buildRefTableFromArray(parent.children);
		// Apply buildGroupedDataFromRefTable
		const groupedData = buildGroupedDataFromRefTable(taskMap, groupByKey,
			{ columnLabel, columnModel }, parent.id, tree._groupedBys.length, aggregateConfigs);

		// have keys as taskNode and assign children and return it.
		parent.replaceChild([]);
		for (const key in groupedData) {
			if (groupedData.hasOwnProperty(key)) {
				parent.pushChild(groupedData[key]);
			}
		}
		aggregateTaskColumns({
			task: parent,
			aggregateConfigs,
			isTemporaryCheck: true
		});
	};

	/**
	 * Create new tree by group by
	 * list provided for multiple groupBy
	 * @param {String} groupBy
	 * @param {String} columnLabel
	 * @param {function} getDisplayValue
	 */
	const _group = ({
		groupBy,
		columnLabel,
		columnId,
		aggregateConfigs = {},
		columns,
		shouldBuildFlatList = true
	}) => {
		if (tree._groupedBys.includes(groupBy)) {
			LOG.error(`Can not group on already grouped column: ${groupBy}.`);

			return;
		}

		const columnModel = getColumnModelFromId(columns, columnId);

		if (!columnModel) {
			LOG.warn(`Can not group on the column ${groupBy}. It is not in view.`);

			return;
		}

		if (tree._groupedBys.length < 1) {
			if (tree._isDirty) {
				cleanNodes();
			}
			tree._root.children = [];
			tree._grouped = true;
		}

		tree._groupedBys.push(groupBy);

		if (tree._groupedBys.length <= 1) {
			const groupedData = buildGroupedDataFromRefTable(tree._referenceTable, groupBy, { columnLabel,
				columnModel }, null, '', aggregateConfigs);

			for (const key in groupedData) {
				if (groupedData.hasOwnProperty(key)) {
					tree._referenceTable[key] = groupedData[key];
					tree._root.pushChild(groupedData[key]);
				}
			}
			aggregateTaskColumns({
				task: tree._root,
				aggregateConfigs,
				isTemporaryCheck: true
			});
		} else {
			const refTables = [];

			// Get Grouped Data
			if (tree._groupedBys.length === 2) {
				refTables.push(tree._groupedData[1]);
			} else {
				// getting result from last level group by op.
				const startingKey = tree._groupedBys.length - 1;

				for (const key in tree._groupedData) {
					if (tree._groupedData.hasOwnProperty(key)) {
						if (key.startsWith(`${startingKey}__`)) {
							refTables.push(tree._groupedData[key]);
						}
					}
				}
			}

			// Build Multiple RefTable from it.
			refTables.map(refTable => {
				for (const key in refTable) {
					if (refTable.hasOwnProperty(key)) {
						buildGroupedDataFromArray(refTable[key], groupBy, { columnLabel,
							columnModel }, aggregateConfigs);
					}
				}
			});
		}

		if (groupBy === tree._sortedBy) {
			tree._root.children = orderBy(tree._root.children, (node) =>
				columnModel.getDisplayValue(columnModel, node), [tree._sortingOrder]);
		}

		if (shouldBuildFlatList) {
			// Updates child counts because of group by.
			updateChildCount();
			buildFlatList();
			if (tree.totalTaskNode) {
				_generateTotalNode(aggregateConfigs);
			}
		}

		return tree;
	};

	/**
	 * Does Group by with Provided columnId.
	 * @param {String} columnId
	 */
	const groupWithColumnId = (columnId, columns, choiceListValueMaps, shouldBuildFlatList = true) => {
		const columnModel = getColumnModelFromId(columns, columnId);

		if (!columnModel) {
			LOG.warn(`Can not group on the column ${columnId}. It is not in view.`);

			return;
		}

		_group({
			groupBy: columnModel.columnDefinition.name,
			columnLabel: columnModel.columnDefinition.label,
			aggregateConfigs: getAggregationConfigs(columns),
			columnId,
			columns,
			shouldBuildFlatList
		});

		if (!isEmpty(tree._filterExpression)) {
			filterHelper(
				tree._filterExpression,
				getAggregationConfigs(columns),
				columns,
				choiceListValueMaps
			);
		}
	};

	const isTaskSingleRootNode = taskId => (tree._root && tree._root.children.length === 1 && tree._root.children[0].id === taskId);

	const removeAllGroups = (columns) => {
		hierarchy(tree.heirarchyField, columns);
		buildFlatList();
	};

	/**
	 * Removes group by and rearranges the data.
	 * @param {String} columnName
	 * @param groupedColumnIds
	 */
	const removeGroup = (columnName, groupedColumnIds, columns, choiceListValueMaps, shouldBuildFlatList = true) => {
		if (!tree._groupedBys.includes(columnName)) {
			LOG.error(`Can not remove group by on ${columnName}. Tree is not grouped on given key.`);

			return;
		}

		// handles removal of one group by.
		if (tree._groupedBys.length === 1) {
			hierarchy(tree.heirarchyField, columns);

			return;
		}

		// Get index and Level
		// drop all keys from given level onwards.
		const groupByIndex = tree._groupedBys.indexOf(columnName);

		groupedColumnIds.splice(groupByIndex, 1);

		// clean all
		if (tree._isDirty) {
			cleanNodes();
		}

		// Emptying _groupedData.
		tree._groupedData = {};

		// Apply all group by
		for (let index = 0; index < groupedColumnIds.length; index++) {
			const element = groupedColumnIds[index];
			const columnModel = getColumnModelFromId(columns, element);

			_group({
				groupBy: columnModel.columnDefinition.name,
				columnLabel: columnModel.columnDefinition.label,
				aggregateConfigs: getAggregationConfigs(columns),
				columnId: element,
				columns,
				shouldBuildFlatList
			});
		}
		if (!isEmpty(tree._filterExpression)) {
			filterHelper(
				tree._filterExpression,
				getAggregationConfigs(columns),
				columns,
				choiceListValueMaps
			);
		}

		if (tree._sortedBy) {
			sort({
				sortByColumnId: tree._sortedBy,
				sortOrder: tree._sortingOrder,
				shouldBuildFlatList: false
			});
		}

		updateChildCount();

		if (shouldBuildFlatList) {
			buildFlatList();
		}

		if (tree.totalTaskNode) {
			_generateTotalNode(getAggregationConfigs(columns));
		}
	};
	/**
	 * Updates total number of Child counts. Total number of Leaf nodes in tree from given node.
	 * @param {TaskNode} task
	 */
	const updateChildCount = () => {
		const _calculateChildCount = (node) => {
			let childrenCount = 0;

			if (!node.visible) {
				return 0;
			}

			if (node.children.length === 0) {
				return 1;
			}

			node.children.map(child => {
				childrenCount += _calculateChildCount(child);
			});

			return childrenCount;
		};

		Object.values(tree._groupedData).forEach(data => {
			Object.values(data).forEach(vNode => {
				vNode.childCount = _calculateChildCount(vNode);
			});
		});
	};

	/**
	 *
	 * @param {*} nodeId Node ID
	 * @param columnData
	 * @param {*} nodeData node data
	 * @private
	 */
	const _getNewTaskNode = (nodeId, columnData, nodeData) => {
		return new TaskNode(
			{
				id: nodeId.toString(),
				...nodeData,
				text: { value: columnData, displayValue: columnData }
			},
			true
		);
	};

	/**
	* Build flat list from tree
	*/
	const buildFlatList = () => {
		const _buildFlatListHelper = (root, list, level) => {
			for (const element of root.children) {
				if (element.visible) {
					element.index = list.length;
					element.level = level;
					list.push(element);
					if (element.expanded) {
						if (element.children && element.children.length > 0) {
							_buildFlatListHelper(element, list, level + 1);
						}
					}
				}
			}
		};

		tree.tasks = [];
		_buildFlatListHelper(tree._root, tree.tasks, 0);

		return tree;
	};

	/**
	 * returns level of group from the groupByKey, if it's not grouped returns -1.
	 * @param {String} groupByKey
	 */
	const getGroupByLevel = (groupByKey) => {
		return tree._groupedBys.indexOf(groupByKey);
	};

	/**
	 * updates a task
	 * @param {String} taskId
	 * @param {Object} updateObj
	 */
	const updateTask = (task, updateObj) => {
		for (const key in updateObj) {
			if (updateObj.hasOwnProperty(key)) {
				task[key] = updateObj[key];
			}
		}

		return tree;
	};

	/**
	 * updates all tasks
	 * @param {Object} updateObj
	 */
	const updateTasks = (updateObj) => {
		if (tree._groupedBys.length > 1) {
			Object.values(tree._groupedData).forEach(element => {
				Object.values(element).forEach(task => updateTask(task, updateObj));
			});
		}
		Object.values(tree._referenceTable).forEach((task) => updateTask(task, updateObj));

		buildFlatList();

		return tree;
	};

	/**
	 * Function to update task aggregation
	 * @param task
	 * @param taskId
	 * @param aggregateConfigs
	 * @param isTemporaryCheck
	 */
	const aggregateTaskColumns = ({ task, taskId, aggregateConfigs, isTemporaryCheck = true }) => {
		const taskNode = task && task instanceof TaskNode
			? task
			: tree._referenceTable[taskId];

		if (!taskNode) {
			LOG(`tree not found - id: ${taskId} `);

			return;
		}

		const validTask = isTemporaryCheck
			? taskNode.temporary && taskNode.children.length > 0
			: taskNode.children.length > 0;
		const validInputs = validTask && aggregateConfigs && Array.isArray(aggregateConfigs);

		if (validInputs) {
			aggregateConfigs.forEach(aggregateConfig => {
				const {
					setter,
					columnModel,
					aggregation:
						{ aggregateType,
							aggregateFunction
						}
				} = aggregateConfig;
				const aggregator = aggregateType.toUpperCase() === 'CUSTOM'
					? aggregateFunction
					: getAggregator(aggregateType);

				setter(columnModel, taskNode, aggregator(taskNode, aggregateConfig));
			});
		}
	};

	const findTaskNode = (taskId) => {
		return get(tree._referenceTable, taskId, get(tree._groupedData, taskId));
	};

	const expandCollapseAll = (value) => {
		if (!value) {
			clearIndex(tree._root); // TODO find a better way to handle index and clearance
		}

		// Expanding VNodes for multipleGroup Bys
		if (tree._groupedBys.length > 1) {
			Object.values(tree._groupedData).map(element => {
				Object.values(element).map(node => {
					node.expanded = value;
					node.index = null;
					tree.taskExpandMap[node.id] = node.expanded;
				});
			});
		} else {
			Object.values(tree._referenceTable).map(node => {
				node.expanded = value;
				node.index = null;
				tree.taskExpandMap[node.id] = node.expanded;
			});
		}

		tree.expanded = value;

		buildFlatList();

		return tree;
	};

	const clearIndex = (node) => {
		if (node) {
			if (node.children && node.children.length > 0) {
				node.children.forEach(child => {
					clearIndex(child);
				});
			}
			node.index = null;
		}
	};

	/**
	 *
	 * @param {String} id
	 * @param {String} value
	 */
	const expandCollapseNode = ({ id, expanded }) => {
		let parent;
		if (tree._referenceTable[id]) {
			parent = tree._referenceTable[id];
			parent.expanded = expanded;
			tree.taskExpandMap[id] = expanded;
		} else {
			for (const key in tree._groupedData) {
				if (tree._groupedData.hasOwnProperty(key)) {
					if (id.indexOf(key) === 0 && tree._groupedData[key][id]) {
						parent = tree._groupedData[key][id];
						parent.expanded = expanded;
						tree.taskExpandMap[id] = expanded;
						break;
					}
				}
			}
		}

		if (!expanded) {
			clearIndex(parent);
		}
		buildFlatList();
	};

	const getNodeById = (id) => {
		return tree._referenceTable[id];
	};

	const getTaskColumnData = (id) => {
		const taskData = { ...getNodeById(id).taskData };

		delete taskData.id;
		delete taskData._expanded;
		delete taskData[tree.heirarchyField];
		// TODO: isSummary hardcoded temporarily for sn-task-planner usecase
		delete taskData.isSummary;

		return taskData;
	};

	return {
		init,
		buildFlatList,
		getNodeById,
		expandCollapseNode,
		clearIndex,
		expandCollapseAll,
		aggregateTaskColumns,
		updateTask,
		updateTasks,
		getGroupByLevel,
		updateChildCount,
		removeGroup,
		removeAllGroups,
		isTaskSingleRootNode,
		groupWithColumnId,
		buildGroupedDataFromArray,
		buildGroupedDataFromRefTable,
		filter,
		filterHelper,
		unSort,
		sort,
		cleanNodes,
		hierarchy,
		findTaskNode,
		getTaskByIndex,
		getTaskColumnData
	};
};


