import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import rtlBehavior from '@servicenow/behavior-rtl';
import view from './view';
import styles from './styles/index.scss';
import {
	gridActionHandlers,
	dragAndDropActionHandlers,
	columnDragAndDropActionHandlers,
	keyboardNavHandlers,
	panelActionHandlers
} from '../core/actions';
import {
	gridServerActionHandlers
} from '../core/actions/gridServer';

import { CONFIG_SERVER } from '../constants';
import transformConfig from '../core/helpers/transformConfig';
import componentHandlers from '../common/actions/component';
import { getGridInitialState, transformState } from '../core/helpers/gridStateParams';
import eventHandlers from '../core/eventHandlers';

createCustomElement('sn-datagrid-server', {
	renderer: { type: snabbdom },
	view,
	initialState: {
		...getGridInitialState(),
		visibleHeight: 0,
		config: transformConfig(CONFIG_SERVER),
		gridWidth: 0
	},
	transformState,
	styles,
	actionHandlers: {
		...componentHandlers,
		...gridActionHandlers,
		...gridServerActionHandlers,
		...dragAndDropActionHandlers,
		...columnDragAndDropActionHandlers,
		...keyboardNavHandlers,
		...panelActionHandlers
	},
	behaviors: [
		{ behavior: rtlBehavior }
	],
	properties: {
		config: {
			default: CONFIG_SERVER
		},
		columns: {
			default: []
		},
		tasks: {
			default: []
		},
		sortBy: {
			default: {}
		},
		groupBy: {
			default: []
		},
		pagination: {
			default: {}
		}
	},
	eventHandlers
});
