import set from 'lodash/set';
import get from 'lodash/get';
import { getAggregator } from './helper';

export default class TaskNode {
	/**
	 * Task node in data tree to represent a single task
	 * @param taskData
	 * @param temporary
	 * @param initialOrder
	 */
	constructor(taskData, temporary = false, initialOrder = 0) {
		this._data = taskData;
		this.id = taskData.id;
		this.children = [];
		this._meta = {
			visible: true,
			level: 0,
			index: null,
			childCount: 0,
			expanded: false,
			initialOrder
		};
		this.temporary = temporary;
		this.selected = false;
	}

	/**
	 * return initial order of task received from parent component
	 * @returns {number}
	 */
	get initialOrder() {
		return this._meta.initialOrder;
	}

	/**
	 *
	 * @return {boolean}
	 */
	get visible() {
		return this._meta.visible;
	}

	/**
	 *
	 * @param visibility
	 */
	set visible(visibility) {
		this._meta.visible = visibility;
	}

	/**
	 *
	 * @param value
	 */
	set level(value) {
		this._meta.level = value;
	}

	/**
	 *
	 * @return {boolean}
	 */
	get level() {
		return this._meta.level;
	}

	/**
	 * @returns {int}
	 */
	get childCount() {
		return this._meta.childCount;
	}

	/**
	 * @param {int} value
	 */
	set childCount(value) {
		this._meta.childCount = value;
	}

	/**
	 *
	 * @return {boolean}
	 */
	get expanded() {
		return this._meta.expanded;
	}

	/**
	 *
	 * @param value
	 */
	set expanded(value) {
		this._meta.expanded = value;
	}

	/**
	 *
	 * @return {boolean}
	 */
	get index() {
		return this._meta.index;
	}

	/**
	 *
	 * @param {int} value
	 */
	set index(value) {
		this._meta.index = value;
	}

	/**
	 *
	 * @param key
	 * @param value
	 * @return {Object}
	 */
	setValue(key, value) {
		if (this.temporary) {
			// Only update the temporary tasks. Data updates should be taken care by theconnected component
			set(this._data, key, value);
		}
	}

	/**
	 *
	 * @param key
	 * @return {*}
	 */
	getValue(key) {
		return get(this._data, key);
	}

	/**
	 * Gets the data stored inside the tree node
	 * @return {*}
	 */
	get taskData() {
		return this._data;
	}

	/**
	 *
	 * @param aggregateConfigs
	 * @private
	 * @deprecated
	 */
	_aggregateColumns(aggregateConfigs) {
		if (this.temporary && this.children.length > 0 && aggregateConfigs && Array.isArray(aggregateConfigs)) {
			aggregateConfigs.forEach(aggregateConfig => {
				const {
					setter,
					columnModel,
					aggregation:
						{ aggregateType,
							aggregateFunction
						}
				} = aggregateConfig;
				const aggregator = aggregateType.toUpperCase() === 'CUSTOM'
					? aggregateFunction
					: getAggregator(aggregateType);

				setter(columnModel, this, aggregator(this, aggregateConfig));
			});
		}
	}

	/**
	 *
	 * @param aggregateConfigs
	 * @deprecated
	 */
	aggregateColumns(aggregateConfigs) {
		this._aggregateColumns(aggregateConfigs);
	}


	/**
	 * add a new children to tree node
	 * @param node
	 * @return {number}
	 */
	pushChild(node) {
		this.children.push(node);
		this._meta.childCount = this.children.length;
	}

	/**
	 * Insert a children to specific position to node
	 * @param node
	 * @param location
	 */
	insertChild(/* node, location = 0 */) {
		/** implement here */
	}


	/**
	 * replace node children with new children
	 * @param children
	 * @returns {Error}
	 */
	replaceChild(children) {
		if (!Array.isArray(children)) {
			return Error('Child element should be Array Object');
		}
		this.children = children;
		this._meta.childCount = this.children.length;
	}

	/**
	 *
	 * @return {*}
	 */
	getParentId(parentColumn) {
		return this.getValue(parentColumn);
	}
}
