import {
	ASCENDING_ORDER,
	DESCENDING_ORDER
} from '../../constants';
/**
 * This is a default implementation of column model APIs
 * The consumer component may choose to override one or all of them
 * As long as the consumer wants to sort on the value returned by the `getValue`,
 * there is no need to override `getSortComparator` API
 *
 * It is expected that the data for all column types will be an object containing `value` and `displaValue` fields
 * If that is not the case, then the consumer component has to override the getValue and getDisplayValue APIs;
 */

export default class Column {
	/**
	 * returns the Jsx for each column type
	 * @param {Object} { displayValue }
	 * @return {Jsx}
	 */
	renderer({ displayValue }) {
		return <div className="sn-datagrid-content">
			{displayValue}
		</div>;
	}


	setValue(columnModel, task, value) {
		task.setValue(`${columnModel.columnDefinition.name}`, { value: value, displayValue: String(value) });
	}

	getValue(columnModel, task) {
		const columnName = columnModel.columnDefinition.name;

		return (task.taskData[columnName] || {}).value;
	}

	getDisplayValue(columnModel, task) {
		const columnName = columnModel.columnDefinition.name;

		return (task.taskData[columnName] || {}).displayValue || '';
	}

	getSortComparator(columnModel, sortOrder) {
		const sortComparator = (task1, task2) => {
			// we are avoiding `this` to make it easier for the consumer component to override these functions
			const task1Val = (columnModel.getDisplayValue(columnModel, task1) || '').toLowerCase().trim();
			const task2Val = (columnModel.getDisplayValue(columnModel, task2) || '').toLowerCase().trim();
			switch (sortOrder) {
				default:
				case ASCENDING_ORDER: return task1Val.localeCompare(task2Val);
				case DESCENDING_ORDER: return task2Val.localeCompare(task1Val);
			}
		};

		return sortComparator;
	}
}
