import { actionTypes } from '@servicenow/ui-core';
import { CONTAINER_SELECTED } from '../constants';
import {
	addOutsideScrollListener,
	removeOutsideListener
} from '../util/outsideScroll';
import { focusFirstElement } from '../core/helpers/accessibilityHelpers';

const { COMPONENT_DOM_TREE_READY, COMPONENT_DISCONNECTED, COMPONENT_RENDERED } = actionTypes;

export const actionHandlers = {
	[COMPONENT_DOM_TREE_READY]: {
		stopPropagation: true,
		effect ({
			host,
			dispatch
		}) {
			addOutsideScrollListener(host, () => dispatch(CONTAINER_SELECTED));
			focusFirstElement({ host });

		}
	},
	[COMPONENT_RENDERED]: {
		stopPropagation: true,
		effect: focusFirstElement
	},
	[COMPONENT_DISCONNECTED]: ({
		action: { payload: { host } }
	}) => {
		removeOutsideListener(host);
	}
};
