import { actionTypes } from '@servicenow/ui-core';
import { MOVE_COLUMN_TO_SECTION, DROPDOWN_ITEM_CLICKED } from '../constants';
import { COL_PIN_DROPDOWN_CHANGED, NOW_TABS_CHANGED } from './constants';
import { focusFirstElement } from '../core/helpers/accessibilityHelpers';
const { COMPONENT_DOM_TREE_READY, COMPONENT_RENDERED } = actionTypes;

export const actionHandlers = {
	[DROPDOWN_ITEM_CLICKED]: {
		stopPropagation: true,
		effect({
			action: { payload: { item, column, type } },
			dispatch
		}) {
			if (type === COL_PIN_DROPDOWN_CHANGED && column.section !== item.id) {
				dispatch(MOVE_COLUMN_TO_SECTION, {
					moveTo: item.id,
					column
				});
			}
		}
	},
	[NOW_TABS_CHANGED]: {
		stopPropagation: true,
		effect({
			action,
			updateState
		}) {
			updateState({
				tab: action.payload.value
			});
		}
	},
	[COMPONENT_DOM_TREE_READY]: {
		stopPropagation: true,
		effect: focusFirstElement
	},
	[COMPONENT_RENDERED]: {
		stopPropagation: true,
		effect: focusFirstElement
	}
};
