import '@servicenow/now-tabs';
import {
	SIDE_PANEL_TAB,
	LEGEND,
	LEGEND_LABEL,
	SHORTCUTS,
	SHORTCUTS_LABEL,
	SIZE_MEDIUM,
	SIZE_SMALL,
	LEGEND_TAB_HEADER,
	LEGEND_TAB_CONTENT,
	SHORTCUTS_TAB_CONTENT
} from '../constants';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';


const getIconsHtml = (iconNames, size) => {
	return iconNames.map((iconName, index) => {
		const separator = index + 1 < iconNames.length ? '/' : '';

		return <Fragment><now-icon icon={iconName} size={size}/>{separator}</Fragment>
	});
};

const getLegendTabContents = (state) => {
	const visibleItems = LEGEND_TAB_CONTENT.filter(item => !(item.hideItem && item.hideItem(state)));
	const legendItems = visibleItems.map(legendItem => {
	const icons = getIconsHtml(legendItem.icons, SIZE_MEDIUM)
		return (
			<div className="panel-legend-item">
				<span className="panel-legend-item-left">
				{ icons }
				</span>
				<span className="panel-legend-item-right">{legendItem.description}</span>
			</div>
		);
	});

	return (
		<div className="panel-row-items-container">
			<div className="panel-legend-header">{LEGEND_TAB_HEADER}</div>
			{legendItems}
		</div>
	);
};

const getShortcutsTabContents = (state) => {
	return SHORTCUTS_TAB_CONTENT.map((section, index) => {
		const header = section.header;
		const visibleItems = section.items.filter((item) => !(item.hideItem && item.hideItem(state)));
		const htmlItems = visibleItems.map(item => {
			return (
				<div className="panel-shortcuts-item">
					<div className="panel-shortcuts-item-left">
						{item.key ? item.key: <Fragment/>}
						{item.keyIcons ? getIconsHtml(item.keyIcons, SIZE_SMALL) : <Fragment/>}
					</div>
					<div className="panel-shortcuts-item-right">{item.description}</div>
				</div>
			);
		});
		const horizontalLine = index + 1 < SHORTCUTS_TAB_CONTENT.length ? <hr className="panel-shortcuts-hr"/> : <Fragment />;

		return (
			<div className="panel-row-items-container">
				<div className="panel-shortcuts-header">{header}</div>
				{htmlItems}
				{horizontalLine}
			</div>
		);
	});
};

const getTabContents = (state, sidePanel) => {
	switch (sidePanel.mode) {
		case LEGEND:
			return getLegendTabContents(state);
		case SHORTCUTS:
			return getShortcutsTabContents(state);
	}
};

const view = (state) => {
	const { properties:  { sidePanel } } = state;
	return (
		<div className="panel">
			<now-tabs
				selectedItem={sidePanel.mode}
				items={[
					{ id: LEGEND, label: LEGEND_LABEL },
					{ id: SHORTCUTS, label: SHORTCUTS_LABEL }]}
				fixedWidth={true}
				appendToPayload={{ type: SIDE_PANEL_TAB }}></now-tabs>
			<div className="panel-container">{ getTabContents(state, sidePanel) }</div>
		</div>
	);
};

export default view;

