import {
	COLUMN_DEFINITION_ATTRIBUTE_UPDATED,
	SECTION,
	REORDER_COLUMN,
	REORDER_SUBCOLUMN,
	COLUMNS_INIT_WIDTH_AUTO_ADJUSTED
} from '../../../constants';
import isUndefined from 'lodash/isUndefined';
import { getWhiteSpaceAdjustedColumns, getCurrentGridWidth } from '../../helpers/sections';

const getUpdatedColumns = (targetIndex, sourceIndex, columns, column) => {
	if (targetIndex < sourceIndex) {
		return [
			...columns.slice(0, targetIndex),
			column,
			...columns.slice(targetIndex, sourceIndex),
			...columns.slice(sourceIndex + 1)
		];
	} else if (targetIndex > sourceIndex) {
		return [
			...columns.slice(0, sourceIndex),
			...columns.slice(sourceIndex + 1, targetIndex),
			column,
			...columns.slice(targetIndex)
		];
	} else {
		return columns;
	}
};

const getSectionCols = ({ columns, column, targetIndex, section = SECTION.CENTER, groupBy }) => {
	const sectionColumns = columns[section];
	const sourceSection = column.section;
	const sourceSectionColumns = columns[sourceSection];
	const sourceIndex = sourceSectionColumns.findIndex(col => col.columnId === column.columnId);
	let updatedColumns;

	// GroupBy column should always be first
	if (section === SECTION.LEFT && targetIndex === 0 && groupBy.length > 0) {
		targetIndex = 1;
	}
	if (sourceSection === section) {
		updatedColumns = {
			[section]: getUpdatedColumns(targetIndex, sourceIndex, sectionColumns, column)
		};
	} else {
		const updatedCol = {
			...column,
			section
		};

		updatedColumns = {
			[section]: [
				...sectionColumns.slice(0, targetIndex),
				updatedCol,
				...sectionColumns.slice(targetIndex)
			],
			[sourceSection]: [
				...sourceSectionColumns.slice(0, sourceIndex),
				...sourceSectionColumns.slice(sourceIndex + 1)
			]
		};
	}

	return updatedColumns;
};

const getSectionColsForSubColUpdate = ({ columns, column, parentColumn, targetIndex }) => {
	const { section, subColumns } = parentColumn;
	const parentSectionColumns = columns[section];
	const parentColumnIndex = parentSectionColumns.findIndex(col => col.columnId === parentColumn.columnId);
	const sourceIndex = subColumns.findIndex(col => col.columnId === column.columnId);

	const updatedSubCols = getUpdatedColumns(targetIndex, sourceIndex, subColumns, column);

	if (updatedSubCols === subColumns) {
		return {};
	}

	const updatedParentCol = {
		...parentColumn,
		subColumns: updatedSubCols
	};

	return {
		[section]: [
			...parentSectionColumns.slice(0, parentColumnIndex),
			updatedParentCol,
			...parentSectionColumns.slice(parentColumnIndex + 1)
		]
	};
};

export const handleColumnReorder = ({ updateState, column, offsetY, parentColumn }) => {
	updateState({
		columnReorder: {
			column,
			offsetY,
			parentColumn,
			type: parentColumn ? REORDER_SUBCOLUMN : REORDER_COLUMN
		}
	});
};

export const handleColumnReorderEnd = ({
	state,
	updateState,
	dispatch,
	targetIndex,
	parentColumn,
	column,
	section,
	error
}) => {
	// error is set to true when button to drag row is clicked instead of drag (mousedown)
	if (error) {
		updateState({
			columnReorder: {
				type: null
			}
		});
	} else {
		const { columns, sectionsWidth, groupBy } = state;
		let updatedColumns = {};

		if (!isUndefined(targetIndex) && targetIndex !== -1) {
			updatedColumns = parentColumn
				? getSectionColsForSubColUpdate({
					columns,
					column,
					targetIndex,
					parentColumn
				})
				: getSectionCols({
					columns,
					column,
					section,
					targetIndex,
					groupBy
				});
		}

		let consolidatedColumns = {
			...columns,
			...updatedColumns
		};

		consolidatedColumns = getWhiteSpaceAdjustedColumns({
			columns: consolidatedColumns,
			sectionsWidth,
			gridWidth: getCurrentGridWidth(state)
		});

		updateState({
			columns: consolidatedColumns,
			columnReorder: {
				type: null
			}
		});
		// Autofitting columns whose width has not been set manually
		dispatch(COLUMNS_INIT_WIDTH_AUTO_ADJUSTED);
		dispatch(COLUMN_DEFINITION_ATTRIBUTE_UPDATED, { columns: consolidatedColumns });
	}
};
