/*
 * Copied from sn-component-form. Many component are using same way as implemented below with minor tweaks.
 */
const LOG_STATUS_KEY = '__SEISMIC_LOG_LEVEL';

export const LOG_LEVEL_ALL = 0;
export const LOG_LEVEL_DEBUG = 1;
export const LOG_LEVEL_LOG = 2;
export const LOG_LEVEL_INFO = 2;
export const LOG_LEVEL_WARN = 3;
export const LOG_LEVEL_ERROR = 4;

// set default logging level
if (typeof window[LOG_STATUS_KEY] === 'undefined') {
	window[LOG_STATUS_KEY] = LOG_LEVEL_LOG;
}

export function isDebugMode() {
	return window[LOG_STATUS_KEY] < 2;
}

/**
 * Adds Debugging point. Additional feature to avoid debugger statements for higher LOG level.
 */
export function debug() {
	if (isDebugMode()) {
		debugger; // eslint-disable-line no-debugger
	}
}

/**
 * Set logging level if not set by default in UIB.
 * @param {*} logLevel integer LOG_LEVEL_*
 */
export function enable(logLevel = LOG_LEVEL_ALL) {
	switch (logLevel) {
		case LOG_LEVEL_ALL:
		case LOG_LEVEL_DEBUG:
		case LOG_LEVEL_LOG:
		case LOG_LEVEL_INFO:
		case LOG_LEVEL_WARN:
		case LOG_LEVEL_ERROR:
			window[LOG_STATUS_KEY] = logLevel;
			break;
		default:
			logLevel = LOG_LEVEL_ERROR;
	}
}

/**
 * Disable debug mode.
 */
export function disableDebugMode() {
	window[LOG_STATUS_KEY] = LOG_LEVEL_ERROR;
}

/**
 * Simple logging utilty to include context
 * @usage const LOG = createLog('ComponentName');
 *
 * @param name
 * @return {{log, debug, warn, error}}
 */
export function createLog(name) {
	function _log(type, level, args) {
		if (level < window[LOG_STATUS_KEY] || args < 1) {
			return;
		}

		// if string add name with space before the first arg.
		if (typeof args[0] === 'string') {
			args[0] = `(${name}) ${args[0]}`;
		} else {
			args = Array.prototype.splice.call(args, 0);
			args.unshift(`(${name})`);
		}

		console[type].apply(console, args); // eslint-disable-line no-console
	}

	return {
		debug: (...args) => _log('debug', LOG_LEVEL_DEBUG, args),
		log: (...args) => _log('log', LOG_LEVEL_LOG, args),
		info: (...args) => _log('info', LOG_LEVEL_INFO, args),
		warn: (...args) => _log('warn', LOG_LEVEL_WARN, args),
		error: (...args) => _log('error', LOG_LEVEL_ERROR, args)
	};
}
