import isEmpty from 'lodash/isEmpty';
import {
	ARROW_UP_KEY,
	ARROW_DOWN_KEY,
	ARROW_RIGHT_KEY,
	ARROW_LEFT_KEY,
	OVERSCAN_COUNT,
	SCROLL_DIRECTION,
	AGGREGATION
} from '../../../constants';

import { getKeyboardNavigableColumns } from '../../helpers/sections';
import { handleFocus } from '../../helpers/accessibilityHelpers';
import { getScrollWindow } from '../../helpers/smartRendering';
import { getTreeUtils } from '../../store/tree/IndexTree';

const isIndexOutofBounds = (index, array) => index < 0 || index >= array.length;

const getUpdatedSelectedRowIds = ({
	tree,
	rowIndex,
	keyCode,
	startingRowIndex,
	selectedRowIds = new Set()
}) => {
	const treeUtils = getTreeUtils(tree);
	const currentTask = treeUtils.getTaskByIndex(rowIndex-1);
	if (!currentTask) { // Edge case: user is traversing form the header or totals row so return
		return;
	}
	const isCurrentTemp = currentTask.temporary;
	if (!selectedRowIds.has(currentTask.id)) { // if current task is not selected, select it first
		!isCurrentTemp && selectedRowIds.add(currentTask.id);
	}

	if (keyCode === ARROW_DOWN_KEY) {
		const nextTask = treeUtils.getTaskByIndex(rowIndex);
		if (!nextTask) { // last row, so just return
			return;
		}
		const isNextTemp = nextTask.temporary;
		if (startingRowIndex > rowIndex) {
			!isCurrentTemp && selectedRowIds.delete(currentTask.id); // move in opposite direction so unselecting row
		} else if (!nextTask.selected) {
			!isNextTemp && selectedRowIds.add(nextTask.id); // user is moving in the same direction so selecting row
		}
	} else if (keyCode === ARROW_UP_KEY) {
		const nextTask = treeUtils.getTaskByIndex(rowIndex-2);
		if (!nextTask) { // user is at the first row
			if (startingRowIndex <= rowIndex && currentTask) {
				// Edge case: rows selection started from the firstrow and user went to the header so unselect the row
				!isCurrentTemp && selectedRowIds.delete(currentTask.id);
			}

			return;
		}
		const isNextTemp = nextTask.temporary;
		if (startingRowIndex < rowIndex) {
			!isCurrentTemp && selectedRowIds.delete(currentTask.id); // unselecting row
		} else if (!nextTask.selected) {
			!isNextTemp && selectedRowIds.add(nextTask.id); // selecting row
		}
	}

	return selectedRowIds;
};

export const handleComponentRendering = ({ action: { payload: { host } }, state }) => {
	handleFocus({ host, state });
};

export const handleKeyboardNav = ({ action: { payload }, state, updateState }) => {
	const {
		selectedCell,
		tree,
		config: { rowHeight },
		scrollTop,
		visibleHeight,
		isScrolling
	} = state;
	const { keyCode, isRTL, segment, bodyWrapperRef, shiftKey = false } = payload;
	const { tasks } = tree;
	if (isEmpty(selectedCell)) {
		return;
	}
	const { rowIndex, columnId, selectedRowIds } = selectedCell;
	let { startingRowIndex } = selectedCell;
	const findColumnIndex = (allColumns, columnId) => {
		return allColumns.findIndex(column => column.columnId === columnId);
	};
	if ((keyCode === ARROW_UP_KEY && rowIndex <= 0) || (keyCode === ARROW_DOWN_KEY && rowIndex >= tasks.length)) {
		return;
	}
	const currentTask = getTreeUtils(tree).getTaskByIndex(rowIndex-1);
	const isCurrentTaskValid = currentTask && !currentTask.temporary && segment !== AGGREGATION;
	let updatedRowIds;
	if (shiftKey) { // if the user presses shift + up/down arrow then we have to select/unselect the rows
		if (!startingRowIndex) {
			startingRowIndex = rowIndex;
		}
		updatedRowIds = getUpdatedSelectedRowIds({
			tree,
			rowIndex,
			keyCode,
			startingRowIndex,
			selectedRowIds
		});
	} else {
		startingRowIndex = undefined;
	}

	switch (keyCode) {
		case ARROW_DOWN_KEY: {
			const indices = getScrollWindow({
				totalCount: tasks.length,
				scrollDirection: SCROLL_DIRECTION.TOP, //  scroll direction is top for down arrow-click
				scrollTop: scrollTop + rowHeight,
				visibleHeight,
				rowHeight,
				isScrolling
			});
			let endCount = indices.end - OVERSCAN_COUNT;
			if (indices.end === tasks.length) { // if true then the buffer of OVERSCAN_COUNT will not be present
				endCount = Math.ceil((scrollTop + visibleHeight)/ rowHeight);
			}
			if (endCount <= rowIndex + 1) {
				// for last visible row we are manually scrolling down to avoid scroll jumping
				bodyWrapperRef && bodyWrapperRef.current.scrollTo({ top: scrollTop + rowHeight });
			}

			updateState({
				selectedCell: {
					rowIndex: rowIndex + 1,
					columnId: columnId,
					segment,
					startingRowIndex,
					selectedRowIds: updatedRowIds
				},
				isScrolling: false
			});
			break;
		}

		case ARROW_UP_KEY: {
			const indices = getScrollWindow({
				totalCount: tasks.length,
				scrollDirection: SCROLL_DIRECTION.BOTTOM,
				scrollTop: scrollTop + rowHeight,
				visibleHeight,
				rowHeight,
				isScrolling
			});

			let startCount = indices.start + OVERSCAN_COUNT;
			if (indices.start === 0) { // if true then the buffer of OVERSCAN_COUNT will not be present
				startCount = Math.floor(scrollTop / rowHeight) + 1;
			}
			if (startCount >= rowIndex) {
				// for first visible row we are manually scrolling up to avoid scroll jumping
				bodyWrapperRef && bodyWrapperRef.current.scrollTo({ top: scrollTop - rowHeight });
			}

			updateState({
				selectedCell: {
					rowIndex: rowIndex - 1,
					columnId: columnId,
					segment,
					startingRowIndex,
					selectedRowIds: updatedRowIds
				},
				isScrolling: false
			}); break;
		}

		case ARROW_LEFT_KEY: {
			const allColumns = getKeyboardNavigableColumns(state.columns, state.config);
			const columnIndex = findColumnIndex(allColumns, columnId);
			const newColumnIndex = isRTL ? columnIndex+1 : columnIndex-1;

			if (isIndexOutofBounds(newColumnIndex, allColumns)) {
				return;
			}

			updateState({
				selectedCell: {
					rowIndex: rowIndex,
					columnId: allColumns[newColumnIndex].columnId,
					segment,
					... isCurrentTaskValid && { selectedRowIds: new Set().add(currentTask.id) }
				},
				isScrolling: false
			}); break;
		}

		case ARROW_RIGHT_KEY: {
			const allColumns = getKeyboardNavigableColumns(state.columns, state.config);
			const columnIndex = findColumnIndex(allColumns, columnId);
			const newColumnIndex = isRTL ? columnIndex-1 : columnIndex+1;

			if (isIndexOutofBounds(newColumnIndex, allColumns)) {
				return;
			}

			updateState({
				selectedCell: {
					rowIndex: rowIndex,
					columnId: allColumns[newColumnIndex].columnId,
					segment,
					... isCurrentTaskValid && { selectedRowIds: new Set().add(currentTask.id) }
				},
				isScrolling: false
			}); break;
		}
		default: break;
	}
};
